import { actionTypes } from '../action-types';
import { IUsers } from '../../models/store/users';

const createEmptyResult = (): IUsers => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Users = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_USERS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IUsers, payload: IUsers): IUsers => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
