import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IBlock } from '../models/block';
import { IUser } from '../models/user';
import { IHome } from '../models/home';
import { save } from '../store/actions/block';
import Page from '../components/home/manager/edit-block';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    blocks: state.Blocks,
    homes: state.Homes
});
const mapDispatchToProps = (dispatch: any) => ({
    onSave: (appClientId: string, data: IBlock, user: IUser, blocks: Array<IBlock>, homes: Array<IHome>, callback: (result: boolean, message: string) => void) => dispatch(save(appClientId, data, user, blocks, homes, callback))
});

export const EditBlock = connect(mapStateToProps, mapDispatchToProps)(Page);