import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IOutings } from '../models/store/outings';
import { IOuting } from '../models/outing';
import { save } from '../store/actions/outing';
import Page from '../components/home/new-outing';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    outings: state.Outings
});
const mapDispatchToProps = (dispatch: any) => ({
    onSave: (data: IOuting, homeId: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => dispatch(save(data, homeId, user, outings, callback))
});

export const NewOuting = connect(mapStateToProps, mapDispatchToProps)(Page);