import * as React from 'react';
import { View, Text, TouchableHighlight } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { styles } from '../../../../styles/page';
import { ITicket } from '../../../../models/home';
import { ITicketComment } from '../../../../models/home/ticket-comment';
import Toast from '../../../../util/toast';
import {styles as commentsStyles} from '../../../../styles/comments';
import { toDateTimeString } from '../../../../util/convert';
import { COLOR } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<ITicket>,
  viewId: string,
  comments: Array<ITicketComment>
};

export default class Tickets extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      viewId: '',
      comments: []
    }
    this.onView = this.onView.bind(this);
  };

  componentDidMount(){
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, (result: Array<ITicket>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Vehicles.');
      });
    }
  };

  componentWillUnmount() {}

  onView = (id: string, comments: Array<ITicketComment>) => {
    if(this.state.viewId === id){
      this.setState({
        viewId: '',
        comments: []
      });
    }else{
      this.setState({
        viewId: id,
        comments: comments
      });
    }
  };

  render() {
    let { data } = this.state;
      return(
        <View>
        <View className='center' style={styles.ele}>
          <Text style={{color: COLOR.PRIMARY, fontWeight: 'bold', fontSize: 17, marginBottom: 10}}>Tickets</Text>
        </View>
          {isNN(data) ?
            data.map((d: ITicket, i: number) => {
            return(
              <View key={'tkt' + i} style={styles.row}>
               <View style={styles.ele}>
                  <Text className='bold'>{d.createdBy + ' from ' + d._homeId + ' on ' + toDateTimeString(d.createdAt)}</Text>
                </View>
                <View style={styles.ele}>
                    <Text>{d.title}</Text>
                </View>
                <View style={styles.ele}>
                    <Text>{d.description}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.status}</Text>
                </View>
                <View style={styles.ele}>
                  <TouchableHighlight style={{}} onClick={() => {
                    this.onView(d._id, d.comments);
                  }}>
                    <Text className='pointer' style={{color: COLOR.SECONDARY}}>{(this.state.viewId === d._id ? 'Hide' : 'View') + ' Comments'}</Text>
                  </TouchableHighlight>
                </View>
                {this.state.viewId === d._id &&
                <View style={commentsStyles.cmntsCnr}>
                  <View style={commentsStyles.cmntsHdr}>
                    <Text className='bold' style={commentsStyles.cmntsHdrTxt}>Comments</Text>
                  </View>
                  <View style={commentsStyles.cmntsCnt}>
                    {isNN(this.state.comments) ?
                      this.state.comments.map((e: ITicketComment, j: number) => {
                        return(
                        <View key={'tkycmt' + j} style={j === 0 ? commentsStyles.cmntCnrFst : commentsStyles.cmntCnr}>
                          <View style={commentsStyles.cmntHdr}>
                            <Text className='bold'>{e.createdByName + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            <Text>{e.comment}</Text>
                          </View>
                        </View>
                        )
                      })
                      :
                      <Text style={{padding: 20}}>No Comments found!!!</Text>
                    }
                  </View>
                </View>
                }
              </View>
              );
          })
          :
          <Text>No data found!!!</Text>
        }
        </View>
      );
  }
};