import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text, TextInput, TextArea } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { ITickets } from '../../models/store/tickets';
import { ITicket } from '../../models/home/ticket';
import { ITicketComment } from '../../models/home/ticket-comment';
import { styles } from "../../styles/page";
import { styles as iconStyles } from "../../styles/icon";
import { styles as modelStyles } from "../../styles/model";
import {styles as commonStyles} from '../../styles/common';
import {styles as commentsStyles} from '../../styles/comments';
import Images from '../images';
import { isNN, isNNObject } from '../../util/index';
import { toDateTimeString } from '../../util/convert';
import { COLOR, ROLE } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  tickets: ITickets,
  onLoad: (appClientId: string, homeId: string, callback: (result: boolean) => void) => void,
  onSave: (data: ITicket, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onResolve: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onStart: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void,
  onAddComment: (ticketId: string, comment: ITicketComment, appUser: IAppUser, user: IUser, tickets: ITickets, callback: (result: boolean) => void) => void,
  onRemoveComment: (id: string, ticketId: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  data: Array<ITicket>,
  isEdit: boolean,
  id: string,
  title: string,
  description: string,
  status: string,
  viewId: string,
  comments: Array<ITicketComment>,
  comment: string,
  editCommentId: string,
  editComment: string
};

export default class Tickets extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      data: [],
      isEdit: false,
      id: '',
      title: '',
      description: '',
      status: 'INIT',
      viewId: '',
      comments: [],
      comment: '',
      editCommentId: '',
      editComment: ''
    }
    // this.getTickets = this.getTickets.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
    this.onResolve = this.onResolve.bind(this);
    this.onStart = this.onStart.bind(this);
    this.onView = this.onView.bind(this);
    this.onAddComment = this.onAddComment.bind(this);
    this.onUpdateComment = this.onUpdateComment.bind(this);
    this.onRemoveComment = this.onRemoveComment.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, (this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) ? '' : this.props.user.homes[this.props.appUser.homeIndex]._id, (result: boolean) => {
      this.setState({
        isLoading: false,
        // data: this.getTickets()
      });
      if(!result){
        Toast.fail('Failed to load Tickets.');
      }
    });
    /*
    if(this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) {
      this.setState({
        isLoading: true
      });
      this.props.onLoadAll(this.props.user.client._id, (result: boolean) => {
        this.setState({
          isLoading: false,
          data: this.getTickets()
        });
        if(!result){
          Toast.fail('Failed to load Tickets.');
        }
      });
    }else{
      this.setState({
        isLoading: true
      });
      this.props.onLoadAllByHome(this.props.user, this.props.appUser.homeIndex, (result: boolean) => {
        this.setState({
          isLoading: false,
          data: this.getTickets()
        });
        if(!result){
          Toast.fail('Failed to load Tickets.');
        }
      });
    }
    */
  };
  
  componentWillUnmount(){};

  /*
  getTickets = () => {
    if(this.props.appUser.role === ROLE.USER) {
      return isNN(this.props.user.homes[this.props.appUser.homeIndex].tickets) ? this.props.user.homes[this.props.appUser.homeIndex].tickets : [];
    }else if(this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) {
      return (this.props.tickets.isUpToDate && !this.props.tickets.isError && isNN(this.props.tickets.data)) ? this.props.tickets.data : [];
    }else{
      return [];
    }
  };
  */

  onSave = () => {
    if(isNN(this.state.title) && isNN(this.state.description)){
        this.setState({
          isLoading: true
        });
        let ticket = {
          _id: this.state.id,
          title: this.state.title,
          description: this.state.description,
          status: this.state.status
        } as ITicket;
        this.props.onSave(ticket, this.props.user, this.props.appUser.homeIndex, this.props.tickets, (result: boolean) => {          
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              title: '',
              description: '',
              status: 'INIT',
              viewId: '',
              comments: [],
              comment: '',
              editCommentId: '',
              editComment: ''
            });
            Toast.success('Ticket saved successfully.');
          }else{
            Toast.fail('Failed to save ticket.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.user, this.props.appUser.homeIndex, this.props.tickets, (result: boolean) => {
              this.setState({
                isLoading: false,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket removed successfully.');
              }else{
                Toast.fail('Failed to remove ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onResolve = (id: string) => {
    confirmAlert({
      title: 'Confirm to resolve ticket',
      message: 'Are you sure you wish to resolve this ticket?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onResolve(id, this.props.user, this.props.appUser.homeIndex, this.props.tickets, (result: boolean) => {
              this.setState({
                isLoading: false,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket resolved successfully.');
              }else{
                Toast.fail('Failed to resolve ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onStart = (id: string) => {
    confirmAlert({
      title: 'Confirm to start ticket',
      message: 'Are you sure you wish to start this ticket?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onStart(id, this.props.user, this.props.appUser.homeIndex, this.props.tickets, (result: boolean) => {
              this.setState({
                isLoading: false,
                viewId: '',
                comments: [],
                comment: '',
                editCommentId: '',
                editComment: ''
              });
              if(result === true){
                Toast.success('Ticket started successfully.');
              }else{
                Toast.fail('Failed to start ticket.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onView = (id: string, comments: Array<ITicketComment>) => {
    if(this.state.viewId === id){
      this.setState({
        viewId: '',
        comments: [],
        comment: '',
        editCommentId: '',
        editComment: ''
      });
    }else{
      this.setState({
        viewId: id,
        comments: comments,
        comment: '',
        editCommentId: '',
        editComment: ''
      });
    }
  };

  onAddComment = (id: string) => {
    if(isNN(this.state.comment)){
      this.setState({
        isLoading: true
      });
      this.props.onAddComment(id, {
        _id: '',
        comment: this.state.comment,
        createdBy: this.props.user._id,
        createdByName: this.props.user.name,
        createdAt: new Date(),
        updatedBy: this.props.user._id,
        updatedAt: new Date()
      } as ITicketComment, this.props.appUser, this.props.user, this.props.tickets, (result: boolean) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          this.setState({
            comment: ''
          });
          Toast.success('Comment added successfully.');
        }else{
          Toast.fail('Failed to add comment.');
        }
      });
  }else{
    Toast.warn('Please fill comment before Add.');
  }
  };

  onUpdateComment = (id: string) => {
    if(isNN(this.state.editCommentId) && isNN(this.state.editComment)){
      this.setState({
        isLoading: true
      });
      this.props.onAddComment(id, {
        _id: this.state.editCommentId,
        comment: this.state.editComment,
        updatedBy: this.props.user._id,
        updatedAt: new Date()
      } as ITicketComment, this.props.appUser, this.props.user, this.props.tickets, (result: boolean) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          this.setState({
            editCommentId: '',
            editComment: ''
          });
          Toast.success('Comment updated successfully.');
        }else{
          Toast.fail('Failed to update comment.');
        }
      });
  }else{
    Toast.warn('Please fill comment before update.');
  }
  };

  onRemoveComment = (id: string, ticketId: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete this comment?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemoveComment(id, ticketId, this.props.user, this.props.appUser.homeIndex, this.props.tickets, (result: boolean) => {
              if(result === true){
                this.setState({
                  isLoading: false,
                  editCommentId: '',
                  editComment: '',
                  viewId: ''
                });
                Toast.success('Comment removed successfully.');
              }else{
                this.setState({
                  isLoading: false
                });
                Toast.fail('Failed to remove comment.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Tickets', link: 'tickets'}]} />
        {
          this.props.appUser.role === ROLE.USER &&
          <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
            <Text className='fr pointer' onClick={() => this.props.history.push('new-ticket')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
          </View>
        }
        {(isNNObject(this.props.tickets) && this.props.tickets.isUpToDate && !this.props.tickets.isError && isNN(this.props.tickets.data)) ?
        this.props.tickets.data.map((d: ITicket, i: number) => {
            return(
              <View key={'tkt' + i} style={styles.row}>
               <View className='bold' style={styles.ele}>
                  <Text className='bold'>{d.createdBy + ' from ' + d._homeId + ' on ' + toDateTimeString(d.createdAt)}</Text>
                </View>
                <View style={styles.ele}>
                  {(this.state.isEdit && this.state.id === d._id) ?
                    <TextInput type='text' placeholder='Title' className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.title} onChange={(event: any) => this.setState({title: event.target.value})} />
                    :
                    <Text>{d.title}</Text>
                  }
                </View>
                <View style={styles.ele}>
                  {(this.state.isEdit && this.state.id === d._id) ?
                    <TextArea placeholder='Description' className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
                    :
                    <Text>{d.description}</Text>
                  }
                </View>
                <View style={styles.ele}>
                  <Text>{d.status}</Text>
                </View>
                {(this.state.isEdit && this.state.id === d._id) ?
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                    <Image src={Images.tick} style={iconStyles.editIcon} />
                  </TouchableHighlight>                
                  <TouchableHighlight style={{}} onClick={() => {
                    this.setState({
                      isEdit: false,
                      id: '',
                      title: '',
                      description: '',
                      status: 'INIT',
                      viewId: '',
                      comments: [],
                      comment: ''
                    });
                  }}>
                    <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>      
                </View>
                :
                <View style={styles.ele}>
                {this.props.appUser.role === ROLE.USER &&
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.setState({
                      isEdit: true,
                      id: d._id,
                      title: d.title,
                      description: d.description,
                      status: 'INIT',
                      viewId: '',
                      comments: [],
                      comment: ''
                    });
                  }}>
                    <Image src={Images.edit} style={iconStyles.editIcon} />
                  </TouchableHighlight>
                }
                {this.props.appUser.role === ROLE.USER &&
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.onRemove(d._id);
                  }}>
                    <Image src={Images.delete} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>
                }
                {this.props.appUser.role === ROLE.USER &&
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.onResolve(d._id);
                  }}>
                    <Text className='pointer' style={{color: COLOR.SECONDARY}}>RESOLVE</Text>
                  </TouchableHighlight>
                }
                {((this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) && d.status === 'INIT') &&
                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                  this.onStart(d._id);
                }}>
                  <Text className='pointer' style={{color: COLOR.SECONDARY}}>START</Text>
                </TouchableHighlight>
                }
                <TouchableHighlight style={{}} onClick={() => {
                  this.onView(d._id, d.comments);
                }}>
                  <Text className='pointer' style={{color: COLOR.SECONDARY}}>{(this.state.viewId === d._id ? 'Hide' : 'View') + ' Comments'}</Text>
                </TouchableHighlight>
                </View>
                }
                {this.state.viewId === d._id &&
                <View style={commentsStyles.cmntsCnr}>
                  <View style={commentsStyles.cmntsHdr}>
                    <Text className='bold' style={commentsStyles.cmntsHdrTxt}>Comments</Text>
                  </View>
                  <View style={commentsStyles.cmntsCnt}>
                    <View style={commentsStyles.addCmntCnt}>
                      <TextArea placeholder='Comment Text' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt}} value={this.state.comment} onChange={(event: any) => this.setState({comment: event.target.value})} />
                      <View style={commentsStyles.saveBtn}>
                        <Button
                          title='ADD COMMENT'
                          onPress={() => this.onAddComment(d._id)}
                          />
                      </View>
                    </View>
                    {isNN(this.state.comments) ?
                      this.state.comments.map((e, j) => {
                        return(
                        <View key={'tktcmt' + j} style={j === 0 ? commentsStyles.cmntCnrFst : commentsStyles.cmntCnr}>
                          <View style={commentsStyles.cmntHdr}>
                            <Text className='bold'>{e.createdByName + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            {
                              this.state.editCommentId === e._id ?
                              <TextArea placeholder='Comment Text' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt}} value={this.state.editComment} onChange={(event: any) => this.setState({editComment: event.target.value})} />
                              :
                              <Text>{e.comment}</Text>
                            }
                          </View>
                          {
                            e.createdBy === this.props.user._id &&
                            <>
                              {
                              this.state.editCommentId === e._id ?
                              <View style={styles.ele}>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => this.onUpdateComment(d._id)}>
                                  <Image src={Images.tick} style={iconStyles.editIcon} />
                                </TouchableHighlight>
                                <TouchableHighlight style={{}} onClick={() => {
                                  this.setState({
                                    editCommentId: '',
                                    editComment: ''
                                  });
                                }}>
                                  <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                                </TouchableHighlight>
                              </View>
                              :
                              <View style={styles.ele}>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                                  this.setState({
                                    editCommentId: e._id,
                                    editComment: e.comment
                                  });
                                }}>
                                  <Image src={Images.edit} style={iconStyles.editIcon} />
                                </TouchableHighlight>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                                  this.onRemoveComment(e._id, d._id);
                                }}>
                                  <Image src={Images.delete} style={iconStyles.deleteIcon} />
                                </TouchableHighlight>
                              </View>
                              }
                            </>
                          }
                        </View>
                        )
                      })
                      :
                      <Text style={{padding: 20}}>No Comments found!!!</Text>
                    }
                  </View>
                </View>
                }
              </View>
            )
          })
          :
          <Text style={{padding: 20}}>{(this.props.tickets.isUpToDate ? (this.props.tickets.isError ? this.props.tickets.errorMessage : "No Ticket found!!!") : "Loading...")}</Text>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};