import * as React from 'react';
import { View, TextInput, Text } from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import Breadcrum from '../common/breadcrum';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import { IUser } from '../../models/user/user';
import { isNN, onEnter } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  user: IUser,
  onUpdate: (userId: string, password: string, newPassword: string, sCallback: (result: boolean, message: string) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  password: string,
  newPassword: string,
  confirmNewPassword: string
};

export default class ChangePassword extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      password: '',
      newPassword: '',
      confirmNewPassword: ''
    }
    this.passwordPolicyCheck = this.passwordPolicyCheck.bind(this);
    this.onUpdate = this.onUpdate.bind(this);
  };

  componentWillUnmount() {};
  
  passwordPolicyCheck = (p: string) => {
    let anUpperCase = /[A-Z]/;
    let aLowerCase = /[a-z]/; 
    let aNumber = /[0-9]/;
    let aSpecial = /[!|@|#|$|%|^|&|*|(|)|-|_]/;
    let result = "";

    if(p.length < 6){
      result="New Password not long enough!"
        return result;
    }

    var numUpper = 0;
    var numLower = 0;
    var numNums = 0;
    var numSpecials = 0;
    for(var i=0; i<p.length; i++){
        if(anUpperCase.test(p[i]))
            numUpper++;
        else if(aLowerCase.test(p[i]))
            numLower++;
        else if(aNumber.test(p[i]))
            numNums++;
        else if(aSpecial.test(p[i]))
            numSpecials++;
    }

    if(numUpper < 1 || numLower < 1 || numNums < 1 || numSpecials < 1){
      result="New Password dose not match with password policy!";
        return result;
    }
    return result;
  };

  onUpdate = () => {
    if(isNN(this.state.password) && isNN(this.state.newPassword) && isNN(this.state.confirmNewPassword)){
      if(this.state.newPassword === this.state.confirmNewPassword){
        let policyResult = this.passwordPolicyCheck(this.state.newPassword);
        if(policyResult.length < 1){
          this.setState({
            isLoading: true
          });
          this.props.onUpdate(this.props.user._id, this.state.password, this.state.newPassword, (result: boolean, message: string) => {
            this.setState({
              isLoading: false,
            });
            if(result === true){
              Toast.success('Password updated successfully. Please login with your new password.');
              this.props.onLogout((result: boolean) => {
              });
            }else{
              Toast.fail(message);
            }
          });
        }else{
          Toast.warn(policyResult);
        }
      }else{
        Toast.warn('New Password and Confirm New Password not matchs.');
      }
    }else{
      Toast.warn('Please fill all the fields before Update.');
    }
  };

  render() {
      return(
        <main style={{...styles.content, flex: 1}}>
          <Breadcrum data={[{title: 'Change Password', link: 'change-password'}]} />
          <View style={modelStyles.container}>
            <View style={modelStyles.row}>
              <TextInput type='password' placeholder='Password' className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.password} onChange={(event: any) => this.setState({password: event.target.value})} />
            </View>
            <View style={modelStyles.row}>
              <TextInput type='password' placeholder='New Password' className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.newPassword} onChange={(event: any) => this.setState({newPassword: event.target.value})} />
            </View>
            <View style={modelStyles.row}>
              <TextInput type='password' placeholder='Confirm New Password' className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.confirmNewPassword} onChange={(event: any) => this.setState({confirmNewPassword: event.target.value})}
              onKeyPress={(e) => {
                onEnter(e, this.onUpdate);
              }} />
            </View>
            <View className='center' style={modelStyles.saveBtn}>
              <Button
              title="UPDATE"
              onPress={this.onUpdate}
              />
            </View>
            <View style={{ marginTop: 7 }}>
              <Text>Note: Password should contain adleast one uppercase, one lowercase, one number and one special character.</Text>
            </View>
          </View>
          <Spinner visible={this.state.isLoading} />
        </main>
      );
  }
};