import { actionTypes } from '../action-types';
import { IDocuments } from '../../models/store/documents';
import { IUser } from '../../models/user';
import { IDocument } from '../../models/document';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';
// import { FILE_BASE_PATH } from '../../constants';

let controller: string = 'document';

export const loadAll = (homeId: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getDocumentResult([], false, false, ''));
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      dispatch(getDocumentResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const loadAllByHome = (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Documents.');
    }
  });
};

export const save = (homeId: string, data: IDocument, homeIndex: number, user: IUser, documents: Array<IDocument>, callback: (result: boolean) => void) => (dispatch: any) => {  
  // document.file = FILE_BASE_PATH;
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
          var foundIndex = documents.findIndex(d => d._id === result.data._id);
          documents[foundIndex]._id = result.data._id;
          ////documents[foundIndex].userId = data.userId;
          ////documents[foundIndex].homeId = data.homeId;
          documents[foundIndex].title = data.title;
          documents[foundIndex].description = data.description;
          documents[foundIndex].file = data.file;
        dispatch(getDocumentResult(documents, true, false, ''));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }else{
    new Api().post(controller + '/' + homeId, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
          documents.unshift({
            _id: result.data._id,
            ////userId: data.userId,
            ////homeId: data.homeId,
            title: data.title,
            description: data.description,
            file: data.file,
            createdBy: user.name,
            createdAt: new Date()
          } as IDocument);
        dispatch(getDocumentResult(documents, true, false, ''));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }
};

export const remove = (id: string, documents: Array<IDocument>, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      documents = documents.filter((d, i) => d._id !== id);
      dispatch(getDocumentResult(documents, true, false, ''));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getDocumentResult = (data: Array<IDocument>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_DOCUMENTS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IDocuments
});