import { actionTypes } from '../action-types';
import { IHomes } from '../../models/store/homes';

const createEmptyResult = (): IHomes => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Homes = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_HOMES:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IHomes, payload: IHomes): IHomes => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
