import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IAd } from '../models/ad';
import { loadAll, save, remove } from '../store/actions/ad';
import Page from '../components/home/manager/ads';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    ads: state.Ads
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, callback: (result: boolean) => void) => dispatch(loadAll(appClientId, callback)),
    onSave: (appClientId: string, data: IAd, user: IUser, ads: Array<IAd>, callback: (result: boolean) => void) => dispatch(save(appClientId, data, user, ads, callback)),
    onRemove: (id: string, ads: Array<IAd>, callback: (result: boolean) => void) => dispatch(remove(id, ads, callback))
});

export const Ads = connect(mapStateToProps, mapDispatchToProps)(Page);