import * as React from 'react';
import { View, Text, TextInput, Image } from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import Images from '../images';
import {styles as CommonStyles} from '../../styles/common';
import { COLOR } from '../../constants';
import { isNN, onEnter } from '../../util';
import Toast from  '../../util/toast';
import { IMember } from '../../models/home';
import SignUp from './sign-up'

interface IProps {
    onLogin: (userName: string, password: string, callback: (result: boolean, message: string) => void) => void,
    onForgetPassword: (mobileNumber: string, email: string, callback: (result: boolean, message: string) => void) => void,
    onSignUp: (data: IMember, clientCode: string, sCallback: (result: boolean, message: string) => void) => void
}

interface IState{
    userName: string,
    password: string,
    message: string,
    isLoading: boolean,
    isForgetPass: boolean,
    isSignUp: boolean,
    mobileNumber: string,
    email: string
};

export default class Index extends React.Component<IProps, IState> {
    constructor(props: any) {
        super(props);
        this.state = {
            userName: '',
            password: '',
            message: '',
            isLoading: false,
            isForgetPass: false,
            isSignUp: false,
            mobileNumber: '',
            email: ''
        };
        this.onLogin = this.onLogin.bind(this);
        this.onForgetPassword = this.onForgetPassword.bind(this);
        this.onForgetPassSubmit = this.onForgetPassSubmit.bind(this);
        this.onCreateAccount = this.onCreateAccount.bind(this);
        this.onSignUp = this.onSignUp.bind(this);
    };

    componentWillUnmount() {};

    onLogin = () =>{
        if(isNN(this.state.userName) && isNN(this.state.password)){
            this.setState({
                message: '',
                isLoading: true
            });
            this.props.onLogin(this.state.userName, this.state.password, (result: boolean, message: string) => {
                if(result === true){                        
                    this.setState({
                        isLoading: false,
                        message: ''
                    });
                }else{
                    this.setState({
                        message: message,
                        isLoading: false
                    });
                }
            });
        }else{
            // Toast.warn('Enter Mobile Number and Password to continue.');
            Toast.warn('Enter Mobile Number and Password to continue.');
        }
    };
    
    onForgetPassword = () =>{
        this.setState({
            isForgetPass: true,
            isSignUp: false,
            mobileNumber: '',
            email: '',
            message: ''
        });
    };

    onForgetPassSubmit = () => {
        if(isNN(this.state.mobileNumber) && isNN(this.state.email)){
            this.setState({
                message: '',
                isLoading: true
            });
            this.props.onForgetPassword(this.state.mobileNumber, this.state.email.toLowerCase(), (result: boolean, message: string) => {
                if(result === true){
                    Toast.success('Password reset success. Login with New password sent to your Email. Change password after login.');
                    this.setState({
                        isLoading: false,
                        message: '',
                        isForgetPass: false,
                        isSignUp: false,
                        userName: '',
                        password: ''
                    });
                }else{
                    this.setState({
                        message: message,
                        isLoading: false
                    });
                }
            });
        }else{
            Toast.warn('Enter Mobile Number and Email to continue.');
        }
    };

    onCreateAccount = () =>{
        this.setState({
            isForgetPass: false,
            isSignUp: true,
            mobileNumber: '',
            email: '',
            message: ''
        });
    };

    onSignUp = (data: IMember, clientCode: string, callback: (result: boolean) => void) => {
        this.setState({
            message: '',
            isLoading: true
        });
        this.props.onSignUp(data, clientCode, (result: boolean, message: string) => {
            this.setState({
              isLoading: false
            });
            if(result === true){
              this.setState({
                  isForgetPass: false,
                  isSignUp: false,
                  message: ''
              });
              Toast.success('Sign Up success. Please wait till Manager approves then you can access your account.');
              callback(true);
            }else{
              Toast.fail(isNN(message) ? message : 'Failed to Sign Up.');
              callback(false);
            }
          });
    };

  render() {
    return (
            <View className='container' style={{backgroundColor: COLOR.INVERSE, maxWidth: '270px'}}>
                <View className='row' style={{flexDirection: 'row', alignItems: 'center', justifyContent:'center', marginBottom: 30, marginTop: 5}}>
                    <Image src={Images.logo} style={{width: 150, height: 150}}/>
                </View>
                {this.state.isForgetPass &&
                <View className='row' style={{flex: 1, alignItems: 'center', justifyContent:'center'}}>
                    <View className='col-12' style={styles.row}>
                        <Image className='absolute' src={Images.mobile} style={styles.txtIcon} />
                        <TextInput type='text'  className='form-control' placeholder='Registered Mobile Number' value={this.state.mobileNumber} style={{...CommonStyles.textInput, ...styles.txt}}
                        onChange={(event: any) => this.setState({mobileNumber: event.target.value})}
                        onKeyPress={(e) => {
                            onEnter(e, this.onForgetPassSubmit);
                        }} />
                    </View>
                    <View className='col-12' style={{...styles.row, marginBottom: 40}}>
                        <Image className='absolute' src={Images.email} style={styles.txtIcon} />
                        <TextInput type='text'  className='form-control' placeholder='Registered Email' value={this.state.email} style={{...CommonStyles.textInput, ...styles.txt}}
                        onChange={(event: any) => this.setState({email: event.target.value})}
                        onKeyPress={(e) => {
                            onEnter(e, this.onForgetPassSubmit);
                        }} />
                    </View>
                    <Button
                    title={this.state.isLoading === false ? "Reset my password" : "Loading..."}
                    onPress={this.onForgetPassSubmit}
                    />
                    <Text className='col-12 center' style={{marginTop: 5, color: (this.state.isLoading ? COLOR.FONT_COLOR2 : COLOR.ERROR)}}>{this.state.message}</Text>

                    <View style={{marginTop: 15}}>
                        <Text className='pointer' onClick={() => { 
                            this.setState({
                                isForgetPass: false,
                                isSignUp: false,
                                mobileNumber: "",
                                email: "",
                                message: ""
                            });
                         }} style={{marginBottom: 5, textAlign: 'center', color: COLOR.SECONDARY}}>Go back to Login.</Text>
                    </View>
                </View>
                }
                {this.state.isSignUp && <>
                    <SignUp onSignUp={this.onSignUp} />
                    <View style={{marginTop: 15, textAlign: 'center'}}>
                        <Text className='pointer' onClick={() => { 
                            this.setState({
                                isForgetPass: false,
                                isSignUp: false,
                                mobileNumber: "",
                                email: "",
                                message: ""
                            });
                         }} style={{marginBottom: 5, textAlign: 'center', color: COLOR.SECONDARY}}>Go back to Login.</Text>
                    </View>
                    </>
                }
                {(!this.state.isForgetPass && !this.state.isSignUp) && 
                <View className='row' style={{flex: 1, alignItems: 'center', justifyContent:'center'}}>
                    <View className='col-12' style={styles.row}>
                        <Image className='absolute' src={Images.username} style={styles.txtIcon} />
                        <TextInput type='text' className='form-control' placeholder='Mobile Number' style={{...CommonStyles.textInput, ...styles.txt}} value={this.state.userName} 
                        onChange={(event: any) => this.setState({userName: event.target.value})}
                        onKeyPress={(e) => {
                            onEnter(e, this.onLogin);
                        }} />
                    </View>
                    <View className='col-12' style={{...styles.row, marginBottom: 15}}>
                        <Image className='absolute' src={Images.password} style={styles.txtIcon} />
                        <TextInput type='password'  className='form-control' placeholder='Password' value={this.state.password} style={{...CommonStyles.textInput, ...styles.txt}}
                        onChange={(event: any) => this.setState({password: event.target.value})}
                        onKeyPress={(e) => {
                            onEnter(e, this.onLogin);
                        }} />
                    </View>
                    <Text className='col-12 pointer' onClick={this.onForgetPassword} style={{marginBottom: 15, textAlign: 'right', color: COLOR.SECONDARY}}>Forgot Password?</Text>
                    <Button
                    title={this.state.isLoading === false ? "LOGIN" : "Loading..."}
                    onPress={this.onLogin}
                    />
                    <Text className='col-12 center' style={{marginTop: 5, color: (this.state.isLoading ? COLOR.FONT_COLOR2 : COLOR.ERROR)}}>{this.state.message}</Text>

                    <View className='col-12' style={{marginTop: 15, alignItems: 'center', justifyContent:'center'}}>
                        <Text className='col-12' style={{marginBottom: 5, textAlign: 'center'}}>Dont have an account?</Text>
                        <View className='col' style={{float: 'left', alignItems: 'center', justifyContent:'center', borderWidth: 1, borderColor: '#000000', borderStyle: 'solid', borderRadius: 10}}>
                            <Text style={{textAlign: 'center', margin: '7px', cursor: 'pointer'}} onClick={this.onCreateAccount}>CREATE / JOIN YOUR FLAT</Text>
                        </View>
                    </View>
                </View>
                }
                <Spinner visible={this.state.isLoading} text={""} />
            </View>
    );
  }
};

const styles = {
    row: {
        // flexDirection: 'row',
        // justifyContent: 'center',
        // alignItems: 'center',
        marginBottom: 25
    },     
    txtIcon: {
        marginLeft: 6,
        // marginRight: -15,
        height: 15,
        width: 15,
        resizeMode : 'stretch',
        alignItems: 'center',
        top: 11

    },
    txt: {
        flex: 1,
        paddingLeft: 25
    }
};