import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, TouchableHighlight, TextInput, Image, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import { styles } from "../../../styles/page";
import { styles as iconStyles } from "../../../styles/icon";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IHome, IMember } from '../../../models/home';
import { IUserHomes } from '../../../models/store/user-homes';
import { IUserHome } from '../../../models/user-home';
import Images from '../../images';
import { COLOR } from '../../../constants';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  searchText: string,
  searchResult: Array<IMember>,
  searchResultIndex: number
};

export default class Users extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      searchText: '',
      searchResult: [],
      searchResultIndex: 10
    }
    this.getHomes = this.getHomes.bind(this);
    this.onSearch = this.onSearch.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};
  
  getHomes = (userId: string) => {
    let homes = '';
    if((this.props.homes.isUpToDate && !this.props.homes.isError && isNN(this.props.homes.data)) && (this.props.userHomes.isUpToDate && !this.props.userHomes.isError && isNN(this.props.userHomes.data))){
      let homeIds = [] as Array<string>;
      this.props.userHomes.data.filter(uh => uh.userId === userId).map((uh: IUserHome, i: number) => homeIds.push(uh.homeId));
      this.props.homes.data.filter(d => homeIds.indexOf(d._id) >= 0).map((h: IHome, i: number) => homes += (homes.length > 0 ? ', ' : '') + h.number);
    }
    return homes;
  };

  onSearch = () => {
    if(isNN(this.state.searchText) && this.props.users.isUpToDate && !this.props.users.isError && isNN(this.props.users.data)){
      let searchText = this.state.searchText.toLocaleLowerCase();
      let homes = this.props.homes.data.filter(h => h.number.toLocaleLowerCase() === searchText);
      this.setState({
        searchResult: this.props.users.data.filter(d => (d.name + '').toLocaleLowerCase().indexOf(searchText) >= 0 || (d.mobileNo + '').toLocaleLowerCase().indexOf(searchText) >= 0 || (d.emailId + '').toLocaleLowerCase().indexOf(searchText) >= 0 || (d.number + '').toLocaleLowerCase().indexOf(searchText) >= 0 || (isNN(homes.filter(h => h.memberIds.indexOf(d._id) >= 0)))),
        searchResultIndex: 10
      });
    }else{
      this.setState({
        searchResult: [],
        searchResultIndex: 10
      });
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.appUser.homeIndex, this.props.user, this.props.users, (result: boolean) => {
              this.setState({
                isLoading: false
              });
              if(result === true){
                this.onSearch();
                Toast.success('User removed successfully.');
              }else{
                Toast.fail('Failed to remove user.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
      return(
        <main style={styles.content}>
          <Breadcrum data={[{title: 'Users', link: 'users'}]} />
          <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
            <Text className='fr pointer' onClick={() => this.props.history.push('edit-user/0/false')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
            <Text className='fr pointer' onClick={() => this.props.history.push('approve-users')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right', marginRight: 15}}>View Users Waiting for Approval</Text>
          </View>
          <View style={{...modelStyles.row, marginTop: 0, paddingTop: 0}}>
            <TextInput type='text' placeholder="Search User" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele, display: 'inline', width: 'calc(100% - 40px)', marginRight: 10}} value={this.state.searchText} onChange={(event: any) => this.setState({searchText: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSearch);
            }} />
            <TouchableHighlight style={{height: 30, marginTop: 13}} onClick={this.onSearch}>
                <Image className='pointer' src={Images.search} style={{width: 30, height: 30}} />
            </TouchableHighlight> 
          </View>

          {isNN(this.state.searchResult) ?
          this.state.searchResult.slice(0, this.state.searchResultIndex).map((d: IMember, i: number) => {
            return(
              <View key={'usr' + i} style={styles.row}>
                {/* <View style={styles.ele}>
                  <Text>{'User Id: ' + (isNN(d.number) ? d.number : '')}</Text>                  
                </View> */}
                <View style={styles.ele}>
                  <Text>{'Name: ' + (isNN(d.name) ? d.name : '')}</Text>                  
                </View>
                {/* <View style={styles.ele}>
                  <Text>{'Mobile No: ' + (isNN(d.mobileNo) ? d.mobileNo : '')}</Text>         
                </View>
                <View style={styles.ele}>
                  <Text>{'Email: ' + (isNN(d.emailId) ? d.emailId : '')}</Text>         
                </View> */}
                {/* <View style={styles.ele}>
                  <Text>{'Owner Name: ' + (isNN(d.ownername) ? d.ownername : '')}</Text>         
                </View>
                <View style={styles.ele}>
                  <Text>{'Owner Mobile No: ' + (d.ownermobile > 0 ? d.ownermobile : '')}</Text>         
                </View> */}
                {/* <View style={styles.ele}>
                  <Text>{'Role: ' + (isNN(d.type) ? d.type.replace(/,/g, ', ') : '')}</Text>         
                </View>
                <View style={styles.ele}>
                  <Text>{'Home(s): ' + this.getHomes(d._id)}</Text>         
                </View> */}
                {(d._id !== this.props.user._id) &&
                  <View style={styles.ele}>
                    <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                      this.props.history.push('edit-user/' + d._id + '/false');////TODO
                      // this.props.navigation.navigate('EditUser', {id: d._id});
                      // this.setState({
                      //   isEdit: true,
                      //   id: d.id
                      // });
                    }}>
                      <Image src={Images.edit} style={iconStyles.editIcon} />
                    </TouchableHighlight>
                    <TouchableHighlight style={{}} onClick={() => {
                      this.onRemove(d._id);
                    }}>
                      <Image src={Images.delete} style={iconStyles.deleteIcon} />
                    </TouchableHighlight>
                  </View>
                }
              </View>
              
            )
          })
          :
          <Text style={{padding: 20}}>No user match with your search!!!</Text>
        }
        {(this.state.searchResult.length > this.state.searchResultIndex) &&
        <View style={styles.row}>
          <View style={{flex: 1, flexDirection: 'row', padding: 10}}>
            <Text style={{color: COLOR.SECONDARY, fontWeight: 'bold'}} onClick={() => 
            this.setState({
              searchResultIndex: this.state.searchResultIndex + 10
            })}>Show More</Text>
          </View>
        </View>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
      );
  }
};