import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Text, Image, TextInput, TextArea } from '../common/elements';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IServant, ITakeaway } from '../../models/home/servant';
import { styles } from "../../styles/page";
import { styles as iconStyles } from "../../styles/icon";
import { styles as modelStyles } from "../../styles/model";
import {styles as commonStyles} from '../../styles/common';
import {styles as commentsStyles} from '../../styles/comments';
import Images from '../images';
import { isNN } from '../../util/index';
import { COLOR } from '../../constants';
import Toast from  '../../util/toast';
import Button from '../common/button';
import { toDateTimeString } from '../../util/convert';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onLoad: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onSave: (homeId: string, data: IServant, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, sCallback: (result: boolean) => void) => void,
  onCreateTakeaway: (id: string, data: ITakeaway, sCallback:(result: string) => void, fCallback:(message: string) => void) => void,
  onRemoveTakeaway: (id: string, sCallback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IServant>,
  isLoading: boolean,
  isEdit: boolean,
  id: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  emailId: string,
  viewId: string,
  takeaways: Array<ITakeaway>,
  takeawayTitle: string,
  takeawayDesc: string,
  editTakeawayId: string,
  editTakeawayTitle: string
  editTakeawayDesc: string
};

export default class Servants extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isLoading: false,
      isEdit: false,
      id: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      emailId: '',
      viewId: '',
      takeaways: [],
      takeawayTitle: '',
      takeawayDesc: '',
      editTakeawayId: '',
      editTakeawayTitle: '',
      editTakeawayDesc: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
    this.onView = this.onView.bind(this);
    this.onCreateTakeaway = this.onCreateTakeaway.bind(this);
    this.onUpdateTakeaway = this.onUpdateTakeaway.bind(this);
    this.onRemoveTakeaway = this.onRemoveTakeaway.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.homes[this.props.homeIndex]._id, (result: Array<IServant>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender)){
        this.setState({
          isLoading: true
        });
        let servant = {
          _id: this.state.id,
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: this.state.emailId ? this.state.emailId.toLowerCase() : ''
        } as IServant;
        this.props.onSave(this.props.user.homes[this.props.homeIndex]._id, servant, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            var foundIndex = this.state.data.findIndex(d => d._id === this.state.id);
            this.state.data[foundIndex].name = this.state.name;
            this.state.data[foundIndex].age = this.state.age;
            this.state.data[foundIndex].gender = this.state.gender;
            this.state.data[foundIndex].mobileNo = this.state.mobileNo;
            this.state.data[foundIndex].emailId = this.state.emailId;
            this.setState({
              isEdit: false,
              id: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              viewId: '',
              takeaways: [],
              takeawayTitle: '',
              takeawayDesc: '',
              editTakeawayId: '',
              editTakeawayTitle: '',
              editTakeawayDesc: ''
            });
            Toast.success('Servant saved successfully.');
          }else{
            Toast.fail('Failed to save servant.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, (result: boolean) => {
              if(result === true){
                this.setState({
                  isLoading: false,
                  data: this.state.data.filter((d, i) => d._id !== id),
                  viewId: '',
                  takeaways: [],
                  takeawayTitle: '',
                  takeawayDesc: '',
                  editTakeawayId: '',
                  editTakeawayTitle: '',
                  editTakeawayDesc: ''
                });
                Toast.success('Servant removed successfully.');
              }else{
                this.setState({
                  isLoading: false,
                });
                Toast.fail('Failed to remove servant.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };
  
  onView = (id: string, takeaways: Array<ITakeaway>) => {
    if(this.state.viewId === id){
      this.setState({
        viewId: '',
        takeaways: [],
        takeawayTitle: '',
        takeawayDesc: '',
        editTakeawayId: '',
        editTakeawayTitle: '',
        editTakeawayDesc: ''
      });
    }else{
      this.setState({
        viewId: id,
        takeaways: takeaways,
        takeawayTitle: '',
        takeawayDesc: '',
        editTakeawayId: '',
        editTakeawayTitle: '',
        editTakeawayDesc: ''
      });
    }
  };

  onCreateTakeaway = (id: string) => {
    if(isNN(this.state.takeawayTitle) && isNN(this.state.takeawayDesc)){
      this.setState({
        isLoading: true
      });
      this.props.onCreateTakeaway(id, {
        _id: '',
        title: this.state.takeawayTitle,
        description: this.state.takeawayDesc,
        createdBy: this.props.user.name,
        createdAt: new Date(),
        updatedBy: this.props.user._id,
        updatedAt: new Date()
      } as ITakeaway, (result: string) => {
        var foundIndex = this.state.data.findIndex(d => d._id === id);
        this.state.data[foundIndex].takeaways.unshift({
          _id: result,
          title: this.state.takeawayTitle,
          description: this.state.takeawayDesc,
          createdAt: new Date(),
          createdBy: this.props.user.name,
          updatedBy: this.props.user._id,
          updatedAt: new Date()
        } as ITakeaway);
        this.setState({
          isLoading: false,
          takeawayTitle: '',
          takeawayDesc: ''
        });
        Toast.success('Takeayway added successfully.');
      }, (message: string) => {
        this.setState({
          isLoading: false,
        });
        Toast.fail('Failed to add takeaway.');
      });
    }else{
      Toast.warn('Please fill Takeaway title & description before Add.');
    }
  };

  onUpdateTakeaway = (id: string) => {
    if(isNN(this.state.editTakeawayTitle) && isNN(this.state.editTakeawayDesc)){
      this.setState({
        isLoading: true
      });
      this.props.onCreateTakeaway(id, {
        _id: this.state.editTakeawayId,
        title: this.state.editTakeawayTitle,
        description: this.state.editTakeawayDesc,
        createdBy: this.props.user.name,
        createdAt: new Date(),
        updatedBy: this.props.user._id,
        updatedAt: new Date()
      } as ITakeaway, (result: string) => {
        var foundIndex = this.state.data.findIndex(d => d._id === id);        
        var takeawayIndex = this.state.data[foundIndex].takeaways.findIndex(d => d._id === this.state.editTakeawayId);
        this.state.data[foundIndex].takeaways[takeawayIndex].title = this.state.editTakeawayTitle;
        this.state.data[foundIndex].takeaways[takeawayIndex].description = this.state.editTakeawayDesc;
        this.state.data[foundIndex].takeaways[takeawayIndex].updatedBy = this.props.user.name;
        this.state.data[foundIndex].takeaways[takeawayIndex].updatedAt = new Date();
        this.setState({
          isLoading: false,
          editTakeawayId: '',
          editTakeawayTitle: '',
          editTakeawayDesc: ''
        });
        Toast.success('Takeayway updated successfully.');
      }, (message: string) => {
        this.setState({
          isLoading: false,
        });
        Toast.fail('Failed to update takeaway.');
      });
    }else{
      Toast.warn('Please fill Takeaway title & description before Add.');
    }
  };

  onRemoveTakeaway = (id: string, servantId: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete this takeaway?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemoveTakeaway(id, (result: boolean) => {
              var foundIndex = this.state.data.findIndex(d => d._id === servantId);        
              this.state.data[foundIndex].takeaways = this.state.data[foundIndex].takeaways.filter((d, i) => d._id !== id);
              if(result === true){
                this.setState({
                  isLoading: false,
                  editTakeawayId: '',
                  editTakeawayTitle: '',
                  editTakeawayDesc: '',
                  viewId: ''
                });
                Toast.success('Takeaway removed successfully.');
              }else{
                this.setState({
                  isLoading: false
                });
                Toast.fail('Failed to remove takeaway.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Servants', link: 'servants'}]} />
        <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
          <Text className='fr pointer' onClick={() => this.props.history.push('new-servant')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
        </View>
        {isNN(this.state.data) ?
          this.state.data.map((d: IServant, i: number) => {
            return(
              (this.state.isEdit === true && this.state.id === d._id) ?
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                <TextInput type='text' placeholder="Name" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
                </View>
                <View style={styles.ele}>
                  <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
                </View>
                <View style={styles.ele}>
                  <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
                  <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
                  <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
                </View>
                <View style={styles.ele}>
                  <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
                </View>
                <View style={styles.ele}>
                  <TextInput type='text' placeholder="Email Id" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})} />
                </View>
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                    <Image src={Images.tick} style={iconStyles.editIcon} />
                  </TouchableHighlight>                
                  <TouchableHighlight style={{}} onClick={() => {
                    this.setState({
                      isEdit: false,
                      id: '',
                      name: '',
                      age: 0,
                      gender: '',
                      mobileNo: '',
                      emailId: '',
                      viewId: '',
                      takeaways: [],
                      takeawayTitle: '',
                      takeawayDesc: '',
                      editTakeawayId: '',
                      editTakeawayTitle: '',
                      editTakeawayDesc: ''
                    });
                  }}>
                    <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>          
                </View>
              </View>
              :
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <Text>{d.name}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                </View>
                {isNN(d.mobileNo) &&
                <View style={styles.ele}>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) &&
                <View style={styles.ele}>
                  <Text>{d.emailId}</Text>
                </View>
                }
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.setState({
                      isEdit: true,
                      id: d._id,
                      name: d.name,
                      age: d.age,
                      gender: d.gender,
                      mobileNo: d.mobileNo,
                      emailId: d.emailId,
                      viewId: '',
                      takeaways: [],
                      takeawayTitle: '',
                      takeawayDesc: '',
                      editTakeawayId: '',
                      editTakeawayTitle: '',
                      editTakeawayDesc: ''
                    });
                  }}>
                    <Image src={Images.edit} style={iconStyles.editIcon} />
                  </TouchableHighlight>                
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.onRemove(d._id);
                  }}>
                    <Image src={Images.delete} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>
                  <TouchableHighlight style={{}} onClick={() => {
                    this.onView(d._id, d.takeaways);
                  }}>
                    <Text className='pointer' style={{color: COLOR.SECONDARY}}>{(this.state.viewId === d._id ? 'Hide' : 'View') + ' Takeaways'}</Text>
                  </TouchableHighlight>   
                </View>
                {this.state.viewId === d._id &&
                <View style={commentsStyles.cmntsCnr}>
                  <View style={commentsStyles.cmntsHdr}>
                    <Text className='bold' style={commentsStyles.cmntsHdrTxt}>Takeaways</Text>
                  </View>
                  <View style={commentsStyles.cmntsCnt}>
                    <View style={commentsStyles.addCmntCnt}>
                      <TextInput type='text' placeholder='Takeaway Title' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt, marginBottom: 10}} value={this.state.takeawayTitle} onChange={(event: any) => this.setState({takeawayTitle: event.target.value})} />
                      <TextArea placeholder='Takeaway Description' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt}} value={this.state.takeawayDesc} onChange={(event: any) => this.setState({takeawayDesc: event.target.value})} />
                      <View style={commentsStyles.saveBtn}>
                        <Button
                          title='ADD NEW TAKEAWAY'
                          onPress={() => this.onCreateTakeaway(d._id)}
                          />
                      </View>
                    </View>
                    {isNN(this.state.takeaways) ?
                      this.state.takeaways.map((e: ITakeaway, j: number) => {
                        return(
                        <View key={'tkycmt' + j} style={j === 0 ? commentsStyles.cmntCnrFst : commentsStyles.cmntCnr}>
                          <View style={commentsStyles.cmntHdr}>
                            <Text className='bold'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            {
                              (this.state.editTakeawayId === e._id && !e.isExit) ?
                              <TextInput type='text' placeholder='Takeaway Title' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt, marginBottom: 10}} value={this.state.editTakeawayTitle} onChange={(event: any) => this.setState({editTakeawayTitle: event.target.value})} />
                              :
                              <Text>{e.title}</Text>
                            }
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            {
                              (this.state.editTakeawayId === e._id && !e.isExit) ?
                              <TextArea placeholder='Takeaway Description' className='form-control' style={{...commonStyles.textInput, ...commentsStyles.newCmnt}} value={this.state.editTakeawayDesc} onChange={(event: any) => this.setState({editTakeawayDesc: event.target.value})} />
                              :
                              <Text>{e.description}</Text>
                            }
                          </View>
                          {
                            e.isExit ?
                          <View style={commentsStyles.cmntCnt}>
                            <Text style={{color: COLOR.SUCCESS}}>{'Exit by ' + e.exitBy + ' in Gate ' + e.exitGateNo + ' on ' + toDateTimeString(e.exitAt)}</Text>
                          </View>
                          :
                          <View style={commentsStyles.cmntCnt}>
                            <Text style={{color: COLOR.ERROR}}>Still not exit.</Text>
                          </View>
                          }
                          {
                            !e.isExit &&
                            <>
                              {
                              this.state.editTakeawayId === e._id ?
                              <View style={styles.ele}>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => this.onUpdateTakeaway(d._id)}>
                                  <Image src={Images.tick} style={iconStyles.editIcon} />
                                </TouchableHighlight>
                                <TouchableHighlight style={{}} onClick={() => {
                                  this.setState({
                                    editTakeawayId: '',
                                    editTakeawayTitle: '',
                                    editTakeawayDesc: ''
                                  });
                                }}>
                                  <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                                </TouchableHighlight>
                              </View>
                              :
                              <View style={styles.ele}>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                                  this.setState({
                                    editTakeawayId: e._id,
                                    editTakeawayTitle: e.title,
                                    editTakeawayDesc: e.description
                                  });
                                }}>
                                  <Image src={Images.edit} style={iconStyles.editIcon} />
                                </TouchableHighlight>
                                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                                  this.onRemoveTakeaway(e._id, d._id);
                                }}>
                                  <Image src={Images.delete} style={iconStyles.deleteIcon} />
                                </TouchableHighlight>
                              </View>
                              }
                            </>
                          }
                        </View>
                        )
                      })
                      :
                      <Text style={{padding: 20}}>No takeaways found!!!</Text>
                    }
                  </View>
                </View>
                }
              </View>
            )
          })
          :
          <Text style={{padding: 20}}>No Servant found!!!</Text>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};