import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { INotification } from '../models/notification';
import { loadAll, push } from '../store/actions/notification';
import Page from '../components/home/manager/notification';
import { IUser } from '../models/user';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, sCallback: (result: Array<INotification>) => void, fCallback: (message: string) => void) => dispatch(loadAll(appClientId, sCallback, fCallback)),
    onPush: (appClientId: string, data: INotification, user: IUser, callback: (result: INotification | null) => void) => dispatch(push(appClientId, data, user, callback))
});

export const Notification = connect(mapStateToProps, mapDispatchToProps)(Page);