import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IDocument } from '../models/document';
import { loadAll, save, remove } from '../store/actions/documents';
import { upload } from '../store/actions/file';
import Page from '../components/home/my-documents';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    homeIndex: state.AppUser.homeIndex,
    user: state.User,
    documents: state.Documents
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (homeId: string, callback: (result: boolean) => void) => dispatch(loadAll(homeId, callback)),
    onSave: (homeId: string, data: IDocument, homeIndex: number, user: IUser, documents: Array<IDocument>, callback: (result: boolean) => void) => dispatch(save(homeId, data, homeIndex, user, documents, callback)),
    onRemove: (id: string, documents: Array<IDocument>, callback: (result: boolean) => void) => dispatch(remove(id, documents, callback)),
    onUpload: (data: any, callback: (result: boolean, message: string) => void) => dispatch(upload(data, callback))
});

export const MyDocuments = connect(mapStateToProps, mapDispatchToProps)(Page);