import { actionTypes } from '../action-types';
import { IPolls } from '../../models/store/polls';

const createEmptyResult = (): IPolls => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Polls = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_POLLS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IPolls, payload: IPolls): IPolls => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
