import * as React from 'react';
import Modal from 'react-responsive-modal';
import { TouchableHighlight, ScrollView, View, TextInput, Text, Image, Picker, TextArea } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { styles } from '../../../styles/page';
import { styles as modelStyles } from '../../../styles/model';
import {styles as commonStyles} from '../../../styles/common';
import { styles as mlistStyles} from '../../../styles/mlist';
import {styles as popupStyles} from '../../../styles/popup';
import { COLOR } from '../../../constants';
import { isNN } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import { IHome, IMember } from '../../../models/home';
import Images from '../../images';
import { styles as iconStyles } from '../../../styles/icon';
import Toast from  '../../../util/toast';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => void,
  onSave: (data: IHome, user: IUser, homes: IHomes, callback: (result: boolean, message: string) => void) => void,
  onCheckNumberAvail: (id: string, number: string, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  id: string,
	blockId: string,
	number: string,
	address: string,
  bedRoom: number,
  parking: string,
  description: string,
  members: Array<string>,
  isShowUsers: boolean
};

export default class EditHome extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      id: '',
      blockId: '',
      number: '',
      address: '',
      bedRoom: 1,
      parking: '',
      description: '',
      members: [],
      isShowUsers: false
    }
    this.getUsers = this.getUsers.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onToggleUserModel = this.onToggleUserModel.bind(this);
  };

  componentDidMount(){
    let homeId = this.props.match.params.id; // this.props.navigation.state.params.id;
    if(homeId.length > 0 && homeId !== '0'){
      this.setState({
        isLoading: true
      });
      this.props.onLoad(homeId, (result: IHome) => {
        if(result && isNN(result._id)){
          this.setState({
            isLoading: false,
            id: result._id,
            blockId: result._blockId,
            number: result.number,
            address: result.address,
            bedRoom: result.bedRoom,
            parking: result.parking,
            description: result.description,
            members: result.memberIds
          });
        }else{
          Toast.fail('Failed to load Home.');
        }
      }, (message: string) => {
        Toast.fail(isNN(message) ? message : 'Failed to load Home.');
      });
    }
    
    /*
    if(homeId > 0 && (this.props.homes.isUpToDate && !this.props.homes.isError && isNN(this.props.homes.data))){
      let homes = this.props.homes.data.filter((u: IHome, i: number) => u._id === homeId);
      if(isNNObject(homes) && homes.length > 0){
        let members = [] as Array<string>;
        if(this.props.userHomes.isUpToDate && !this.props.userHomes.isError && isNN(this.props.userHomes.data)){
          this.props.userHomes.data.filter((uh: IUserHome, i: number) => uh.homeId === homeId).map(uh => {
            members.push(uh.userId);
          });
        }
        this.setState({
          id: homes[0]._id,
          blockId: homes[0]._blockId,
          number: homes[0].number,
          address: homes[0].address,
          bedRoom: homes[0].bedRoom,
          parking: homes[0].parking,
          description: homes[0].description,
          members: members
        });
      }
    }
    */
  };

  componentWillUnmount() {};
  
  getUsers = () => {
    let users = '';
    if(isNN(this.state.members) && (this.props.users.isUpToDate && !this.props.users.isError && isNN(this.props.users.data))){
      this.props.users.data.filter(d => this.state.members.indexOf(d._id) >= 0).map((u: IMember, i: number) => 
        users += (users.length > 0 ? ', ' : '') + u.name + (isNN(u.number) ? '(' + u.number + ')' : '')
      );
    }
    return users;
  };

  onSave = () => {
    if(isNN(this.state.blockId) && isNN(this.state.number)){
      this.setState({
        isLoading: true
      });
      /*
      this.props.onCheckNumberAvail(this.state.id, this.state.number, (result: boolean) => {
        if(result === true){
          */
         let home = {
           _id: this.state.id,
           _blockId: this.state.blockId,
           number: this.state.number,
           address: this.state.address,
           bedRoom: this.state.bedRoom,
           parking: this.state.parking,
           description: this.state.description,
           memberIds: this.state.members
         } as IHome;
          this.props.onSave(home, this.props.user, this.props.homes, (result: boolean, message: string) => {
            this.setState({
              isLoading: false,
            });
            if(result === true){
              this.setState({
                id: '',
                blockId: '',
                number: '',
                address: '',
                bedRoom: 1,
                parking: '',
                description: '',
                members: []
              });
              Toast.success('Home saved successfully.');
            }else{
              Toast.fail(isNN(message) ? message : 'Failed to save home.');
            }
          });
          /*
        }else{
          this.setState({
            isLoading: false,
          });
          Toast.warn('Home with same Number already available. Please change the home number.');
        }
      });
      */
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onToggleUserModel = (isShow: boolean) => {
    this.setState({
      isShowUsers: isShow
    });
  }

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Homes', link: 'homes'}, {title: 'Edit Home', link: 'edit-home/0'}]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <Picker
            className='form-control'
            value={this.state.blockId}
            style={modelStyles.ele}
            onChange={(event: any) => this.setState({blockId: event.target.value})}>
              <option key={'block0'} value=''>Select Block</option>
              {(this.props.blocks && this.props.blocks.isUpToDate && !this.props.blocks.isError && isNN(this.props.blocks.data)) &&
                this.props.blocks.data.map((d: IBlock, i: number) => {
                  return(<option key={'block' + i} value={d._id}>{d.name}</option>);
                })
              }
            </Picker>
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Number" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.number} onChange={(event: any) => this.setState({number: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextArea placeholder="Address" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.address} onChange={(event: any) => this.setState({address: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <Text>No of bed rooms: </Text>
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.bedRoom === 1} text='1' onPress={() => this.setState({bedRoom: 1})} />
            <RadioButton isActive={this.state.bedRoom === 2} text='2' onPress={() => this.setState({bedRoom: 2})} />
            <RadioButton isActive={this.state.bedRoom === 3} text='3' onPress={() => this.setState({bedRoom: 3})} />
            <RadioButton isActive={this.state.bedRoom === 4} text='4' onPress={() => this.setState({bedRoom: 4})} />
            <RadioButton isActive={this.state.bedRoom === 5} text='5' onPress={() => this.setState({bedRoom: 5})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Parking" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.parking} onChange={(event: any) => this.setState({parking: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Description" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <Text>{'User(s): ' + this.getUsers()}</Text>
          </View>
          <View style={modelStyles.row}>
            <TouchableHighlight style={{marginRight: 25}} onClick={() => {
              this.setState({
                isShowUsers: true
              });
             }}>
              <Image src={Images.edit} style={iconStyles.editIcon} />
            </TouchableHighlight>
          </View>
          {(this.props.users.isUpToDate && !this.props.users.isError && isNN(this.props.users.data)) &&
          <Modal
          open={this.state.isShowUsers}
          onClose={() => this.onToggleUserModel(false)}>
            <View style={popupStyles.container}>
              <ScrollView style={{...mlistStyles.mListCnr, maxHeight: '50%'}}>
              {
                this.props.users.data.map((d: IMember, i: number) => {
                  return(
                    <Text key={'user' + i} style={(this.state.members.indexOf(d._id) >= 0 ? mlistStyles.mListActiveRow : mlistStyles.mListRow)} onClick={() => {
                      if(this.state.members.indexOf(d._id) >= 0){
                        this.setState({
                          members: this.state.members.filter(e => e !== d._id)
                        });
                      }else{
                        this.state.members.push(d._id);
                        this.setState({});
                      }
                    }}>{d.name + (d.number ? '(' + d.number + ')' : '')}</Text>
                  )
                })
              }
              </ScrollView>
              </View>
          </Modal>
          }
          <View style={modelStyles.saveBtn}>
            <Button
            title="SAVE"
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};