import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUsers } from '../models/store/users';
import { IUser } from '../models/user/user';
import { IMember } from '../models/home';
import { save, remove } from '../store/actions/member';
import Page from '../components/home/members';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    users: state.Users,
    homeIndex: state.AppUser.homeIndex
});
const mapDispatchToProps = (dispatch: any) => ({
    onSave: (data: IMember, user: IUser, homeIndex: number, sCallback: (result: boolean, message: string) => void) => dispatch(save(data, user, homeIndex, sCallback)),
    onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, sCallback: (result: boolean) => void) => dispatch(remove(id, homeIndex, user, users, sCallback))
});

export const Members = connect(mapStateToProps, mapDispatchToProps)(Page);