import { actionTypes } from '../action-types';
import { IBlocks } from '../../models/store/blocks';
import { IBlock } from '../../models/block/block';
import { IUser } from '../../models/user';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util';
import { IHome } from '../../models/home';
import { IHomes } from '../../models/store/homes';

let controller: string = 'block';

export const loadAll = (appClientId: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getBlockResult([], false, false, ''));
  new Api().get(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      dispatch(getBlockResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const save = (appClientId: string, data: IBlock, user: IUser, blocks: Array<IBlock>, homes: Array<IHome>, callback: (result: boolean, message: string) => void) => (dispatch: any) => {  
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        var foundIndex = blocks.findIndex(d => d._id === result.data);
        blocks[foundIndex]._id = result.data;
        blocks[foundIndex].name = data.name;
        blocks[foundIndex].description = data.description;
        dispatch(getBlockResult(blocks, true, false, ''));
        callback(true, '');
      }else{
        callback(false, message);
      }
    }, data);
  }else{
    new Api().post(controller + '/' + appClientId, (result: any, message: string) => {
      if(isNNObject(result) && isNNObject(result.data) && isNN(result.data.blockId))
      {
        blocks.unshift({
            _id: result.data.blockId,
            name: data.name,
            description: data.description,
            createdBy: user.name
          } as IBlock);
        dispatch(getBlockResult(blocks, true, false, ''));
        if(isNN(result.data.homes)){
          result.data.homes.forEach((h: any) => {
            h.memberIds = h.members;
            delete h.members;
          });
          homes = homes.concat(result.data.homes);
          dispatch(getHomesResult(homes, true, false, ''));
        }
        callback(true, '');
      }else{
        callback(false, message);
      }
    }, data);
  }
};

export const remove = (id: string, blocks: Array<IBlock>, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      blocks = blocks.filter((d, i) => d._id !== id);
      dispatch(getBlockResult(blocks, true, false, ''));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getBlockResult = (data: Array<IBlock>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_BLOCKS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IBlocks
});
const getHomesResult = (data: Array<IHome>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_HOMES,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IHomes
});