import { actionTypes } from '../action-types';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home';
import { IHomes } from '../../models/store/homes';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'home';

export const loadAll = (appClientId: string, sCallback: (result: Array<IHome>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result) && isNN(result.data)) {
      result.data.forEach((h: any) => {
        h.memberIds = h.members;
        delete h.members;
      });
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Homes.');
    }
  });
};

export const load = (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + '/' + id, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data) && isNN(result.data._id)) {
      result.data.memberIds = result.data.members;
      delete result.data.members;
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Home.');
    }
  });
};

export const save = (data: IHome, user: IUser, homes: IHomes, callback: (result: boolean, message: string) => void) => (dispatch: any) => {  
  // update
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNNObject(result.data) && result.data.length > 0)
      { 
        if(homes.isUpToDate && !homes.isError && isNN(homes.data)){
          var foundIndex = homes.data.findIndex(d => d._id === result.data);
          homes.data[foundIndex]._id = result.data;
          homes.data[foundIndex].number = data.number;
          dispatch(getHomesResult(homes.data, true, false, ''));
        }
        callback(true, '');
      }else{
        callback(false, message);
      }
    }, data);
  }else{
    new Api().post(controller + '/0', (result: any, message: string) => {
      if(isNNObject(result) && isNNObject(result.data) && result.data.length > 0)
      { 
        if(homes.isUpToDate && !homes.isError && isNN(homes.data)){
            homes.data.push({
              _id: result.data,
              // name: name,
              number: data.number
            } as IHome);
          dispatch(getHomesResult(homes.data, true, false, ''));
        }
        callback(true, '');
      }else{
        callback(false, message);
      }
    }, data);
  }
};

export const checkNumberAvail = (id: string, number: string, callback: (result: boolean) => void) => (dispatch: any) => {  
  new Api().get(controller + '/checkno/' + number, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    { 
      callback(true);
    }else{
      callback(false);
    }
  });
};

export const remove = (id: string, user: IUser, homes: IHomes, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      user.homes = user.homes.filter((d, i) => d._id !== id);
      homes.data = homes.data.filter((d, i) => d._id !== id);
      ////userHomes.data = userHomes.data.filter((d, i) => d.homeId !== id);
      dispatch(getUserResult(user));
      dispatch(getHomesResult(homes.data, true, false, ""));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getUserResult = (data: IUser) => ({
  type: actionTypes.LOAD_USER,
  payload: data
});

const getHomesResult = (data: Array<IHome>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_HOMES,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IHomes
});