import * as React from 'react';
import { View, TouchableHighlight, Image } from '../common/elements';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home/home';
import { ITransaction } from '../../models/home/transaction';
import MyDues from './my-dues';
import History from './history';
import Images from '../images';
import CustomList from './custom-list';
import { styles as cardStyles } from '../../styles/card';
import { COLOR } from '../../constants';

interface IProps {
  history: any,
  user: IUser,
  home: IHome
  onPaymentUpload: (file: any, homeId: string, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => void
};

interface IState{
  activeTab: number
};

export default class Home extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeTab: 1
    }
  };

  componentWillUnmount() {};
  
  render() {
    return(
      <main style={styles.content}>
        {/*<View style={{flex: 1, flexDirection: 'row', marginTop: 1, marginBottom: 2}}>
          <Text style={{flex: 1, justifyContent: 'flex-start', padding: 5}}>{new Date().toLocaleDateString()}</Text>
          <Text style={{flex: 1, justifyContent: 'flex-end', alignItems:'flex-end', textAlign: "right", padding: 5}}>{new Date().toLocaleTimeString()}</Text>
        </View>*/}
        <View className='fl' style={cardStyles.container}>
            <View style={cardStyles.row}>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('members')}>
                  <Image src={Images.members} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                  {(this.props.home.members ? this.props.home.members.length + '' : '') + ' MEMBER' + ((this.props.home.members.length === 0 || this.props.home.members.length > 1) ? 'S' : '')}
                  </View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('vehicles')}>
                  <Image src={Images.vehicle} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                  {(this.props.home.vehicles ?  this.props.home.vehicles.length + '' : '') + ' VEHICLE' + ((this.props.home.vehicles.length === 0 || this.props.home.vehicles.length > 1) ? 'S' : '')}
                  </View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('servants')}>
                  <Image src={Images.servant} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>SERVANT(S)</View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('tickets')}>
                  <Image src={Images.support} style={cardStyles.img} />
                  {/* <Text style={cardStyles.txt}>
                  {(this.props.home.tickets ? this.props.home.tickets.length + '' : '') + ' TICKET' + ((this.props.home.tickets.length === 0 || this.props.home.tickets.length > 1) ? 'S' : '')}
                  </Text> */}
                  <View className='pointer' style={cardStyles.txt}>TICKET(S)</View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('visitors')}>
                  <Image src={Images.visitors} style={cardStyles.img} />
                  {/* <Text style={cardStyles.txt}>
                  {(this.props.home.visitors ? this.props.home.visitors.length + '' : '') + ' VISITOR' + ((this.props.home.visitors.length === 0 || this.props.home.visitors.length > 1) ? 'S' : '')}
                  </Text> */}
                  <View className='pointer' style={cardStyles.txt}>VISITOR(S)</View>
                </View>
            </View>
        </View>
        <View style={{flex: 1, flexDirection: 'column', width: '100%', marginBottom: 2}}>
          <View className='tab-bar' style={{flex: 1, flexDirection: 'row'}}>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 1 ? {...styles.tab, ...styles.activeTab}: styles.tab)} onClick={() => this.setState({activeTab: 1})}>          
              MY DUES
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 2 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 2})}>
              ADVANCE
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 3 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 3})}>
              HISTORY
            </TouchableHighlight>
            {/* <TouchableHighlight className='tab' style={(this.state.activeTab === 4 ? {...styles.tab, ...styles.activeTab} : styles.activeTab)} onClick={() => this.setState({activeTab: 4})}>
              DEPOSITS
            </TouchableHighlight> */}
          </View>
          <View style={{flex: 1, flexDirection: 'row', padding: 10}}>
          {this.state.activeTab === 1 ?
              <MyDues history={this.props.history} user={this.props.user} home={this.props.home} onPaymentUpload={this.props.onPaymentUpload} onPaymentUpdate={this.props.onPaymentUpdate} />
            :
            this.state.activeTab === 2 ?
              <CustomList data={[]} message="No advance found!" />
              :
              this.state.activeTab === 3 ?
              <History history={this.props.history} home={this.props.home} />
              :
              this.state.activeTab === 4 ?
              <CustomList data={[]} message="Work in progress" />
              :
              []
            }
          </View>
        </View>        
      </main>
    );
  }
};

const styles = {
  content: {
    flex: 1,
    // backgroundColor: 'transparent',
    width: '100%',
  },
  tab: {
    flex: 1,
    paddingTop: 5,
    paddingBottom: 5,
    alignItems: 'center'
  },
  activeTab: {
    borderBottomWidth: 1,
    borderBottomColor: COLOR.PRIMARY,
    borderBottomStyle: 'solid',
    color: COLOR.PRIMARY
  }
};