import * as React from 'react';
import { View, Text, ScrollView } from '../common/elements';
import { IHome } from '../../models/home/home';
import { IPayment } from '../../models/home';
import { ITransaction } from '../../models/home/transaction';
import { isNN } from '../../util';
import { toDateString } from '../../util/convert';
import { MONTH, COLOR } from '../../constants';

interface IProps {
  history: any,
  home: IHome,
};

interface IState {
  activeTab: number
};

export default class History extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeTab: 1
    }
  };

  componentWillUnmount() {};
  
  render() {
    return (
      <ScrollView style={{}}>
        {isNN(this.props.home.payments.filter((d: IPayment, i: number) => isNN(d.transactions) && isNN(d.transactions.filter((t: ITransaction, j: number) => t.isSuccess === true)))) ?
        this.props.home.payments.filter((d: IPayment, i: number) => isNN(d.transactions) && isNN(d.transactions.filter((t: ITransaction, j: number) => t.isSuccess === true))).slice(0, 3).map((d: IPayment, i: number) => {
          let amountPaid = 0;
          let paidDate = null;
          d.transactions.filter(t => t.isSuccess === true).map((t: ITransaction, j: number) => {
            amountPaid += t.amount;
            if(j === 0){
              paidDate = t.createdAt;
            }
            return 1;
          });
              return (
                <View key={'my-his' + i} style={(i === 0) ? styles.myDueFirst : styles.myDue }>
                  <View style={{ marginTop: 4 }}>
                    <Text style={{}}>{"Paid for: " + MONTH.filter(m => m.value === d.forMonth)[0].title + "/" + d.forYear}</Text>
                  </View>
                  <View style={{ width: '100%', marginTop: 4, marginBottom: 3 }}>
                    <Text style={{ fontSize: 17 }}>{"Rs " + amountPaid}</Text>
                  </View>
                  <View style={{ width: '100%'}}>
                    <Text>{"Paid On: " + toDateString(paidDate)}</Text>
                  </View>
                </View>
              )
          })
          :<Text style={{ padding: 20 }}>No History found</Text>
        }
        </ScrollView>
    );
  }
};

const styles = {
  myDue: {
    margin: 7, 
    paddingLeft: 15,
    borderLeftColor: COLOR.HR_COLOR, 
    borderLeftWidth: 1 
  },
  myDueFirst: {
    margin: 7
  }
};
