import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IAppUser } from '../models/app-user';
import { IUser } from '../models/user/user';
import { ITickets } from '../models/store/tickets';
import { ITicket } from '../models/home';
import { ITicketComment } from '../models/home/ticket-comment';
import { loadAll, save, remove, resolve, start, addComment, removeComment } from '../store/actions/ticket';
import Page from '../components/home/tickets';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    tickets: state.Tickets
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, callback: (result: boolean) => void) => dispatch(loadAll(appClientId, homeId, callback)),
    onSave: (data: ITicket, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => dispatch(save(data, user, homeIndex, tickets, sCallback)),
    onRemove: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => dispatch(remove(id, user, homeIndex, tickets, sCallback)),
    onResolve: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => dispatch(resolve(id, user, homeIndex, tickets, sCallback)),
    onStart: (id: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => dispatch(start(id, user, homeIndex, tickets, sCallback)),
    onAddComment: (ticketId: string, comment: ITicketComment, appUser: IAppUser, user: IUser, tickets: ITickets, callback: (result: boolean) => void) =>  dispatch(addComment(ticketId, comment, appUser, user, tickets, callback)),
    onRemoveComment: (id: string, ticketId: string, user: IUser, homeIndex: number, tickets: ITickets, sCallback: (result: boolean) => void) => dispatch(removeComment(id, ticketId, user, homeIndex, tickets, sCallback))
});

export const Tickets = connect(mapStateToProps, mapDispatchToProps)(Page);