import { actionTypes } from '../action-types';
import { IUser } from '../../models/user';
import { IOutings } from '../../models/store/outings';
import { IOuting } from '../../models/outing';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'outing';

export const loadAll = (appClientId: string, homeId: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getOutingResult([], false, false, ''));
  new Api().get(controller + 's' + (isNN(homeId) ? '/' + homeId : ''), (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      dispatch(getOutingResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const save = (data: IOuting, homeId: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => (dispatch: any) => {
  if(data && isNN(data._id)){
    // Not Implemented
        callback(false);
  }else{
    new Api().post(controller + '/' + homeId, (result: any, message: string) => {
      console.log('result:', result);
      if(isNNObject(result) && isNN(result.data))
      {
        data._id = result.data;
        data.createdBy = user.name;
        outings.data.unshift(data);
        dispatch(getOutingResult(outings.data, true, false, ''));
        callback(true);
      }else{
        callback(false);
      }
    },data);
  }
};

export const retunBack = (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => (dispatch: any) => {  
  new Api().delete(controller + '/return/' + id, (result: any, message: string) => {
    debugger;
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      var foundIndex = outings.data.findIndex(d => d._id === id);
      outings.data[foundIndex].isReturned = true;
      outings.data[foundIndex].returnedAt = new Date();
      dispatch(getOutingResult(outings.data, true, false, ''));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getOutingResult = (data: Array<IOuting>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_OUTINGS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IOutings
});