import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IVisitor } from '../models/home';
import { loadAll, enter, exit } from '../store/actions/visitor';

import Page from '../components/home/gatekeeper/visitors';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser,
    homes: state.Homes,
    gateNo: state.GateNo
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(appClientId, homeId, isActive, sCallback, fCallback)),
    onEnter: (id: string, gateNo: string, callback: (result: boolean) => void) => dispatch(enter(id, gateNo, callback)),
    onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => dispatch(exit(id, gateNo, callback))
});

export const AllVisitors = connect(mapStateToProps, mapDispatchToProps)(Page);