import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IServant, ITakeaway } from '../models/home';
import { loadAllByHome, save, remove, createTakeaway, removeTakeaway } from '../store/actions/servant';
import Page from '../components/home/servants';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    homeIndex: state.AppUser.homeIndex
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => dispatch(loadAllByHome(homeId, sCallback, fCallback)),
    onSave: (homeId: string, data: IServant, sCallback: (result: boolean) => void) => dispatch(save(homeId, data, sCallback)),
    onRemove: (id: string, sCallback: (result: boolean) => void) => dispatch(remove(id, sCallback)),
    onCreateTakeaway: (id: string, data: ITakeaway, sCallback:(result: string) => void, fCallback:(message: string) => void) => dispatch(createTakeaway(id, data, sCallback, fCallback)),
    onRemoveTakeaway: (id: string, sCallback: (result: boolean) => void) => dispatch(removeTakeaway(id, sCallback))
});

export const Servants = connect(mapStateToProps, mapDispatchToProps)(Page);