import * as React from 'react';
import Modal from 'react-responsive-modal';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Text, Image, TextInput, TextArea } from '../../common/elements';
import DatePicker from 'react-date-picker';
import Button from '../../common/button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import { IPolls } from '../../../models/store/polls';
import { IPoll } from '../../../models/poll/index';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { styles } from "../../../styles/page";
import { styles as iconStyles } from "../../../styles/icon";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import {styles as popupStyles} from '../../../styles/popup';
import Images from '../../images';
import { COLOR } from '../../../constants';
import { toDateString } from '../../../util/convert';
import { isNN, isNNObject } from '../../../util/index';
import { percentage } from '../../../util/calc';
import Toast from  '../../../util/toast';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  polls: IPolls,
  onSave: (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, polls: Array<IPoll>, userID: string, sCallback: (result: boolean) => void) => void
};

interface IState{
  isEdit: boolean,
  isLoading: boolean,
  id: string,
  topic: string,
  description: string,
  isAllowMultiple: boolean,
  isOneVotePerFlat: boolean,
  isAnonymous: boolean,
  fromDate: any,
  toDate: any,
  pollOption: Array<string>,
  showVotePoll: IPoll,
  datePickerType: string,
  datePickerDate: Date
};

export default class Poll extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isEdit: false,
      isLoading: false,
      id: '',
      topic: '',
      description: '',
      isAllowMultiple: false,
      isOneVotePerFlat: false,
      isAnonymous: false,
      fromDate: new Date(),
      toDate: new Date(),
      pollOption: [],
      showVotePoll: { _id: '' } as IPoll,
      datePickerType: '',
      datePickerDate: new Date()
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};
  
   onSave = () => {
    if(isNN(this.state.topic) && isNN(this.state.description) && isNNObject(this.state.fromDate) && isNNObject(this.state.toDate) && isNN(this.state.pollOption.filter(po => isNN(po)))){
        this.setState({
          isLoading: true
        });
        let poll = {
          _id: this.state.id,
          topic: this.state.topic,
          description: this.state.description,
          isAllowMultiple: this.state.isAllowMultiple,
          isOneVotePerFlat: this.state.isOneVotePerFlat,
          isAnonymous: this.state.isAnonymous,
          fromDate: this.state.fromDate,
          toDate: this.state.toDate,
          options: this.state.pollOption.filter(po => isNN(po))
        } as IPoll;
        this.props.onSave(this.props.user.client._id, poll, this.props.polls.data, this.props.user, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              topic: '',
              description: '',
              isAllowMultiple: false,
              isOneVotePerFlat: false,
              isAnonymous: false,
              fromDate: new Date(),
              toDate: new Date(),
              pollOption: [],
              showVotePoll: { _id: '' } as IPoll,
              datePickerType: '',
              datePickerDate: new Date()
            });
            Toast.success('Poll saved successfully.');
          }else{
            Toast.fail('Failed to save pol.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.polls.data, this.props.user._id, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Poll removed successfully.');
              }else{
                Toast.fail('Failed to remove poll.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  loadVotes = () => {
    if(isNNObject(this.state.showVotePoll) && this.state.showVotePoll._id.length > 0){
      let votes = [] as any;
      this.state.showVotePoll.options.map((po: string, poi: number) => 
        votes.push({
          option: po,
          votes: this.state.showVotePoll.votes.filter(v => v.option === poi).length
        })
      );
      return(
        <View style={{padding: 15}}>
          <View style={styles.ele}>
            <Text style={{fontWeight: 'bold'}}>{this.state.showVotePoll.topic}</Text>
          </View>
          <View style={styles.ele}>
            <Text>{this.state.showVotePoll.description}</Text>
          </View>
          <View style={styles.ele}>
            <Text style={{fontWeight: 'bold'}}>{'Votes: ' + this.state.showVotePoll.votes.length}</Text>
          </View>
        {
        votes.sort(function(a: any, b: any){return b.votes - a.votes}).map((vo: any, voi: number) => {
          return(
            <View key={'pvo' + voi} style={styles.ele}>
              <Text>
                {vo.votes + ' (' + percentage(vo.votes, this.state.showVotePoll.votes.length) +  '%) - ' + vo.option}
              </Text>
            </View>
          )
        })
        }
        </View>
      )
    }else{
      return "";
    }
  };

  render() {
    return(
      <View>
        {this.state.isEdit === false &&
        <View style={{...styles.row, paddingBottom: 5}}>
          <Button
          title="ADD NEW"
          onPress={() => {
            this.setState({
              isEdit: true,
              id: '',
              topic: '',
              description: '',
              isAllowMultiple: false,
              isOneVotePerFlat: false,
              isAnonymous: false,
              fromDate: new Date(),
              toDate: new Date(),
              pollOption: [],
              showVotePoll: { _id: '' } as IPoll,
              datePickerType: '',
              datePickerDate: new Date()
             });
          }}
          />
        </View>
        }
        {
         (this.state.isEdit === true && this.state.id.length === 0)  ?
         <View key={"p0"} style={{...styles.row, borderBottomWidth: 1, borderBottomColor: COLOR.HR_COLOR, borderBottomStyle: 'solid'}}>
          <View style={styles.ele}>
            <TextInput type='text' placeholder="Topic" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.topic} onChange={(event: any) => this.setState({topic: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                fromDate: date
              });
            }}
            value={this.state.fromDate}
            />
          </View>
          <View style={styles.ele}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                toDate: date
              });
            }}
            value={this.state.toDate}
            />
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isAllowMultiple} text='Allow Multiple Votes' onPress={() => this.setState({isAllowMultiple: !this.state.isAllowMultiple})} />
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isOneVotePerFlat} text='One Vote per Flat' onPress={() => this.setState({isOneVotePerFlat: !this.state.isOneVotePerFlat})} />        
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isAnonymous} text='Anonymous Voting' onPress={() => this.setState({isAnonymous: !this.state.isAnonymous})} />
          </View>
          <View style={styles.ele}>
            <Text>Options: </Text>
          </View>
          {
            isNN(this.state.pollOption) ?
            this.state.pollOption.map((e: string, j: number) => {
              return(
                <View key={'pop' + j} style={styles.ele}>
                  <TextInput type='text' key={'popt' + j} placeholder={"Option" + (j + 1)} className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={e} onChange={(event: any) => {
                    let newOptions = this.state.pollOption;
                    // var foundIndex = newOptions.findIndex(f => f.id === e.id);
                    newOptions[j] = event.target.value;
                    this.setState({pollOption: newOptions});
                  }} />
                  <TouchableHighlight style={{}} onClick={() => {
                    let newOptions = this.state.pollOption;
                    delete newOptions[j];
                    this.setState({pollOption: newOptions});
                  }}>
                    <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>
                </View>
              )
            })
            :
            <View style={styles.ele}>
              <Text>No Option found!!!</Text>
            </View>
          }
            <View style={styles.ele}>
              <TouchableHighlight
              onClick={() => {
                if(!isNN(this.state.pollOption.filter(h => !isNN(h))))
                {
                  let newOptions = this.state.pollOption;
                  newOptions.push('');
                  this.setState({pollOption: newOptions});
                }
              }}
              >
                <Text>Add More +</Text>
              </TouchableHighlight>
            </View>
          <View style={styles.ele}>
            <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
              <Image src={Images.tick} style={iconStyles.editIcon} />
            </TouchableHighlight>
            <TouchableHighlight style={{}} onClick={() => {
              this.setState({
                isEdit: false,
                id: '',
                topic: '',
                description: '',
                isAllowMultiple: false,
                isOneVotePerFlat: false,
                isAnonymous: false,
                fromDate: new Date(),
                toDate: new Date(),
                pollOption: []
              });
            }}>
              <Image src={Images.cancel} style={iconStyles.deleteIcon} />
            </TouchableHighlight>
          </View>
        </View>
        :
        []
        }
      {
      (isNNObject(this.props.polls) && this.props.polls.isUpToDate && !this.props.polls.isError && isNN(this.props.polls.data)) ?      
      this.props.polls.data.map((d: IPoll, i: number) => {
      return(
        (this.state.isEdit === true && this.state.id === d._id) ?
        <View key={"p" + d._id} style={{...styles.row, borderBottomWidth: 1, borderBottomColor: COLOR.HR_COLOR, borderBottomStyle: 'solid'}}>
          <View style={styles.ele}>
            <TextInput type='text' placeholder="Topic" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.topic} onChange={(event: any) => this.setState({topic: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                fromDate: date
              });
            }}
            value={this.state.fromDate}
            />
          </View>
          <View style={styles.ele}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                toDate: date
              });
            }}
            value={this.state.toDate}
            />
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isAllowMultiple} text='Allow Multiple Votes' onPress={() => this.setState({isAllowMultiple: !this.state.isAllowMultiple})} />
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isOneVotePerFlat} text='One Vote per Flat' onPress={() => this.setState({isOneVotePerFlat: !this.state.isOneVotePerFlat})} />        
          </View>
          <View style={styles.ele}>
            <Checkbox isActive={this.state.isAnonymous} text='Anonymous Voting' onPress={() => this.setState({isAnonymous: !this.state.isAnonymous})} />
          </View>
          <View style={styles.ele}>
            <Text>Options: </Text>
          </View>
          {
            isNN(this.state.pollOption) ?
            this.state.pollOption.map((e: string, j: number) => {
              return(
                <View key={'pop' + j} style={styles.ele}>
                  <TextInput type='text' key={'popt' + j} placeholder={"Option" + (j + 1)} className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={e} onChange={(event: any) => {
                    let newOptions = this.state.pollOption;
                    // var foundIndex = newOptions.findIndex(f => f.id === e.id);
                    newOptions[j] = event.target.value;
                    this.setState({pollOption: newOptions});
                  }} />
                  <TouchableHighlight style={{}} onClick={() => {
                    let newOptions = this.state.pollOption;
                    delete newOptions[j];
                    this.setState({pollOption: newOptions});
                  }}>
                    <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>
                </View>
              )
            })
            :
            <View style={styles.ele}>
              <Text>No Option found!!!</Text>
            </View>
          }
            <View style={styles.ele}>
              <TouchableHighlight
              onClick={() => {
                if(!isNN(this.state.pollOption.filter(h => !isNN(h))))
                {
                  let newOptions = this.state.pollOption;
                  newOptions.push('');
                  this.setState({pollOption: newOptions});
                }            
              }}
              >
                <Text>Add More +</Text>
              </TouchableHighlight>
            </View>
          <View style={styles.ele}>
            <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
              <Image src={Images.tick} style={iconStyles.editIcon} />
            </TouchableHighlight>                
            <TouchableHighlight style={{}} onClick={() => {
              this.setState({
                isEdit: false,
                id: '',
                topic: '',
                description: '',
                isAllowMultiple: false,
                isOneVotePerFlat: false,
                isAnonymous: false,
                fromDate: new Date(),
                toDate: new Date(),
                pollOption: []
              });
            }}>
              <Image src={Images.cancel} style={iconStyles.deleteIcon} />
            </TouchableHighlight>        
          </View>
        </View>
        :
        <View key={"p" + d._id} style={{...styles.row, borderBottomWidth: 1, borderBottomColor: COLOR.HR_COLOR, borderBottomStyle: 'solid'}}>
          <View style={styles.ele}>
            <Text style={{fontWeight: 'bold'}}>{d.topic}</Text>
          </View>
          <View style={styles.ele}>
            <Text>{d.description}</Text>
          </View>
          <View style={styles.ele}>
            <Text>{toDateString(d.fromDate) + " to " + toDateString(d.toDate)}</Text>
          </View>
          <View style={styles.ele}>
            <View 
            style={(d.isAllowMultiple === true ? modelStyles.radioActive : {...modelStyles.radio, marginLeft: 5})}
            >
              <Text style={(d.isAllowMultiple === true ? {...modelStyles.radioActiveText} : {...modelStyles.radioText})}>Allow Multiple Votes</Text>
            </View>
          </View>
          <View style={styles.ele}>
            <View 
            style={(d.isOneVotePerFlat === true ? {...modelStyles.radioActive} : {...modelStyles.radio, marginLeft: 5})}
             >
              <Text style={(d.isOneVotePerFlat === true ? {...modelStyles.radioActiveText} : {...modelStyles.radioText})}>One Vote per Flat</Text>
            </View>
          </View>
          <View style={styles.ele}>
            <View 
            style={(d.isAnonymous === true ? {...modelStyles.radioActive} : {...modelStyles.radio, marginLeft: 5})}
             >
              <Text style={(d.isAnonymous === true ? {...modelStyles.radioActiveText} : {...modelStyles.radioText})}>Anonymous Voting</Text>
            </View>
          </View>
          <View style={styles.ele}>
            <Text>Options: </Text>
          </View>
          {
            isNN(d.options) ?
            d.options.map((e: string, j: number) => {
              return(
                <View key={"po" + j} style={styles.ele}>
                  <Text key={"pot" + j} style={{width: '100%'}}>{e}</Text>
                </View>
              )
            })
            :
            <View style={styles.ele}>
              <Text style={{padding: 10}}>No Option found!!!</Text> 
            </View>           
          }
          <View style={styles.ele}>
          {(d.fromDate > new Date()) &&
            <TouchableHighlight style={{marginRight: 25}} onClick={() => {
              this.setState({
                isEdit: true,
                id: d._id,
                topic: d.topic,
                description: d.description,
                isAllowMultiple: d.isAllowMultiple,
                isOneVotePerFlat: d.isOneVotePerFlat,
                isAnonymous: d.isAnonymous,
                fromDate: d.fromDate,
                toDate: d.toDate,
                pollOption: (isNN(d.options) ? d.options : [])
              });
            }}>
              <Image src={Images.edit} style={iconStyles.editIcon} />
            </TouchableHighlight>
          }
            <TouchableHighlight style={{marginRight: 25}} onClick={() => {
              this.onRemove(d._id);
            }}>
              <Image src={Images.delete} style={iconStyles.deleteIcon} />
            </TouchableHighlight>
            <Text className='pointer' style={{color: COLOR.SECONDARY}} onClick={() => {
              this.setState({showVotePoll: d})
            }}>View Votes</Text>
          </View>
        </View>
      )
      })
      :
      <Text style={{padding: 20}}>{(this.props.polls.isUpToDate ? (this.props.polls.isError ? this.props.polls.errorMessage : "No poll found!!!") : "Loading...")}</Text>
    }
    <Modal
      open={(isNNObject(this.state.showVotePoll) && this.state.showVotePoll._id.length > 0)}
      onClose={() => this.setState({showVotePoll : { _id: '' } as IPoll})}>
      <View style={popupStyles.container}>
        <View style={popupStyles.content}>
        {
          this.loadVotes()
        }
        </View>
      </View>
    </Modal>
    <Spinner visible={this.state.isLoading} />
    </View>  
    );
  }
};