import * as React from 'react';
import { Link } from 'react-router-dom';

interface IProps {
  data: Array<{title: string, link: string}>
};

interface IState{
};

export default class Breadcrum extends React.Component<IProps, IState> {
  static defaultProps: IProps = {
    data: []
  }
  render() {
    return (
      <div style={styles.cnr}>
        <Link to='' style={styles.link}>Home</Link>
        {this.props.data.map((d, i) => {
          return(<>
          <span key={'bd' + i} style={styles.sep}>/</span>
          {(i === this.props.data.length - 1) ?
          <span key={'bc' + i} style={styles.link}>{d.title}</span>
          :
          <Link key={'bc' + i} to={'/' + d.link}>{d.title}</Link>
          }
          </>);
        })}
      </div>
    );
  }
}

const styles = {
  cnr: {
    width: '100%',
    MarginTop: 10,
    marginBottom: 10
  },
  sep:{
    marginRight: 10,
    marginLeft: 10
  },
  link: {

  }
};