import * as React from 'react';
import { View, Text, TouchableHighlight } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { styles as pageStyles } from '../../../../styles/page';
import { IPayment } from '../../../../models/home';
import { ITransaction } from '../../../../models/home/transaction';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { MONTH, COLOR } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IPayment>,
  viewId: string,
  transactions: Array<ITransaction>
};

export default class Payments extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      viewId: '',
      transactions: []
    }
    this.onView = this.onView.bind(this);
  };

  componentDidMount(){
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, (result: Array<IPayment>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Payments.');
      });
    }
  };

  componentWillUnmount() {}

  onView = (id: string, transactions: Array<ITransaction>) => {
    if(this.state.viewId === id){
      this.setState({
        viewId: '',
        transactions: []
      });
    }else{
      this.setState({
        viewId: id,
        transactions: transactions
      });
    }
  };

  getAmountPaid = (transactions: Array<ITransaction>) => {
    let amount = 0;
    if(isNN(transactions)){
      transactions.filter((d: ITransaction, i: number) => d.isSuccess === true).map((d:ITransaction, i: number) => amount += d.amount);
    }
    return amount;
  };

  render() {
    let { data } = this.state;
      return(
        <View>
        <View className='center' style={pageStyles.ele}>
          <Text style={{color: COLOR.PRIMARY, fontWeight: 'bold', fontSize: 17, marginBottom: 10}}>Payments</Text>
        </View>
          {isNN(data) ?
            data.map((d: IPayment, i: number) => {
            return(
              <View key={i} style={pageStyles.row}>
                    <View style={pageStyles.ele}>
                        <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                    </View>
                    <View style={pageStyles.ele}>
                      <Text>{'Rs. ' + d.amount + ' for ' + MONTH.filter(m => m.value === d.forMonth)[0].title  + "/" + d.forYear + (isNN(d.mode) ? ' ' + d.mode : '') + (isNN(d.type) ? ' ' + d.type : '')}</Text>
                    </View>
                    <View style={pageStyles.ele}>
                      <Text>{'Amount Paid Rs. ' + this.getAmountPaid(d.transactions)}</Text>
                    </View>
                    {isNN(d.description) &&
                      <View style={pageStyles.ele}>
                        <Text>{d.description}</Text>
                      </View>
                    }
                    <View style={pageStyles.ele}>
                      <TouchableHighlight style={{}} onClick={() => {
                        this.onView(d._id, d.transactions);
                      }}>
                        <Text className='pointer' style={{color: COLOR.SECONDARY}}>{(this.state.viewId === d._id ? 'Hide' : 'View') + ' Transactions'}</Text>
                      </TouchableHighlight>
                    </View>
                    {this.state.viewId === d._id &&
                    <View style={myStyles.cmntsCnr}>
                  <View style={myStyles.cmntsHdr}>
                    <Text style={myStyles.cmntsHdrTxt}>Transactions</Text>
                  </View>
                  <View style={myStyles.cmntsCnt}>
                    {isNN(this.state.transactions) ?
                      this.state.transactions.map((e: ITransaction, j: number) => {
                        return(
                        <View key={'tktcmt' + j} style={j === 0 ? myStyles.cmntCnrFst : myStyles.cmntCnr}>
                          <View style={myStyles.cmntHdr}>
                            <Text>{e.createdBy + ' on ' + toDateString(e.createdAt)}</Text>
                          </View>
                          <View style={myStyles.cmntCnt}>
                            <Text>{'Ref Id: ' + e.txnRef}</Text>
                          </View>
                          <View style={myStyles.cmntCnt}>
                            <Text>{'Txn Id: ' + e.txnId}</Text>
                          </View>
                          <View style={myStyles.cmntCnt}>
                            <Text>{'Amount: ' + e.amount}</Text>
                          </View>
                          <View style={myStyles.cmntCnt}>
                            <Text>{'Status: ' + (e.isSuccess ? 'SUCCESS' : 'FAILED')}</Text>
                          </View>
                          {
                            isNN(e.reason) &&
                            <View style={myStyles.cmntCnt}>
                              <Text>{'Reason: ' + e.reason}</Text>
                            </View>
                          }
                        </View>
                        )
                      })
                      :
                      <Text style={{padding: 20}}>No transactions found!!!</Text>
                    }
                  </View>
                </View>
                }
                  </View>
              );
          })
          :
          <Text>No data found!!!</Text>
        }
        </View>
      );
  }
};

const myStyles = {
  cmntsCnr: {
    width: '100%',
    marginTop: 5,
    marginBottom: 5
  },
  cmntsHdr: {
    borderBottomWidth: 1,
    borderBottomColor: COLOR.HR_COLOR, 
    'border-bottom-style': 'solid'
  },
  cmntsHdrTxt: {
    // fontWeight: 'bold',
  },
  cmntsCnt: {
    marginTop: 3,
    width: '100%',
  },
  cmntCnr: {
    width: '100%',
    paddingLeft: 3,
    paddingRight: 3,
    paddingTop: 5,
    paddingBottom: 5,
    borderTopWidth: 0.5,
    borderTopColor: COLOR.HR_COLOR
  },
  cmntCnrFst: {
    width: '100%',
    paddingLeft: 3,
    paddingRight: 3,
    paddingTop: 5,
    paddingBottom: 5
  },
  cmntHdr:{
  },
  cmntCnt:{
  }
};