import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Text, Image, TextInput, } from '../common/elements';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IVehicle } from '../../models/home/vehicle';
import { styles } from "../../styles/page";
import { styles as iconStyles } from "../../styles/icon";
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import Images from '../images';
import { isNN } from '../../util/index';
import { COLOR } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onSave: (data: IVehicle, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => void,
  onRemove: (id: string, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  id: string, 
  number: string, 
  type: string
};

export default class Vehicles extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      id: '', 
      number: '', 
      type: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.number) && isNN(this.state.type)){
        this.setState({
          isLoading: true
        });
        let vehicle = {
          _id: this.state.id,
          number: this.state.number,
          type: this.state.type
        } as IVehicle;
        this.props.onSave(vehicle, this.props.user, this.props.homeIndex, (result: boolean) => {          
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              number: '',
              type: ''
            });
            Toast.success('Vehicle saved successfully.');
          }else{
            Toast.fail('Failed to save vehicle.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.user, this.props.homeIndex, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Vehicle removed successfully.');
              }else{
                Toast.fail('Failed to remove vehicle.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Vehicles', link: 'vehicles'}]} />
        <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
          <Text className='fr pointer' onClick={() => this.props.history.push('new-vehicle')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
        </View>
        {isNN(this.props.user.homes[this.props.homeIndex].vehicles) ?
          this.props.user.homes[this.props.homeIndex].vehicles.map((d: IVehicle, i: number) => {
            return(
              (this.state.isEdit === true && this.state.id === d._id) ?
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <RadioButton isActive={this.state.type === 'TWO WHEELER'} text='Two Wheeler' onPress={() => this.setState({type: 'TWO WHEELER'})} />
                  <RadioButton isActive={this.state.type === 'FOUR WHEELER'} text='Four Wheeler' onPress={() => this.setState({type: 'FOUR WHEELER'})} />
                </View>
                <View style={styles.ele}>
                <TextInput type='text' placeholder="Number" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.number} onChange={(event: any) => this.setState({number: event.target.value})} />
                </View>
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                    <Image src={Images.tick} style={iconStyles.editIcon} />
                  </TouchableHighlight>                
                  <TouchableHighlight style={{}} onClick={() => {
                    this.setState({
                      isEdit: false,
                      id: '',
                      number: '', 
                      type: ''
                    });
                  }}>
                    <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>      
                </View>
              </View>
              :
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <Text>{d.type}</Text>                  
                </View>
                <View style={styles.ele}>
                  <Text>{d.number}</Text>
                </View>
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.setState({
                      isEdit: true,
                      id: d._id,
                      number: d.number,
                      type: d.type
                    });
                  }}>
                    <Image src={Images.edit} style={iconStyles.editIcon} />
                  </TouchableHighlight>                
                  <TouchableHighlight style={{}} onClick={() => {
                    this.onRemove(d._id);
                  }}>
                    <Image src={Images.delete} style={iconStyles.deleteIcon} />
                  </TouchableHighlight>      
                </View>
              </View>
            )
          })
          :
          <Text style={{padding: 20}}>No Vehicle found!!!</Text>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};