import * as React from 'react';
import { Route, Switch } from 'react-router-dom';
import { View, Text } from '../common/elements';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import TitleBar from './title-bar';
import NavBar from './nav-bar';
import TabBar from './tab-bar';
import { COLOR, ROLE } from '../../constants';
import { isNNObject, isNN } from '../../util';
import Toast from  '../../util/toast';

import { Dashboard as CDashboard} from '../../containers/dashboard';
import { Members as CMembers } from '../../containers/members';
import { NewMember as CNewMember } from '../../containers/new-member';
import { Vehicles as CVehicles } from '../../containers/vehicles';
import { NewVehicle as CNewVehicle } from '../../containers/new-vehicle';
import { Servants as CServants } from '../../containers/servants';
import { AllTakeaways as CAllTakeaways } from '../../containers/all-takeaways';
import { NewServant as CNewServant } from '../../containers/new-servant';
import { Tickets as CTickets } from '../../containers/tickets';
import { NewTicket as CNewTicket } from '../../containers/new-ticket';
import { Visitors as CVisitors } from '../../containers/visitors';
// import { NewVisitor as CNewVisitor } from '../../containers/new-visitor';
import { ChangePassword as CChangePassword } from '../../containers/change-password';
import { MyProfile as CMyProfile } from '../../containers/my-profile';
import { MyRole as CMyRole } from '../../containers/my-role';
import { Ads as CAds } from '../../containers/ads';
import { NewAd as CNewAd } from '../../containers/new-ad';
import { Users as CUsers } from '../../containers/users';
import { Homes as CHomes } from '../../containers/homes';
import { EditUser as CEditUser } from '../../containers/edit-user';
import { ApproveUsers as CApproveUsers } from '../../containers/approve-users';

import { EditHome as CEditHome } from '../../containers/edit-home';
import { AllVisitors as CAllVisitors } from '../../containers/all-visitors';
import { Notification as CNotification } from '../../containers/notification';
import { NewOuting as CNewOuting } from '../../containers/new-outing';
import { MyDocuments as CMyDocuments } from '../../containers/my-documents';
import { Blocks as CBlocks } from '../../containers/blocks';
import { EditBlock as CEditBlock } from '../../containers/edit-block';
import { Payment as CPayment } from '../../containers/payment';
import { ViewHome as CViewHome } from '../../containers/view-home';
import { Notice as CNotice } from '../../containers/notice';
import { Help as CHelp } from '../../containers/help';
import NotFound from '../../components/not-found';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  getProfile: (callback: (result: boolean, message: string) => void) => void,
  onHomeChange: (index: number, callback: (result: boolean) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean
};

export default class Index extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false
    }
    this.isUser = this.isUser.bind(this);
    this.isAdmin = this.isAdmin.bind(this);
    this.isManager = this.isManager.bind(this);
    this.isGateKeeper = this.isGateKeeper.bind(this);
  };

  componentDidMount() {
    if(!isNNObject(this.props.user) || !isNN(this.props.user._id)){
      this.setState({
          isLoading: true
      });
      this.props.getProfile( (result: boolean, message: string) => {
        this.setState({
            isLoading: false
        });
        if(result !== true){
          Toast.fail('Failed to load Profile: ' + message);
        }
      });
    }
  }; 

  componentWillUnmount() {};
  
  isUser = () => {
    return this.props.appUser.role === ROLE.USER;
  };

  isAdmin = () => {
    return this.props.appUser.role === ROLE.ADMIN;
  };
  
  isManager = () => {
    return this.props.appUser.role === ROLE.MANAGER;
  };

  isGateKeeper = () => {
    return this.props.appUser.role === ROLE.GATEKEEPER;
  };

  render() {
    return (
      !this.state.isLoading &&
        <View style={styles.container}>
          <TitleBar appUser={this.props.appUser} user={this.props.user} onHomeChange={this.props.onHomeChange} onLogout={this.props.onLogout}/>
          <NavBar appUser={this.props.appUser} user={this.props.user} />
            {((isNN(this.props.appUser.role) && !this.isUser()) || isNN(this.props.user.homes)) ?
            <Switch>
              <Route path={'/'} exact component={CDashboard} />
              {/* <Route path={'/login'} exact component={CLogin} /> */}
              {this.isUser() && <Route path={'/members'} exact component={CMembers} />}
              {this.isUser() && <Route path={'/new-member'} exact component={CNewMember} />}
              {this.isUser() && <Route path={'/vehicles'} exact component={CVehicles} />}
              {this.isUser() && <Route path={'/new-vehicle'} exact component={CNewVehicle} />}
              {this.isUser() && <Route path={'/servants'} exact component={CServants} />}
              {this.isUser() && <Route path={'/new-servant'} exact component={CNewServant} />}
              {(this.isUser() || this.isAdmin() || this.isManager()) && <Route path={'/tickets'} exact component={CTickets} />}
              {this.isUser() && <Route path={'/new-ticket'} exact component={CNewTicket} />}
              {this.isUser() && <Route path={'/visitors'} exact component={CVisitors} />}
              {this.isUser() && <Route path={'/new-outing'} exact component={CNewOuting} />}
              {/* {this.isUser() && <Route path={'/new-visitor'} exact component={CNewVisitor} />} */}
              {(this.isAdmin() || this.isManager()) && <Route path={'/ads'} exact component={CAds} />}
              {(this.isAdmin() || this.isManager()) && <Route path={'/new-ad'} exact component={CNewAd} />}
              {(this.isAdmin() || this.isManager()) && <Route path={'/notification'} exact component={CNotification} />}
              {this.isAdmin() && <Route path={'/users'} exact component={CUsers} />}
              {this.isAdmin() && <Route path={'/edit-user/:id/:isApprove'} exact component={CEditUser} />}
              {this.isAdmin() && <Route path={'/approve-users'} exact component={CApproveUsers} />}
              {this.isAdmin() && <Route path={'/homes'} exact component={CHomes} />}
              {this.isAdmin() && <Route path={'/view-home/:id'} exact component={CViewHome} />}
              {this.isAdmin() && <Route path={'/edit-home/:id'} exact component={CEditHome} />}
              {this.isAdmin() && <Route path={'/blocks'} exact component={CBlocks} />}
              {this.isAdmin() && <Route path={'/edit-block/:id'} exact component={CEditBlock} />}
              {(this.isAdmin() || this.isManager()) && <Route path={'/payment'} exact component={CPayment} />}
              {(this.isAdmin() || this.isManager() || this.isGateKeeper()) && <Route path={'/all-visitors'} exact component={CAllVisitors} />}
              {(this.isAdmin() || this.isManager() || this.isGateKeeper()) && <Route path={'/all-takeways'} exact component={CAllTakeaways} />}
              <Route path={'/notice'} exact component={CNotice} />
              <Route path={'/my-profile'} exact component={CMyProfile} />
              {this.props.user.role.indexOf(',') > 0 && <Route path={'/my-role'} exact component={CMyRole} />}
              <Route path={'/my-documents'} exact component={CMyDocuments} />
              <Route path={'/change-password'} exact component={CChangePassword} />
              <Route path={'/info'} exact component={CHelp} />              
              <Route component={NotFound} />
            </Switch>
            :
            <Text style={{margin: 20, padding: 15, paddingLeft: 59}}>No home assigned to you. Please contact administrator/manager.</Text>
            }
          {(isNNObject(this.props.user) && isNN(this.props.user._id)) &&
          <TabBar />
          }
          <Spinner visible={this.state.isLoading} text={"Loading..."} />
        </View>
    );
  }
}

const styles = {
  container: {
    flex: 1,
    backgroundColor: COLOR.INVERSE,
    alignItems: 'flex-start',
    justifyContent: 'flex-start'
  }
};