import { actionTypes } from '../action-types';
import { IAds } from '../../models/store/ads';

const createEmptyResult = (): IAds => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Ads = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_ADS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IAds, payload: IAds): IAds => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
