import * as React from 'react';
import Modal from 'react-responsive-modal';
import { View, Image, Text, TextInput } from '../../common/elements';
import Checkbox from '../../common/checkbox';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home/visitor';
import { styles as pageStyles } from "../../../styles/page";
import {styles as CommonStyles} from '../../../styles/common';
import { styles as modelStyles } from '../../../styles/model';
import { isNN } from '../../../util/index';
import { FILE_BASE_PATH, ROLE } from '../../../constants';
import Button from '../../common/button';
import { toDateTimeString } from '../../../util/convert';
import { COLOR } from '../../../constants';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  data: Array<IVisitor>,
  isActive: boolean,
  isSearch: boolean,
  onActiveChange: () => void,
  onEnter: (id: string) => void,
  onExit: (id: string) => void,
  onApprove: (id: string) => void,
  onReject: (id: string) => void,
  onSelect: (data: IVisitor) => void
};

interface IState{
  isZoomImage: boolean,
  zoomImage: string,
  searchText: string
};

export default class VisitorsView extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isZoomImage: false,
      zoomImage: '',
      searchText: ''
    }
    this.onToggleZoomImage = this.onToggleZoomImage.bind(this);
  };
  static defaultProps = {
    isSearch: false,
    onActiveChange: () => {},
    onEnter: () => {},
    onExit: () => {},
    onApprove: () => {},
    onReject: () => {},
    onSelect: () => {}
  };
  
  componentWillUnmount(){};

  onToggleZoomImage = (isZoom: boolean) => {
    this.setState({
      isZoomImage: isZoom
    });
  };

  render() {
    let { searchText } = this.state;
    searchText = isNN(searchText) ? searchText.toLowerCase() : searchText;
    let data = ((isNN(searchText) && isNN(this.props.data)) ? this.props.data.filter((d, i) => d.homeNumber.toLowerCase().indexOf(searchText) >= 0 || d.passCode.toLowerCase().indexOf(searchText) >= 0 || d.name.toLowerCase().indexOf(searchText) >= 0) : this.props.data);
    return(<>
      <View style={pageStyles.row}>
        <Text>{'No of ' + (this.props.isActive ? 'Active' : '') + ' Visitors: ' + this.props.data.length}</Text>
        {(!this.props.isSearch && this.props.appUser.role !== ROLE.GATEKEEPER) &&
        <Checkbox isActive={this.props.isActive} text='Only Active Visitors' onPress={this.props.onActiveChange} className='fr' />
        }
      </View>
      {isNN(this.props.data) &&
      <View style={{width: '100%', marginTop: 10, marginBottom: 5}}>
        <TextInput type='text' placeholder="Enter Search Text" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele, display: 'inline', width: '100%'}} value={this.state.searchText} onChange={(event: any) => this.setState({searchText: event.target.value})} />
      </View>
      }
      <View className='fl' style={styles.cardContainer}>
        {isNN(data) ?
        data.map((d: IVisitor, i: number) => {
          return(
            <View key={i} className='fl' style={styles.card}>
              {isNN(d.image) &&
              <View className='center'>
                <Image src={FILE_BASE_PATH + d.image} className='pointer' style={styles.img} onClick={() => this.setState({isZoomImage: true, zoomImage: d.image})} />
              </View>  
              }
              <View style={styles.content}>
                {isNN(d.passCode) &&
                <View style={pageStyles.ele}>
                  <Text className='bold'>{d.passCode}</Text>
                </View>
                }
                <View style={pageStyles.ele}>
                  <Text className='bold'>{d.name + ' [' + d.type + ']'}</Text>
                </View>
                {(d.age > 0 || isNN(d.gender)) && 
                <View style={pageStyles.ele}>
                  <Text>{(d.age > 0 ? d.age : '') + ((d.age > 0 && isNN(d.gender)) ? ', ' : '') + (isNN(d.gender) ? (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other')) : '')}</Text>
                </View>
                }
                <View style={pageStyles.ele}>
                  To <Text className='bold'>{d.homeNumber}</Text> on <Text className='bold'>{toDateTimeString(d.createdAt)}</Text>
                </View>
                {(!d.isEntered && !d.isExit) &&
                <View style={pageStyles.ele}>
                  <Text>{((d.gateNo) ? 'In gate ' + d.gateNo + ' by ': 'By ') + d.createdBy}</Text>
                </View>
                }
                {isNN(d.mobileNo) && 
                <View style={pageStyles.ele}>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) && 
                <View style={pageStyles.ele}>
                  <Text>{d.emailId}</Text>
                </View>
                }
                {d.personCount > 0 && 
                <View style={pageStyles.ele}>
                  <Text>{d.personCount + ' Person' + (d.personCount > 1 ? 's' : '') + (d.vehicleType === 'WALK' ? ' by WALK' : ' in ' + d.vehicleType + (isNN(d.vehicleNo) ? ' ' + d.vehicleNo : ''))}</Text>
                </View>
                }
                {(!d.isApproved && !d.isRejected && !d.isEntered) && 
                <View style={pageStyles.ele}>
                  <View style={{color: COLOR.PRIMARY}}>WAITING FOR APPROVAL</View>
                </View>
                }
                {(d.isApproved || d.isRejected ) && 
                <View style={pageStyles.ele}>
                  <View style={{color: (d.isApproved ? COLOR.SUCCESS : COLOR.ERROR)}}>{d.isApproved ? 'APPROVED' : 'REJECTED'}</View>
                  <View>{'By ' + d.approvedBy}</View>
                  <View>{'On ' + toDateTimeString(d.approvedAt)}</View>
                </View>
                }
                {d.isEntered &&
                <View style={pageStyles.ele}>
                  <View>{'Entry By ' + d.enteredBy + ' in gate ' + d.gateNo}</View>
                  <View>{'On ' + toDateTimeString(d.enteredAt)}</View>
                </View>
                }
                {(d.isApproved && !d.isEntered) &&
                <View style={pageStyles.ele}>
                  <View style={{color: COLOR.ERROR}}>NOT ENTERED</View>
                </View>
                }
                {(!d.isRejected && d.isExit) &&
                <View style={pageStyles.ele}>
                  <View>{'Exit By ' + d.exitBy + ' in gate ' +  d.exitGateNo}</View>
                  <View>{'On ' + toDateTimeString(d.exitAt)}</View>
                </View>
                }
                {(d.isEntered && !d.isExit) &&
                <View style={pageStyles.ele}>
                  <View style={{color: COLOR.ERROR}}>STAYING IN</View>
                </View>
                }
                {(!this.props.isSearch && this.props.appUser.role === ROLE.GATEKEEPER) &&
                <View style={pageStyles.ele} className='center'>
                  {(!d.isEntered && !d.isRejected) && <Button title='Enter' onPress={() => { this.props.onEnter(d._id) }} />}
                  {((d.isEntered || d.isRejected) && !d.isExit) && <Button title={d.isRejected ? 'Remove' : 'Exit'} onPress={() => { this.props.onExit(d._id) }} />}
                </View>
                }
                {(!this.props.isSearch && this.props.appUser.role === ROLE.USER && !d.isApproved && !d.isRejected && !d.isEntered) &&
                <View style={pageStyles.ele} className='center'>
                  <Button title='Approve' style={{marginRight: '10px'}} onPress={() => { this.props.onApprove(d._id) }} />
                  <Button title='Reject' onPress={() => { this.props.onReject(d._id) }} />
                </View>
                }
                {this.props.isSearch &&
                <View style={pageStyles.ele} className='center'>
                  <Button title='Select' onPress={() => { this.props.onSelect(d) }} />
                </View>
                }
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No Visitor found!!!</Text>
      }
      </View>
      <Modal
          open={this.state.isZoomImage}
          onClose={() => this.onToggleZoomImage(false)}>
          <View className='center'>
            <Image src={FILE_BASE_PATH + this.state.zoomImage} style={{ height: '50%', width: '50%' }} />
          </View>
        </Modal>
    </>
    );
  }
};

export const styles = {
  cardContainer: {
    flex: 1, 
    width: '100%',
    padding: 10,
    marginBottom: 10
  },
  card: {
    flex: 1, 
    margin: 15
  },
  img:{
    // width: 25,
    height: '25vh',
    margin: 0,
    borderRadius: '50%',
    marginBottom: '-25px'
  },
  content: {
    flex: 1,
    padding: 10,
    paddingTop: '25px',
    borderColor: COLOR.HR_COLOR,
    borderWidth: 1,
    borderStyle: 'solid',
    borderRadius: 10,
  }
};