import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text, TextInput, TextArea } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IDocuments } from '../../models/store/documents';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home';
import { IDocument } from '../../models/document';
import { styles } from "../../styles/page";
import { styles as iconStyles } from "../../styles/icon";
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import Images from '../images';
import { FILE_BASE_PATH } from '../../constants';
import { isNN, generateUUID } from '../../util/index';
import { toDateString } from '../../util/convert';
import Toast from  '../../util/toast';

interface IProps {
  homeIndex: number,
  user: IUser,
  documents: IDocuments,
  onLoad: (homeId: string, callback: (result: boolean) => void) => void,
  onSave: (homeId: string, data: IDocument, homeIndex: number, user: IUser, documents: Array<IDocument>, callback: (result: boolean) => void) => void,
  onRemove: (id: string, documents: Array<IDocument>, callback: (result: boolean) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void
};

interface IState {
  isLoading: boolean,
  isEdit: boolean,
  id: string,
  homeId: string,
  title: string,
  description: string,
  fileName: string
};

export default class MyDocuments extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      id: '',
      homeId: isNN(this.props.user.homes) ? this.props.user.homes[0]._id : '',
      title: '',
      description: '',
      fileName: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
    this.onClear = this.onClear.bind(this);
    this.onFileSelect = this.onFileSelect.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.homes[this.props.homeIndex]._id, (result: boolean) => {
      this.setState({
        isLoading: false
      });
      if(!result){
        Toast.fail('Failed to load Documents.');
      }
    });
  };

  onFileSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        this.setState({
          isLoading: true
        });
        // upload file to api.
        let data = {
          file: file,
          type: file.type,
          name: fileName
        };
        this.props.onUpload(data, (result: boolean, message: string) => {
          this.setState({
            isLoading: false
          });
          if(!result){
            Toast.fail(message ? message : 'Failed to upload file.');
          }else{
            this.setState({
              fileName: fileName
            });
          }
        });
      }
    }
  };

  onSave = () => {
    if(isNN(this.state.title) && isNN(this.state.fileName)){
        this.setState({
          isLoading: true
        });
        /*
        let data = this.state.data;
        data.id = this.state.id,
        data.userId = this.props.user._id,
        data.homeId = this.state.homeId;
        data.title = this.state.title;
        data.description = this.state.description;
        data.createdBy = this.props.user._id;
        data.createdOn = new Date();
        */
        
        let document = {
          _id: this.state.id,
          title: this.state.title,
          description: this.state.description,
          file: this.state.fileName,
          createdBy: this.props.user.name,
          createdAt: new Date()
        } as IDocument;
        this.props.onSave(this.state.homeId, document, this.props.homeIndex, this.props.user, this.props.documents.data, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              homeId: isNN(this.props.user.homes) ? this.props.user.homes[0]._id : '',
              title: '',
              description: '',
              fileName: ''
            });
            Toast.success('Document saved successfully.');
          }else{
            Toast.fail('Failed to save document.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onClear = () => {
    this.setState({
      id: '',
      homeId: isNN(this.props.user.homes) ? this.props.user.homes[0]._id : '',
      title: '',
      description: '',
      fileName: ''
    });
  };
  
  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.documents.data, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Document removed successfully.');
              }else{
                Toast.fail('Failed to remove document.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'My Documents', link: 'my-documents'}]} />
        <View style={modelStyles.row}>
          <TextInput type='text' placeholder="Title" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.title} onChange={(event: any) => this.setState({title: event.target.value})} />
        </View>
        <View style={modelStyles.row}>
          <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
        </View>
        {isNN(this.props.user.homes) &&
        <View style={modelStyles.row}>
        <Text style={{marginRight: 10}}>Home:</Text>
        {this.props.user.homes.map((h: IHome, i: number) => {
          return(
            <RadioButton isActive={h._id === this.state.homeId} text={h.number} key={'home' + i} onPress={() => this.setState({homeId: h._id})} />
          )
        })
        }
        </View>
        }
        <View style={modelStyles.row}>
          {/* <Button
              title='Select Document'
              onPress={this.onImageSelect}
              /> */}
              <input type='file' name='file' placeholder='Select Document' onChange={this.onFileSelect}/>
              {isNN(this.state.fileName) &&
              <Text style={{marginLeft: 15}}>{this.state.fileName}</Text>
              }
        </View>
        <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
            <Button
            style={{marginRight: 10}}
            title='SAVE'
            onPress={this.onSave}
            />
            <Button
            title='CLEAR'
            onPress={this.onClear}
            />
        </View>
      {(this.props.documents.isUpToDate && !this.props.documents.isError && isNN(this.props.documents.data)) ?
        this.props.documents.data.map((d: IDocument, i: number) => {
          return(
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
              </View>
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.title}</Text>
              </View>
              {isNN(d.description) &&
              <View style={styles.ele}>
                <Text>{d.description}</Text>
              </View>
              }
              {isNN(this.props.user.homes.filter(ho => ho._id === d._id)) &&
              <View style={styles.ele}>
                <Text>{'Home: ' + this.props.user.homes.filter(ho => ho._id === d._id)[0].number}</Text>
              </View>
              }
              {isNN(d.file) &&
              <View style={styles.ele}>
                <a target='_blank' rel='noopener noreferrer' href={FILE_BASE_PATH + d.file}>{d.file}</a>
              </View>
              }
              <View style={styles.ele}>
                {/* <TouchableHighlight underlayColor={COLOR.ICON_BTN_UNDERLAY} style={{marginRight: 25}} onPress={() => {
                  this.setState({
                    isEdit: true,
                    id: d.id,
                    homeId: d.homeId,
                    title: d.title,
                    description: d.description,
                  });
                }}>
                  <Image source={Images.edit} style={iconStyles.editIcon} />
                </TouchableHighlight> */}
                <TouchableHighlight style={{}} onClick={() => {
                  this.onRemove(d._id);
                }}>
                  <Image src={Images.delete} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>{this.props.documents.isUpToDate ? 'No Documents found!!!' : (this.props.documents.isError ?  this.props.documents.errorMessage : 'No Documents found!!!')}</Text>
      }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};