import * as React from 'react';
import { View, TouchableHighlight, Image } from '../../../common/elements';
import Spinner from '../../../common/spinner';
import Breadcrum from '../../../common/breadcrum';
import { IUser } from '../../../../models/user/user';
import { IAppUser } from '../../../../models/app-user';
import { IUsers } from '../../../../models/store/users';
import { IHomes } from '../../../../models/store/homes';
import { IBlocks } from '../../../../models/store/blocks';
import { IHome, IMember, IVehicle, IServant, ITicket, IVisitor, IPayment } from '../../../../models/home';
import { IDocument } from '../../../../models/document';
import Toast from  '../../../../util/toast';
import Images from '../../../images';
import { COLOR } from '../../../../constants';
import { isNN } from '../../../../util/index';
import Info from './info';
import Members from './members';
import Vehicles from './vehicles';
import Servants from './servants';
import Tickets from './tickets';
import Visitors from './visitors';
import Payments from './payments';
import Documents from './documents';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  blocks: IBlocks,
  onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => void,
  onLoadMembers: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void,
  onLoadVehicles: (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => void,
  onLoadServants: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onLoadTickets: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => void,
  onLoadVisitors: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onLoadPayments: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onLoadDocuments: (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  homeId: string,
  activeTab: number,
  data: IHome
};

export default class ViewHome extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      homeId: '',
      activeTab: 1,
      data: {} as IHome
    }
    this.toggleLoading = this.toggleLoading.bind(this);
  };

  componentDidMount(){
    let homeId = this.props.match.params.id; // this.props.navigation.state.params.id;
    this.setState({
      homeId: homeId
    });
    if(homeId.length > 0){
      this.setState({
        isLoading: true
      });
      this.props.onLoad(homeId, (result: IHome) => {
        this.setState({
          isLoading: false
        });
        if(result && isNN(result._id)){
          this.setState({
            data: result
          });
        }else{
          Toast.fail('Failed to load Home.');
        }
      }, (message: string) => {
        this.setState({
          isLoading: false
        });
        Toast.fail(isNN(message) ? message : 'Failed to load Home.');
      });
    }
  };

  toggleLoading = (isLoading: boolean) => {
    this.setState({
      isLoading: isLoading
    });
  }

  componentWillUnmount() {}

  render() {
    let { activeTab } = this.state;
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Homes', link: 'homes'}, {title: 'View Home', link: 'view-home'}]} />
        <View style={{flex: 1, flexDirection: 'column', width: '100%', marginBottom: 2}}>
          <View className='tab-bar' style={{flex: 1, flexDirection: 'row'}}>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 1 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 1})}>          
              <Image src={activeTab === 1 ? Images.infoInv : Images.info} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 2 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 2})}>          
              <Image src={activeTab === 2 ? Images.membersInv : Images.members} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 3 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 3})}>
              <Image src={activeTab === 3 ? Images.vehicleInv : Images.vehicle} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 4 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 4})}>
              <Image src={activeTab === 4 ? Images.servantInv : Images.servant} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 5 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 5})}>
              <Image src={activeTab === 5 ? Images.supportInv : Images.support} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 6 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 6})}>
              <Image src={activeTab === 6 ? Images.visitorsInv : Images.visitors} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 7 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 7})}>
              <Image src={activeTab === 7 ? Images.moneyInv : Images.money} style={styles.tabImage} />
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 8 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 8})}>
              <Image src={activeTab === 8 ? Images.documentInv : Images.document} style={styles.tabImage} />
            </TouchableHighlight>
          </View>
          <View style={{flex: 1, flexDirection: 'column', padding: 15}}>
            {
              activeTab === 1 ?
                  <Info blocks={this.props.blocks} data={this.state.data} />
              :
                activeTab === 2 ?
                  <Members homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadMembers} />
                :
                  activeTab === 3 ?
                    <Vehicles homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadVehicles} />
                  :
                    activeTab === 4 ?
                      <Servants homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadServants} />
                    :
                      activeTab === 5 ?
                        <Tickets homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadTickets} />
                      :
                        activeTab === 6 ?
                          <Visitors appUser={this.props.appUser} user={this.props.user} homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadVisitors} />
                        :
                          activeTab === 7 ?
                            <Payments homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadPayments} />
                          :
                            <Documents homeId={this.state.homeId} onToggleLoading={this.toggleLoading} onLoad={this.props.onLoadDocuments} />
            }            
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};

const styles = {
  content: {
    // backgroundColor: 'transparent',
    width: '100%'
  },
  tab: {
    flex: 1,
    paddingTop: 10,
    paddingBottom: 10,
    alignItems: 'center'
  },
  activeTab: {
    borderBottomWidth: 1,
    borderBottomColor: COLOR.PRIMARY,
    borderBottomStyle: 'solid',
    color: COLOR.PRIMARY
  },
  tabImage: {
    width: 20,
    height: 20
  }
};