import * as React from 'react';
import { TouchableHighlight, View } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IAds } from '../../models/store/ads';
import { IPolls } from '../../models/store/polls';
import { IOutings } from '../../models/store/outings';
import { IPoll, IVote } from '../../models/poll/index';
import { IComplaint, IRemainder } from '../../models/home/index';
import AMPoll from './manager/poll';
import UserPoll from './poll';
import Outing from './outing';
import CustomList from './custom-list';
import { COLOR, ROLE } from '../../constants';
import { isNNObject } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  ads: IAds,
  polls: IPolls,
  outings: IOutings,
  remainders: Array<IRemainder>,
  complaints: Array<IComplaint>,
  onLoadAds: (appClientId: string, callback: (result: boolean) => void) => void,
  onLoadPoll: (appClientId: string, userRole: string, callback: (result: boolean) => void) => void,
  onSavePoll: (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => void,
  onRemovePoll: (id: string, polls: Array<IPoll>, userId: string, sCallback: (result: boolean) => void) => void,
  onVote: (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => void,
  onLoadOuting: (appClientId: string, homeId: string, callback: (result: boolean) => void) => void,
  onReturnBack: (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => void
};

interface IState{
  activeTab: number,
  isShowPoll: boolean
};

export default class Notification extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      activeTab: (isNNObject(this.props.user) && (this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER)) ? 1 : 2,
      isShowPoll: isNNObject(this.props.user) && (this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER || (this.props.appUser.role === ROLE.USER && this.props.appUser.homeIndex >= 0))
    }
  };
  
  componentDidMount(){
    this.props.onLoadAds(this.props.user.client._id, (result: boolean) => {
      if(!result){
        Toast.fail('Failed to load Ads.');
      }
    });
    if(this.state.isShowPoll){
      this.props.onLoadPoll(this.props.user.client._id, this.props.appUser.role, (result: boolean) => {
        if(!result){
          Toast.fail('Failed to load Poll.');
        }
      });
    }
    this.props.onLoadOuting(this.props.user.client._id, this.props.appUser.role === ROLE.USER ? this.props.user.homes[this.props.appUser.homeIndex]._id : '', (result: boolean) => {
      if(!result){
        Toast.fail('Failed to load Outing.');
      }
    });
  };
  
  componentWillUnmount(){};

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Notice', link: 'notice'}]} />
        <View style={{flex: 1, flexDirection: 'column', width: '100%', marginBottom: 2}}>
          <View className='tab-bar' style={{flex: 1, flexDirection: 'row'}}>
          {this.state.isShowPoll &&
            <TouchableHighlight className='tab' style={(this.state.activeTab === 1 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 1})}>
              POLL
            </TouchableHighlight>
          }
            <TouchableHighlight className='tab' style={(this.state.activeTab === 2 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 2})}>
              ADS
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 3 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 3})}>
              OUTING
            </TouchableHighlight>
            {/* <TouchableHighlight className='tab' style={(this.state.activeTab === 4 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 4})}>
              REMINDERS
            </TouchableHighlight>
            <TouchableHighlight className='tab' style={(this.state.activeTab === 5 ? {...styles.tab, ...styles.activeTab} : styles.tab)} onClick={() => this.setState({activeTab: 5})}>
            COMPLAINTS
            </TouchableHighlight> */}
          </View>
          <View style={{flex: 1, flexDirection: 'column', padding: 15}}>
            {
              this.state.activeTab === 1 ? 
                (this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) ? 
                  <AMPoll appUser={this.props.appUser} user={this.props.user} polls={this.props.polls} onSave={this.props.onSavePoll} onRemove={this.props.onRemovePoll} />
                :
                  <UserPoll appUser={this.props.appUser} user={this.props.user} polls={this.props.polls} onVote={this.props.onVote} />
              :
                this.state.activeTab === 2 ?
                  <CustomList data={(this.props.ads.isUpToDate ? this.props.ads.data : [])} message={(this.props.ads.isUpToDate ? (this.props.ads.isError ? this.props.ads.errorMessage : "No data found!!!") : "Loading...")} />
                :
                this.state.activeTab === 3 ?
                  <Outing history={this.props.history} appUser={this.props.appUser} user={this.props.user} outings={this.props.outings} onReturnBack={this.props.onReturnBack} />
                :
                  <CustomList data={this.state.activeTab === 4 ? this.props.remainders : this.props.complaints} message="No data found!!!" />
            }            
          </View>
        </View>        
      </main>
    );
  }
};

const styles = {
  content: {
    // backgroundColor: 'transparent',
    width: '100%'
  },
  tab: {
    flex: 1,
    paddingTop: 5,
    paddingBottom: 5,
    alignItems: 'center'
  },
  activeTab: {
    borderBottomWidth: 1,
    borderBottomColor: COLOR.PRIMARY,
    borderBottomStyle: 'solid',
    color: COLOR.PRIMARY
  }
};