import Api from '../api/api';
import { actionTypes } from '../action-types';
import { IUser } from '../../models/user';
import { IPayment } from '../../models/home';
import { ITransaction } from '../../models/home/transaction';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'payment';

export const loadAll = (appClientId: string, homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's' + (isNN(homeId) ? '/' + homeId : ''), (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(isNN(message) ? message : 'Failed to load Payments.');
    }
  });
};

export const search = (appClientId: string, data: any, user: IUser, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().post(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result)) {
      sCallback(result.data);
    } else {
      fCallback(isNN(message) ? message : 'Failed to load Payments.');
    }
  }, data);
};

export const save = (appClientId: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().post(controller + '/' + appClientId, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true){
      callback(true, '');
    } else {
      callback(false, message ? message : 'Failed to save Payment.');
    }
  }, data);
};

export const updatePay = (id: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().put(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true){
      callback(true, '');
    } else {
      callback(false, message ? message : 'Failed to update Payment.');
    }
  }, data);
};

export const upload = (file: any, homeId: string, user: IUser, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().post(controller + 'upload', (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data.approvalid > 0)
    {
      /*
      var homeIndex = user.homes.findIndex(d => d._id === homeId);
      if(file.approvalid > 0)
      {        
        var paymentIndex = user.home[homeIndex].paymentApproval.findIndex(d => d.approvalid === file.approvalid);
        user.home[homeIndex].paymentApproval[paymentIndex] = result.data;
      }else{
        user.home[homeIndex].paymentApproval.push(result.data);
      }
      */
      dispatch(getUserResult(user));      
      callback(true);
    }else{
      callback(false);
    }
  }, file);
};

export const update = (paymentId: string, transaction: ITransaction, homeIndex: number, user: IUser, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().post(controller + '/transaction/' + paymentId, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data.length > 0)
    {
      transaction._id = result.data;
      // var homeIndex = user.homes.findIndex(d => d._id === params.homeid);
      // if(paymentId.length > 0)
      // {        
        var paymentIndex = user.homes[homeIndex].payments.findIndex(d => d._id === paymentId);
        if(!user.homes[homeIndex].payments[paymentIndex].transactions){
          user.homes[homeIndex].payments[paymentIndex].transactions = [];
        }
        user.homes[homeIndex].payments[paymentIndex].transactions.push(transaction);
      // }else{
      //   user.homes[homeIndex].payments.push(result.data);
      // }
      dispatch(getUserResult(user));      
      callback(true);
    }else{
      callback(false);
    }
  }, transaction);
};

const getUserResult = (data: IUser) => ({
  type: actionTypes.LOAD_USER,
  payload: data
});