import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IMember } from '../models/home';
import { login, forgetPassword, signup } from '../store/actions/account';

import Page from '../components/login/index';

const mapStateToProps = (state: IAppState, ownProps: any) => ({});
const mapDispatchToProps = (dispatch: any) => ({
    onLogin: (userName: string, password: string, callback: (result: boolean, message: string) => void) => dispatch(login(userName, password, callback)),
    onForgetPassword: (mobileNumber: string, email: string, callback: (result: boolean, message: string) => void) => dispatch(forgetPassword(mobileNumber, email, callback)),
    onSignUp: (data: IMember, clientCode: string, callback: (result: boolean, message: string) => void) => dispatch(signup(data, clientCode, callback))
});

export const Login = connect(mapStateToProps, mapDispatchToProps)(Page);