import * as React from 'react';
import { View, Picker } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  appUser: IAppUser,
  onUpdate: (role: string, callback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  role: string
};

export default class MyRole extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      role: ''
    }
    this.onUpdate = this.onUpdate.bind(this);
  };

  componentDidMount() {
    this.setState({
      role: this.props.appUser.role,
    });
  }; 

  componentWillUnmount() {};
  
  onUpdate = () => {
    if(isNN(this.state.role)){
      this.setState({
        isLoading: true
      });
      this.props.onUpdate(this.state.role, (result: boolean, message: string) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          Toast.success('Role updated successfully.');
          this.props.history.push('my-role')
        }else{
          Toast.fail(message);
        }
      });
    }else{
      Toast.warn('Please select role before Update.');
    }
  };

  render() {
      return(
        <main style={{...styles.content, flex: 1}}>
          <Breadcrum data={[{title: 'My Role', link: 'my-role'}]} />
          <View style={modelStyles.container}>
            <View style={modelStyles.row}>
              <Picker
                className='form-control'
                value={this.state.role}
                style={modelStyles.ele}
                onChange={(event: any) => this.setState({role: event.target.value})}>
                  {
                  this.props.user.role.split(',').map((d: string, i: number) => {
                    return(<option key={'role' + i} value={d}>{d}</option>);
                  })
                  }
              </Picker>
            </View>
            <View className='center' style={modelStyles.saveBtn}>
              <Button
              title="UPDATE"
              onPress={this.onUpdate}
              />
            </View>
          </View>
          <Spinner visible={this.state.isLoading} />
        </main>
      );
  }
};