import * as React from 'react';
import { View, ScrollView, TextInput, Text, Picker, TextArea } from '../../common/elements';
import Button from '../../common/button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IHome } from '../../../models/home';
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { isNN } from '../../../util/index';
import Toast from  '../../../util/toast';
import { COLOR, MONTH } from '../../../constants';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onSave: (appClientId: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

interface IState {
  isLoading: boolean,
  amount: number,
  description: string,
  month: number,
  year: number,
  searchText: string,
  showHomes: boolean,
  isAllHome: boolean,
  homes: Array<IHome>
};

export default class PaymentForm extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      amount: 0,
      description: '',
      month: new Date().getMonth(),
      year: new Date().getFullYear(),
      searchText: '',
      showHomes: false,
      isAllHome: false,
      homes: []
    }
    this.onSave = this.onSave.bind(this);
    this.onClear = this.onClear.bind(this);
  };

  onSave = () => {
    if(this.state.amount > 0 && (this.state.isAllHome || this.state.homes.length > 0)){
      let homeIds = [] as any;
      this.state.homes.map((h,i) => homeIds.push(h._id));
      let data = {
        amount: this.state.amount,
        description: this.state.description,
        month: this.state.month,
        year: this.state.year,
        isAllHome: this.state.isAllHome,
        homes: homeIds
      };
      this.setState({
        isLoading: true
      });
      this.props.onSave(this.props.user.client._id, data, this.props.user, (result: boolean, message: string) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          // this.props.navigation.navigation.goBack(null);
          this.setState({
            amount: 0,
            description: '',
            month: new Date().getMonth(),
            year: new Date().getFullYear(),
            searchText: '',
            showHomes: false,
            isAllHome: false,
            homes: []
          });
          Toast.success('Payment saved successfully.');
        }else{
          Toast.fail( message ? message : 'Failed to save payment.');
        }
      });
    }else{
      Toast.warn('Please fill all required fields before Save.');
    }
  };

  onClear = () => {
    this.setState({ 
      amount: 0,
      description: '',
      month: new Date().getMonth(),
      year: new Date().getFullYear(),
      searchText: '',
      showHomes: false,
      isAllHome: false,
      homes: []
    });
  };

  render() {
    return(
        <View style={styles.container}>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Amount" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.amount > 0 ? this.state.amount + '' : ''} onChange={(event: any) => this.setState({amount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View style={modelStyles.row}>
            <Text style={{marginTop: 15}}>{'Payment for ' + this.state.year + ' '}</Text>
            <Picker
              className='form-control'
              value={this.state.month}
              style={ modelStyles.ele}
              onChange={(event: any) => this.setState({month: event.target.value})}>
              {
                MONTH.filter((m, i) => m.value <= new Date().getMonth()).map((m: any, i: number) => {
                  return(<option key={'month' + i} value={m.value}>{m.title}</option>);
                })
              }
            </Picker>
          </View>
          <View style={modelStyles.row}>
            <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <Text style={{marginRight: 15}}>{'Apply to Home(s): ' + (this.state.isAllHome ? '' : this.state.homes.length)}</Text>
            <Checkbox isActive={this.state.isAllHome} text='All Homes' onPress={() => this.setState({isAllHome: !this.state.isAllHome})} />
          </View>
          {!this.state.isAllHome &&
          <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
                {
                  this.state.homes.map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='pointer'
                      key={'ho' + i}
                      style={{flexDirection: 'column', marginRight: 6, color: COLOR.SECONDARY }}
                      onClick={() => {
                        this.setState({
                          homes: this.state.homes.filter((h, i) => h._id !== d._id)
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
          }
          {!this.state.isAllHome &&
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Enter Home Number" className='form-control'
            style={{...modelStyles.ele,
              flexDirection: 'row',
              padding: 6,
              borderWidth: 1,
              borderColor: COLOR.HR_COLOR,
              borderRadius: 5
            }}
            value={this.state.searchText} 
            onChange={(event: any) => this.setState({showHomes: true, searchText: event.target.value})} />
          </View>
          }
            {(!this.state.isAllHome && this.state.showHomes && isNN(this.state.searchText) && this.props.homes && this.props.homes.data.length > 0 ) &&
              <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
                {
                  this.props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(this.state.searchText.toLowerCase()) >= 0 && (this.state.homes.filter(h => h._id === d._id).length < 1)).map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='pointer'
                      key={'ho' + i}
                      style={{flexDirection: 'column', marginRight: 6}}
                      onClick={() => {
                        let homes = this.state.homes;
                        homes.push(d);
                        this.setState({
                          homes: homes
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
              </ScrollView>
            }
          <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
              <Button
              style={{marginRight: 10}}
              title="SAVE"
              onPress={this.onSave}
              />
              <Button
              title="CLEAR"
              onPress={this.onClear}
              />
          </View>
          <Spinner visible={this.state.isLoading} />
        </View>
    );
  }
};

const styles = {
  container:{
    width: '100%',
    paddingLeft: 15,
    paddingRight: 15,
  }
};