import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user/user';
import { updateProfile } from '../store/actions/account';

import Page from '../components/home/my-profile';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User
});
const mapDispatchToProps = (dispatch: any) => ({
    onUpdate: (userId: string, name: string, mobileNo: string, emailId: string, user: IUser, callback: (result: boolean, message: string) => void) => dispatch(updateProfile(userId, name, mobileNo, emailId, user, callback))
});

export const MyProfile = connect(mapStateToProps, mapDispatchToProps)(Page);