import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IBlock } from '../models/block';
import { remove } from '../store/actions/block';
import Page from '../components/home/manager/blocks';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    blocks: state.Blocks,
    homes: state.Homes
});
const mapDispatchToProps = (dispatch: any) => ({
    onRemove: (id: string, blocks: Array<IBlock>, callback: (result: boolean) => void) => dispatch(remove(id, blocks, callback))
});

export const Blocks = connect(mapStateToProps, mapDispatchToProps)(Page);