import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text, TextInput } from '../common/elements';
import Spinner from '../common/spinner';
import Breadcrum from '../common/breadcrum';
import RadioButton from '../common/radio-button';
import { IUsers } from '../../models/store/users';
import { IUser } from '../../models/user/user';
import { IMember } from '../../models/home/member';
import { styles } from '../../styles/page';
import { styles as iconStyles } from '../../styles/icon';
import { styles as modelStyles } from '../../styles/model';
import {styles as CommonStyles} from '../../styles/common';
import Images from '../images';
import { COLOR } from '../../constants';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  users: IUsers,
  homeIndex: number,
  onSave: (data: IMember, user: IUser, homeIndex: number, sCallback: (result: boolean, message: string) => void) => void,
  onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, sCallback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  id: string,
  number: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  emailId: string,
  type: string,
  role: string
};

export default class Members extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      id: '',
      number: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      emailId: '',
      type: 'OWNER',
      role: 'USER'
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender) && isNN(this.state.type)){
        this.setState({
          isLoading: true
        });
        let member = {
          _id: this.state.id,
          number: '',
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: this.state.emailId ? this.state.emailId.toLowerCase() : '',
          type: this.state.type,
          role: this.state.role
        } as IMember;
        this.props.onSave(member, this.props.user, this.props.homeIndex, (result: boolean, message: string) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              number: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              type: 'OWNER',
              role: 'USER'
            });
            Toast.success('Member saved successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save member.');
          }
        });
    }else{
      // Toast.warn('Please fill all the required fields before '+ (this.state.id > 0 ? 'Update' : 'Save') + '.');
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.homeIndex, this.props.user, this.props.users, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Member removed successfully.');
              }else{
                Toast.fail('Failed to remove member.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Members', link: 'members'}]} />
        <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
          <Text className='fr pointer' onClick={() => this.props.history.push('new-member')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
        </View>
      {isNN(this.props.user.homes[this.props.homeIndex].members) ?
        this.props.user.homes[this.props.homeIndex].members.map((d: IMember, i: number) => {
          return(
            (this.state.isEdit === true && this.state.id === d._id) ?
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                <TextInput type='text' placeholder="Name" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
              </View>
              <View style={styles.ele}>
                <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
              </View>
              <View style={styles.ele}>
                <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
                <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
                <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
              </View>
              <View style={styles.ele}>
                <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
              </View>
              <View style={styles.ele}>
                <TextInput type='text' placeholder="Email ID" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})} />
              </View>
              <View style={styles.ele}>
                <RadioButton isActive={this.state.type === 'OWNER'} text='Owner' onPress={() => this.setState({type: 'OWNER'})} />
                <RadioButton isActive={this.state.type === 'TENANT'} text='Tenant' onPress={() => this.setState({type: 'TENANT'})} />
              </View>
              <View style={styles.ele}>
                <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                  <Image src={Images.tick} style={iconStyles.editIcon} />
                </TouchableHighlight>                
                <TouchableHighlight style={{}} onClick={() => {
                  this.setState({
                    isEdit: false,
                    id: '',
                    number: '',
                    name: '',
                    age: 0,
                    gender: '',
                    mobileNo: '',
                    emailId: '',
                    type: 'OWNER',
                    role: 'USER'
                  });
                }}>
                  <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
            :
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                <Text>{d.name}</Text>
              </View>
              <View style={styles.ele}>
                <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
              </View>
              {isNN(d.mobileNo) &&
              <View style={styles.ele}>
                <Text>{d.mobileNo}</Text>
              </View>
              }
              {isNN(d.emailId) &&
              <View style={styles.ele}>
                <Text>{d.emailId}</Text>
              </View>
              }
              <View style={styles.ele}>
                <Text>{d.type}</Text>
              </View>
              <View style={styles.ele}>
                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                  this.setState({
                    isEdit: true,
                    id: d._id,
                    number: d.number,
                    name: d.name,
                    age: d.age,
                    gender: d.gender,
                    mobileNo: d.mobileNo,
                    emailId: d.emailId,
                    type: d.type,
                    role: d.role
                  });
                }}>
                  <Image src={Images.edit} style={iconStyles.editIcon} />
                </TouchableHighlight>                
                <TouchableHighlight style={{}} onClick={() => {
                  this.onRemove(d._id);
                }}>
                  <Image src={Images.delete} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No Member found!!!</Text>
      }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};