import { actionTypes } from '../action-types';
import { IDocuments } from '../../models/store/documents';

const createEmptyResult = (): IDocuments => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Documents = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_DOCUMENTS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IDocuments, payload: IDocuments): IDocuments => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
