import * as React from 'react';
import Modal from 'react-responsive-modal';
import { View, ScrollView, TextInput, Text, Picker, TouchableHighlight, Image } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import {styles as popupStyles} from '../../../styles/popup';
import { isNN, generateUUID, onEnter } from '../../../util/index';
import { COLOR, ROLE } from '../../../constants';
import Toast from  '../../../util/toast';
import { IHome, IVisitor } from '../../../models/home';
import Images from '../../images';
import VisitorsView from './visitors-view';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  gateNo: string,
  onToggleLoading: (isLoading: boolean) => void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearch: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState {
  homeId: string,
  id: string,
  type: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
	personCount: number,
	vehicleType: string,
	vehicleNo: string,
  fileName: string,
  showHomes: boolean,
  homeSearchText: string,
  searchResult: Array<IVisitor>,
  isShowSearch: boolean
};

export default class NewVisitor extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      homeId: '',
      id: '',
      type: 'VISITOR',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      personCount: 1,
      vehicleType: 'BIKE',
      vehicleNo: '',
      fileName: '',
      showHomes: false,
      homeSearchText: '',
      searchResult: [],
      isShowSearch: false
    }
    this.onSave = this.onSave.bind(this);
    this.onClear = this.onClear.bind(this);
    this.onImageSelect = this.onImageSelect.bind(this);
    this.onSearch = this.onSearch.bind(this);
  };

  componentDidMount(){
    if(this.props.appUser.role === ROLE.USER){
      this.setState({
        homeId: this.props.user.homes[this.props.appUser.homeIndex]._id
      });
    } 
  };

  onImageSelect = (event: any) => {
    let file = event.target.files[0];
    if(file && file.name){
      let arrFileName = file.name.split('.');
      if(arrFileName && arrFileName.length > 1){
        let fileName = generateUUID();
        fileName += '.' + arrFileName[arrFileName.length - 1];
        this.setState({
          fileName: fileName
        });
        let data = {
          file: file,
          type: file.type,
          name: fileName
        };
        this.props.onUpload(data, (result: boolean, message: string) => {
          if(!result){
            Toast.fail(message ? message : 'Failed to upload file.');
          }
        });
      }
    }
  };

  onSave = () => {
    if((this.props.appUser.role === ROLE.USER || isNN(this.props.gateNo)) && this.state.showHomes === false && this.state.homeId.length > 0 && isNN(this.state.name) && this.state.personCount > 0 && isNN(this.state.fileName)){
      let visitor = {
        _id: '',
        type: this.state.type,
        name: this.state.name,
        age: this.state.age,
        gender: this.state.gender,
        mobileNo: this.state.mobileNo,
        emailId: '',
        personCount: this.state.personCount,
        vehicleType: this.state.vehicleType,
        vehicleNo: this.state.vehicleNo,
        image: this.state.fileName,
        passCode: '',
        gateNo: this.props.gateNo
      } as IVisitor;
      visitor = Object.assign(visitor, {
        isUser: (this.props.appUser.role === ROLE.USER)
      });
      this.props.onToggleLoading(true);
      this.props.onSave(this.state.homeId, visitor, this.props.user, this.props.appUser, (result: IVisitor) => {
          // this.props.navigation.navigation.goBack(null);
          this.onClear();
          this.props.onToggleLoading(false);
          if(this.props.appUser.role === ROLE.USER){
            Toast.success('Visitor Request created successfully. Please share the passcode(' + result.passCode + ') to yor visitor and ask him/her to show it to the security in the gate while entering.');
          }else{
            Toast.success('Visitor created successfully. Notification sent to the home members for approval. If this visitor got approved from home member then allow him/her inside.')
          }
      }, (message: string) => {
          this.props.onToggleLoading(false);
          Toast.fail('Failed to save visitor ' + visitor.name + '.');
      });
    }else{
      Toast.warn('Please fill Gate Number, Home, Name, People count and Image before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onClear = () => {
    this.setState({ 
      homeId: (this.props.appUser.role === ROLE.USER) ? this.state.homeId : '',
      id: '',
      type: 'VISITOR',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      personCount: 1,
      vehicleType: 'BIKE',
      vehicleNo: '',
      fileName: '',
      showHomes: false,
      homeSearchText: '',
      searchResult: [],
      isShowSearch: false
    });
  };
  
  onSearch = () => {
    if(this.state.mobileNo.length > 2){
      this.props.onToggleLoading(true);
      this.props.onSearch(this.state.mobileNo, (result: Array<IVisitor>) => {
        this.props.onToggleLoading(false);
        this.setState({
          isShowSearch: true,
          searchResult: result
        });

      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(message);
        this.setState({
          isShowSearch: false,
          searchResult: []
        });
      });
    }
  };

  render() {
    return(
        <View style={styles.container}>
          {this.props.appUser.role === ROLE.GATEKEEPER &&
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder='Enter Home Number' className='form-control'
            style={{...modelStyles.ele, ...styles.ele,
              flexDirection: 'row',
              padding: 12,
              borderWidth: 1,
              borderColor: COLOR.HR_COLOR,
              borderRadius: 5
            }} 
            value={this.state.homeSearchText} 
            onChange={(event: any) => this.setState({showHomes: true, homeSearchText: event.target.value})} />
          </View>
          }
          {
            (this.props.appUser.role === ROLE.GATEKEEPER && this.state.showHomes && isNN(this.state.homeSearchText) && this.props.homes && this.props.homes.data.length > 0 ) &&
            <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
              {
                this.props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(this.state.homeSearchText.toLowerCase()) >= 0).map((d: IHome, i: number) => {
                  return(
                    <Text 
                    className='pointer'
                    key={'ho' + i}
                    style={{...styles.ele, flexDirection: 'column', margin: 3, padding: 6, backgroundColor: COLOR.CARD_UNDERLAY}}
                    onClick={() => {
                      this.setState({homeSearchText: d.number, homeId: d._id, showHomes: false});
                    }}>{d.number}</Text>
                  )
                })
              }
            </ScrollView>
          }
          {this.props.appUser.role === ROLE.GATEKEEPER &&
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.type === 'VISITOR'} text='Visitor' onPress={() => this.setState({type: 'VISITOR'})} />
            <RadioButton isActive={this.state.type === 'DELIVERY'} text='Delivery' onPress={() => this.setState({type: 'DELIVERY'})} />
          </View>
          }
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder='Mobile Number' className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele, display: 'inline', width: 'calc(100% - 40px)', marginRight: 10}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} 
            onKeyPress={(e) => {
              onEnter(e, this.onSearch);
            }} />
            <TouchableHighlight style={{height: 30, marginTop: 13}} onClick={this.onSearch}>
                <Image className='pointer' src={Images.search} style={{width: 30, height: 30}} />
            </TouchableHighlight>
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Name" className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='number' placeholder='Age' className='form-control' maxLength={3} style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele, width: 'auto', display:'inline-block', marginRight: 10}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
            <View style={{display:'inline-block', width: 'auto'}}>
              <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
              <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
              <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
            </View>
          </View>
          <View style={modelStyles.row}>
            <TextInput type='number' placeholder='People Count' className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele, width: 'auto', display:'inline-block', marginRight: 10}} value={this.state.personCount > 0 ? this.state.personCount + '' : ''} onChange={(event: any) => this.setState({personCount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
            <Picker className='form-control' value={this.state.vehicleType} style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele, display:'inline-block', width: 'auto', marginRight: 10}}
            onChange={(event: any) => this.setState({vehicleType: event.target.value})}>
              <option value='WALK'>WALK</option>
              <option value='CYCLE'>CYCLE</option>
              <option value='BIKE'>BIKE</option>
              <option value='CAR'>CAR</option>
              <option value='VAN'>VAN</option>
              <option value='JEEP'>JEEP</option>
              <option value='TEMPO'>TEMPO</option>
              <option value='BUS'>BUS</option>
              <option value='TRUCK'>TRUCK</option>
            </Picker>
            <TextInput type='text' placeholder='Vehicle No' className='form-control' style={{...styles.ele, ...CommonStyles.textInput, ...modelStyles.ele, display:'inline-block', width: 'auto'}} value={this.state.vehicleNo} onChange={(event: any) => this.setState({vehicleNo: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
              <input type='file' name='image' placeholder='SELECT IMAGE' onChange={this.onImageSelect}/>
              {isNN(this.state.fileName) &&
              <Text style={{...styles.ele, marginLeft: 15}}>{this.state.fileName}</Text>
              }
          </View>
          <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
              <Button
              title="SAVE"
              onPress={this.onSave}
              style={{...styles.ele, marginRight: 10}}
              />
              <Button
              title="CLEAR"
              onPress={this.onClear}
              style={styles.ele}
              />
          </View>
          {this.state.isShowSearch &&
          <Modal
          open={this.state.isShowSearch}
          onClose={() => this.setState({ isShowSearch: false })}>
            <View style={popupStyles.container}>
              <VisitorsView user={this.props.user} appUser={this.props.appUser} data={this.state.searchResult} isActive={false} isSearch={true} onSelect={(data: IVisitor) => {
                this.setState({
                  isShowSearch: false,
                  searchResult: [],
                  name: data.name,
                  age: data.age,
                  gender: data.gender,
                  mobileNo: data.mobileNo,
                  vehicleType: data.vehicleType,
                  vehicleNo: data.vehicleNo,
                  fileName: data.image
                });
                }} />
            </View>
          </Modal>
          }
        </View>
    );
  }
};

const styles = {
  container:{
    width: '100%',
    paddingLeft: 15,
    paddingRight: 15,
  },
  ele: {
    fontSize: 17
  }
};