import * as React from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { styles } from '../../../../styles/page';
import { IVehicle } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { COLOR } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IVehicle>
};

export default class Vehicles extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: []
    }
  };

  componentDidMount(){
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, (result: Array<IVehicle>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Vehicles.');
      });
    }
  };

  componentWillUnmount() {}

  render() {
    let { data } = this.state;
      return(
        <View>
        <View className='center' style={styles.ele}>
          <Text style={{color: COLOR.PRIMARY, fontWeight: 'bold', fontSize: 17, marginBottom: 10}}>Vehicles</Text>
        </View>
          {isNN(data) ? 
            data.map((d: IVehicle, i: number) => {
            return(
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.type}</Text>                  
                </View>
                <View style={styles.ele}>
                  <Text>{d.number}</Text>
                </View>
              </View>
              );
          })
          :
          <Text>No data found!!!</Text>
        }
        </View>
      );
  }
};