import * as React from 'react';
import { View, Text } from '../../../common/elements';
import { isNN } from '../../../../util/index';
import { styles } from '../../../../styles/page';
import { IMember } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { toDateString } from '../../../../util/convert';
import { COLOR } from '../../../../constants';

interface IProps {
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IMember>
};

export default class Members extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: []
    }
  };

  componentDidMount(){
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, (result: Array<IMember>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Members.');
      });
    }
  };

  componentWillUnmount() {}

  render() {
    let { data } = this.state;
      return(
        <View>
        <View className='center' style={styles.ele}>
          <Text style={{color: COLOR.PRIMARY, fontWeight: 'bold', fontSize: 17, marginBottom: 10}}>Members</Text>
        </View>
          {isNN(data) ?
            data.map((d: IMember, i: number) => {
            return(
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{(isNN(d.createdBy) ? d.createdBy : '') + ' on ' + toDateString(d.createdAt)}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.name}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                </View>
                {isNN(d.mobileNo) &&
                <View style={styles.ele}>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) &&
                <View style={styles.ele}>
                  <Text>{d.emailId}</Text>
                </View>
                }
                <View style={styles.ele}>
                  <Text>{d.type}</Text>
                </View>
              </View>
              );
          })
          :
          <Text>No data found!!!</Text>
        }
        </View>
      );
  }
};