import { actionTypes } from '../action-types';
import { IUser } from '../../models/user/user';

const createEmptyResult = (): IUser => ({
    _id: '',
    number: '',
    name: '',
    age: 0,
    gender: '',
    mobileNo: '',
    emailId: '',
    type: '',
    role: '',
    image: '',
    createdBy: '',
    updatedBy: '',
    homes: [],
    client: {
        _id: '',
        name: '',
        description: '',
        address: '',
        upi: {
            id: '',
            name: ''
        },
        rep: {
            name: '',
            mobileNo: '',
            emailId: '',
            repType: ''
        }
    }
});

export const User = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_USER:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IUser, payload: IUser): IUser => {
    return {
        ...state,
        _id: payload._id,
        number: payload.number,
        name: payload.name,
        age: payload.age,
        gender: payload.gender,
        mobileNo: payload.mobileNo,
        emailId: payload.emailId,
        type: payload.type,
        role: payload.role,
        image: payload.image,
        createdBy: payload.createdBy,
        updatedBy: payload.updatedBy,
        homes: payload.homes,
        client: payload.client
    };
};
