import * as React from 'react';
import { View, TextInput } from '../common/elements';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import { IUser } from '../../models/user/user';
import { isNN, onEnter } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  user: IUser,
  onUpdate: (userId: string, name: string, mobileNo: string, emailId: string, user: IUser, callback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  name: string,
  mobileNo: string,
  emailId: string
};

export default class MyProfile extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      name: '',
      mobileNo: '',
      emailId: ''
    }
    this.onUpdate = this.onUpdate.bind(this);
  };

  componentDidMount() {
    this.setState({
      name: this.props.user.name,
      mobileNo: this.props.user.mobileNo,
      emailId: this.props.user.emailId
    });
  }; 

  componentWillUnmount() {};
  
  onUpdate = () => {
    if(isNN(this.state.name) && isNN(this.state.mobileNo) && isNN(this.state.emailId)){
      this.setState({
        isLoading: true
      });
      this.props.onUpdate(this.props.user._id, this.state.name, this.state.mobileNo, (this.state.emailId ? this.state.emailId.toLowerCase() : ''), this.props.user, (result: boolean, message: string) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          Toast.success('Profile updated successfully.');
        }else{
          Toast.fail(message);
        }
      });
    }else{
      Toast.warn('Please fill all the fields before Update.');
    }
  };

  render() {
      return(
        <main style={{...styles.content, flex: 1}}>
          <Breadcrum data={[{title: 'My Profile', link: 'my-profile'}]} />
          <View style={modelStyles.container}>
            <View style={modelStyles.row}>
              <TextInput type='text' placeholder="Name" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
            </View>
            <View style={modelStyles.row}>
              <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
            </View>
            <View style={modelStyles.row}>
              <TextInput type='text' placeholder="Email Id" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})}
              onKeyPress={(e) => {
                onEnter(e, this.onUpdate);
              }} />
            </View>
            <View className='center' style={modelStyles.saveBtn}>
              <Button
              title="UPDATE"
              onPress={this.onUpdate}
              />
            </View>
          </View>
          <Spinner visible={this.state.isLoading} />
        </main>
      );
  }
};