import * as React from 'react';
import { View, TextInput, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import { IHomes } from '../../../models/store/homes';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home';
import NewVisitor from './new-visitor';
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { isNN } from '../../../util';
import Toast from  '../../../util/toast';
import { COLOR } from '../../../constants';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  gateNo: string,
  onGateNoChanged: (gateNo: string) => void,
  onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearchVisitor: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  gateNo: string,
  isLoading: boolean
};

export default class Home extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      gateNo: '',
      isLoading: false
    }
    this.viewAllVisitors = this.viewAllVisitors.bind(this);
    this.viewAllTakeaways = this.viewAllTakeaways.bind(this);
  };

  componentDidMount() {
    this.setState({
      gateNo: this.props.gateNo
    })
  };

  componentWillUnmount() {};
  
  viewAllVisitors = () => {
    if(isNN(this.state.gateNo)) {
      this.props.history.push('all-visitors')
    } else {
      Toast.warn('Please enter Gate Number to view this page.');
    }
  };
  
  viewAllTakeaways = () => {
    if(isNN(this.state.gateNo)) {
      this.props.history.push('all-takeways')
    } else {
      Toast.warn('Please enter Gate Number to view this page.');
    }
  };
  
  render() {
    return(
      <main style={styles.content}>
        <View style={modelStyles.row}>
          <TextInput type='text' placeholder='Gate Number' className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele, width: 'auto'}} value={this.state.gateNo} onChange={(event: any) => this.setState({gateNo: event.target.value})} onBlur={() => this.props.onGateNoChanged(this.state.gateNo)} />
        </View>
        <View style={modelStyles.row}>
          <Text className='fr pointer' onClick={this.viewAllTakeaways} style={{...styles.ele, color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'right'}}>View all Active Takeaways</Text>
          <Text className='fr pointer' onClick={this.viewAllVisitors} style={{...styles.ele, color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'right', marginRight: 20}}>View all Active Visitors</Text>
        </View>
        <NewVisitor appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} gateNo={this.state.gateNo} onToggleLoading={(isLoading) => this.setState({ isLoading: isLoading })} onSave={this.props.onSaveVisitor} onUpload={this.props.onUpload} onSearch={this.props.onSearchVisitor} />
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};

const styles = {
  content: {
    flex: 1,
    // backgroundColor: 'transparent',
    width: '100%'
  },
  ele: {
    fontSize: 17
  }
};
