import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { updateRole} from '../store/actions/account';

import Page from '../components/home/my-role';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser
});
const mapDispatchToProps = (dispatch: any) => ({
    onUpdate: (role: string, callback: (result: boolean, message: string) => void) => dispatch(updateRole(role, callback))
});

export const MyRole = connect(mapStateToProps, mapDispatchToProps)(Page);