import * as React from 'react';
import { View, TextInput } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
// import { CheckBox  } from 'react-native-elements';
import { IUser } from '../../models/user/user';
import { IMember } from '../../models/home';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import { COLOR } from '../../constants';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  homeIndex: number,
  onSave: (data: IMember, user: IUser, homeIndex: number, sCallback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  id: string,
  number: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  emailId: string,
  type: string,
  role: string
};

export default class NewMember extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      id: '',
      number: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      emailId: '',
      type: 'OWNER',
      role: 'USER'
    }
    this.onSave = this.onSave.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender) && isNN(this.state.type)){
        this.setState({
          isLoading: true
        });
        let member = {
          _id: this.state.id,
          number: '',
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: this.state.emailId ? this.state.emailId.toLowerCase() : '',
          type: this.state.type,
          role: this.state.role
        } as IMember;
        this.props.onSave(member, this.props.user, this.props.homeIndex, (result: boolean, message: string) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              id: '',
              number: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              type: 'OWNER',
              role: 'USER'
            });
            // this.props.navigation.navigation.goBack(null)
            Toast.success('Member saved successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save member.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before Save.');
    }
  };

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Members', link: 'members'}, {title: 'New Member', link: 'new-member'}]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Name" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
            <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
            <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
          </View>            
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <TextInput type='text' placeholder="Email ID" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.type === 'OWNER'} text='Owner' onPress={() => this.setState({type: 'OWNER'})} />
            <RadioButton isActive={this.state.type === 'TENANT'} text='Tenant' onPress={() => this.setState({type: 'TENANT'})} />
          </View>
          <View className='center' style={modelStyles.saveBtn}>
            <Button
            title="SAVE"
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};