export const login = (userName: string, password: string, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  let fcm_token: string | null = localStorage.getItem('fcm_token');
    new Api().post(controller + 'login', (result: any, message: string) => {
      // console.log('OUT: ', result);
      if(isNNObject(result) && isNNObject(result.data) && isNN(result.data.token) && isNN(result.data.userId))
      {
        // console.log('IN: ', result);
        try {
          // console.log(result.token);
          sessionStorage.setItem('auth_token', result.data.token);
          sessionStorage.setItem('client_id', result.data.clientId);
          sessionStorage.setItem('user_id', result.data.userId);
        } catch (error) {
          // console.error('AsyncStorage error: ' + error.message);
        }
        dispatch(setLoginResult(true));
        callback(true, '');
      }else{
        callback(false, message);
      }
    }, {
      userName: userName,
      password: password,
      rememberMe: false,
      fcmToken: fcm_token,
      os: 'web', // Platform.OS, ////TODO
      osVersion: 'web' // Platform.Version ////TODO
    });
};

export const getProfile = (callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 'profile', (result: any, message: string) => {
    // console.log('OUT: ', result);
    if(isNNObject(result) && isNNObject(result.data) && isNN(result.data._id))
    {
      dispatch(getUserResult(result.data as IUser));
      dispatch(setHomeIndexResult(isNN(result.data.homes) ? 0 : -1));
      let roles = result.data.role.split(',');
      if(isNN(roles)){
        dispatch(setRoleResult(roles[0]));
      }
      // dispatch(getAdResult(((isNNObject(result.data.ad) && result.data.ad instanceof Array) ? result.data.ad as Array<IAd> : []), true, false, ''));
      // dispatch(getPollResult(((isNNObject(result.data.poll) && result.data.poll instanceof Array) ? result.data.poll as Array<IPoll> : []), true, false, ''));
    
      if(roles.indexOf(ROLE.ADMIN) >= 0 || roles.indexOf(ROLE.MANAGER) >= 0){
        new Api().get('members', (mresult: any, message: string) => {
          if (isNNObject(mresult) && isNN(mresult.data)) {
            dispatch(getUsersResult(mresult.data, true, false, ''));
          } else {
            // fCallback(isNN(mresult.message) ? mresult.message : 'Failed to load Members.');
          }
        });
        /*
        loadAllMember(result.data.client._id, (members: Array<IMember>) => {
          console.log('members: ', members)
          dispatch(getUsersResult(members, true, false, ''));
        }, (emsg: string) => {});
        */
        new Api().get('blocks/' + result.data.client._id, (bresult: any, message: string) => {
          if (isNNObject(bresult) && isNN(bresult.data)) {
            dispatch(getBlockResult(bresult.data, true, false, ''));
          } else {
            // fCallback(isNN(hresult.message) ? hresult.message : 'Failed to load Homes.');
          }
        });
      }
      if(roles.indexOf(ROLE.ADMIN) >= 0 || roles.indexOf(ROLE.MANAGER) >= 0 || roles.indexOf(ROLE.GATEKEEPER) >= 0){
        new Api().get('homes/' + result.data.client._id, (hresult: any, message: string) => {
          if (isNNObject(hresult) && isNN(hresult.data)) {
            hresult.data.forEach((h: any) => {
              h.memberIds = h.members;
              delete h.members;
            });
            dispatch(getHomesResult(hresult.data, true, false, ''));
          } else {
            // fCallback(isNN(hresult.message) ? hresult.message : 'Failed to load Homes.');
          }
        });
        
        /*
        loadAllHome(result.data.client._id, (homes: Array<IHome>) => {
          console.log('homes: ', homes)
          dispatch(getHomesResult(homes, true, false, ''));
        }, (emsg: string) => {});
        */
      }
      
      // dispatch(getUsersResult(((isNNObject(result.data.users) && result.data.users instanceof Array) ? result.data.users as Array<IUser> : []), true, false, ''));
      // dispatch(getHomesResult(((isNNObject(result.data.homes) && result.data.homes instanceof Array) ? result.data.homes as Array<IHome> : []), true, false, ''));
      // dispatch(getUserHomesResult(((isNNObject(result.data.userHomes) && result.data.userHomes instanceof Array) ? result.data.userHomes as Array<IUserHome> : []), true, false, ''));
      
      // dispatch(getHomesResult([], true, false, ''));
      // dispatch(getUserHomesResult([], true, false, ''));
      // dispatch(getTicketsResult(((isNNObject(result.data.tickets) && result.data.tickets instanceof Array) ? result.data.tickets as Array<ITicket> : []), true, false, ''));
      //dispatch(setLoginResult(true));
      callback(true, '');
    }else{
      callback(false, message);
    }
  });
};

export const homeChange = (index: number, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(setHomeIndexResult(index));
  callback(true);
};

export const changePassword = (userId: string, password: string, newPassword: string, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().post(controller + 'changepassword/' + userId, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      callback(true, '');
    }else{
      callback(false, message);
    }
  }, {
    password: password,
    newPassword: newPassword
  });
};

export const forgetPassword = (mobileNumber: string, email: string, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().post(controller + 'resetpassword', (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && isNNObject(result.data.data))
    {
      if(result.data.data === true){
        callback(true, '');
      }else{
        callback(false, result.data.message);
      }
    }else{
      callback(false, message);
    }
  }, {
    mobileNo: mobileNumber,
    emailId: email
  });
};

export const updateProfile = (userId: string, name: string, mobileNo: string, emailId: string, user: IUser, callback: (result: boolean, message: string) => void) =>  (dispatch: any) => {
  new Api().put(controller + 'profile/' + userId, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      user.name = name;
      user.mobileNo = mobileNo;
      user.emailId = emailId;

      dispatch(getUserResult(user));
      callback(true, '');
    }else{
      callback(false, message);
    }
  }, {
    name: name,
    mobileNo: mobileNo,
    emailId: emailId
  });
};

export const updateRole = (role: string, callback: (result: boolean, message: string) => void) =>  (dispatch: any) => {
  dispatch(setRoleResult(role));
  callback(true, '');
};

export const logout = (callback: (result: boolean) => void) => (dispatch: any) => {
  try {
    sessionStorage.removeItem('auth_token');
    sessionStorage.removeItem('user_id');
    sessionStorage.removeItem('client_id');
    dispatch(setLoginResult(false));
    dispatch(setHomeIndexResult(-1));
    dispatch(resetState());
    callback(true);
  } catch (error) {
    // console.error('AsyncStorage error: ' + error.message);
    callback(false);
  }
};

export const setLogin = (isLoggedIn: boolean) => (dispatch: any) => {
  dispatch(setLoginResult(isLoggedIn));
};

export const signup = (data: IMember, clientCode: string, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  data = Object.assign(data, {
    clientCode: clientCode,
    password: data.mobileNo
  });
  new Api().post(controller + 'signup', (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data.length > 0)
    {
      callback(true, '');
    }else{
      callback(false, message);
    }
  }, data);
};

export const setGateNo = (gateNo: string) => (dispatch: any) => {
  dispatch(setGateNoResult(gateNo));
};

