import Api from '../api/api';
import { isNNObject } from '../../util/index';

let controller: string = 'upload';

export const upload = (data: any, callback: (result: boolean, message: string) => void) => (dispatch: any) => {
  new Api().upload(controller, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      callback(true, '');
    }else{
      callback(false, message);
    }
  }, data);
};