import { actionTypes } from '../action-types';
import { IBlocks } from '../../models/store/blocks';

const createEmptyResult = (): IBlocks => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Blocks = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_BLOCKS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IBlocks, payload: IBlocks): IBlocks => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
