import { IServant, ITakeaway } from '../../models/home/servant';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'servant';

export const loadAllByHome = (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Servants.');
    }
  });
};

