import * as React from 'react';
import { isNN } from '../../../../util/index';
import { IVisitor } from '../../../../models/home';
import Toast from '../../../../util/toast';
import { IAppUser } from '../../../../models/app-user';
import { IUser } from '../../../../models/user';
import VisitorsView from '../../gatekeeper/visitors-view';
import { View, Text } from '../../../common/elements';
import { COLOR } from '../../../../constants';
import { styles } from '../../../../styles/page';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  homeId: string,
  onToggleLoading: (isLoading: boolean) => void,
  onLoad: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isActive: boolean
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isActive: true
    }
    this.loadData = this.loadData.bind(this);
    this.onActiveChange = this.onActiveChange.bind(this);
  };

  loadData = () => {
    if(this.props.homeId.length > 0){
      this.props.onToggleLoading(true);
      this.props.onLoad(this.props.homeId, this.state.isActive, (result: Array<IVisitor>) => {
        this.props.onToggleLoading(false);
        this.setState({
          data: result
        });
      }, (message: string) => {
        this.props.onToggleLoading(false);
        Toast.fail(isNN(message) ? message : 'Failed to load Visitors.');
      });
    }
  };

  componentDidMount(){
    this.loadData();
  };

  componentWillUnmount() {}

  onActiveChange = () => {
    this.setState({
      isActive: !this.state.isActive
    }, this.loadData);
  };

  render() {
    return(
      <View>
        <View className='center' style={styles.ele}>
          <Text style={{color: COLOR.PRIMARY, fontWeight: 'bold', fontSize: 17, marginBottom: 10}}>Visitors</Text>
        </View>
        <VisitorsView data={this.state.data} isActive={this.state.isActive} user={this.props.user} appUser={this.props.appUser} onActiveChange={this.onActiveChange} />
      </View>
    );
  }
};