import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IHomes } from '../models/store/homes';
import { remove } from '../store/actions/home';

import Page from '../components/home/manager/homes';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    users: state.Users,
    homes: state.Homes,
    userHomes: state.UserHomes
});
const mapDispatchToProps = (dispatch: any) => ({
    onRemove: (id: string, user: IUser, homes: IHomes, callback: (result: boolean) => void) => dispatch(remove(id, user, homes, callback))
});

export const Homes = connect(mapStateToProps, mapDispatchToProps)(Page);