import { actionTypes } from '../action-types';
import { IOutings } from '../../models/store/outings';

const createEmptyResult = (): IOutings => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Outings = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_OUTINGS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IOutings, payload: IOutings): IOutings => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
