import * as React from 'react';
import { View } from '../common/elements';
import DatePicker from 'react-date-picker';
import Button from '../common/button';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IOutings } from '../../models/store/outings';
import { IOuting } from '../../models/outing';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import { COLOR } from '../../constants';
import { isNNObject } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  outings: IOutings,
  onSave: (data: IOuting, homeId: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  fromDate: any,
  toDate: any,
  place: string,
  address: string,
  datePickerType: string,
  datePickerDate: Date
};

export default class NewOuting extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      fromDate: '',
      toDate: '',
      place: '',
      address: '',
      datePickerType: '',
      datePickerDate: new Date()
    }
    this.onSave = this.onSave.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    // if(isNNObject(this.state.fromDate) && isNNObject(this.state.toDate) && isNN(this.state.place) && isNN(this.state.address)){
    if(isNNObject(this.state.fromDate) && isNNObject(this.state.toDate)){
        this.setState({
          isLoading: true
        });
        let outing = {
          ////homeId: this.props.user.home[this.props.appUser.homeIndex].id,
          _homeId: this.props.user.homes[this.props.appUser.homeIndex].number,
          fromDate: this.state.fromDate,
          toDate : this.state.toDate,
          place: this.state.place,
          address: this.state.address,
          isReturned: false,
          createdBy: this.props.user.name,
          createdAt: new Date()
        } as IOuting;
        this.props.onSave(outing, this.props.user.homes[this.props.appUser.homeIndex]._id, this.props.user, this.props.outings, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              fromDate: '',
              toDate: '',
              place: '',
              address: '',
              datePickerType: '',
              datePickerDate: new Date()
            });
            Toast.success('Outing submitted successfully.');
          }else{
            Toast.fail('Failed to submit Outing.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before submit Outing.');
    }
  };

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Notice', link: 'notice'}, {title: 'New Outing', link: 'new-outing' }]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                fromDate: date
              });
            }}
            value={this.state.fromDate}
            />
          </View>
          <View style={modelStyles.row}>
            <DatePicker
            onChange={(date : any) => {
              this.setState({
                toDate: date
              });
            }}
            value={this.state.toDate}
            />
          </View>
          {/* <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Place" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.place} onChange={(event: any) => this.setState({place: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextArea placeholder="Address" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.address} onChange={(event: any) => this.setState({address: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSave);
            }} />
          </View> */}
          <View className='center' style={modelStyles.saveBtn}>
            <Button
            title="SUBMIT"
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};