import * as React from 'react';
import { View, TextInput } from '../common/elements';
import Button from '../common/button';
import RadioButton from '../common/radio-button';
import { IMember } from '../../models/home';
import { styles as modelStyles } from "../../styles/model";
import {styles as CommonStyles} from '../../styles/common';
import { isNN } from '../../util/index';
import Toast from  '../../util/toast';

interface IProps {
  onSignUp: (data: IMember, clientCode: string, sCallback: (result: boolean) => void) => void
};

interface IState{
  id: string,
  clientCode: string,
  number: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  emailId: string,
  type: string,
  role: string
};

export default class SignUp extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      id: '',
      clientCode: '',
      number: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      emailId: '',
      type: 'OWNER',
      role: 'USER'
    }
    this.onSave = this.onSave.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.clientCode) && isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender) && isNN(this.state.mobileNo) && isNN(this.state.emailId)){
        let member = {
          _id: this.state.id,
          number: '',
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: this.state.emailId ? this.state.emailId.toLowerCase() : '',
          type: this.state.type,
          role: this.state.role
        } as IMember;
        this.props.onSignUp(member, this.state.clientCode, (result: boolean) => {
          if(result === true){
            this.setState({
              id: '',
              clientCode: '',
              number: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              type: 'OWNER',
              role: 'USER'
            });
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before Submit.');
    }
  };

  render() {
    return(
        <View  style={{flex: 1}}>
          <View style={modelStyles.row}>
            <TextInput type='text' className='form-control' placeholder="App Code" style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.clientCode} onChange={(event: any) => this.setState({clientCode: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' className='form-control' placeholder="Name" style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='number' className='form-control' placeholder="Age" style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
            <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
            <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
          </View>            
          <View style={modelStyles.row}>
            <TextInput type='text' className='form-control' placeholder="Mobile Number" style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' className='form-control' placeholder="Email ID" style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.type === 'OWNER'} text='Owner' onPress={() => this.setState({type: 'OWNER'})} />
            <RadioButton isActive={this.state.type === 'TENANT'} text='Tenant' onPress={() => this.setState({type: 'TENANT'})} />
          </View>
          <View className='center' style={modelStyles.saveBtn}>
            <Button
            title="SUBMIT"
            onPress={this.onSave}
            />
          </View>
        </View>
    );
  }
};