import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IAppUser } from '../models/app-user';
import { IUser } from '../models/user/index';
import { IPolls } from '../models/store/polls';
import { IOutings } from '../models/store/outings';
import { IPoll, IVote } from '../models/poll/index';
import { loadAll as loadAllAds } from '../store/actions/ad';
import { loadAll as loadAllPools, save, remove, vote } from '../store/actions/poll';
import { loadAll as loadAllOutings, retunBack } from '../store/actions/outing';
import Page from '../components/home/notification';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    ads: state.Ads,
    polls: state.Polls,
    outings: state.Outings,
    remainders: [],
    complaints: []
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoadAds: (appClientId: string, callback: (result: boolean) => void) => dispatch(loadAllAds(appClientId, callback)),
    onLoadPoll: (appClientId: string, userRole: string, callback: (result: boolean) => void) => dispatch(loadAllPools(appClientId, userRole, callback)),
    onSavePoll: (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => dispatch(save(appClientId, data, polls, user, sCallback)),
    onRemovePoll: (id: string, polls: Array<IPoll>, userId: string, sCallback: (result: boolean) => void) => dispatch(remove(id, polls, userId, sCallback)),
    onVote: (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => dispatch(vote(pollId, votes, polls, user, appUser, sCallback)),
    onLoadOuting: (appClientId: string, homeId: string, callback: (result: boolean) => void) => dispatch(loadAllOutings(appClientId, homeId, callback)),
    onReturnBack: (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => dispatch(retunBack(id, user, outings, callback))
});

export const Notice = connect(mapStateToProps, mapDispatchToProps)(Page);