import * as React from 'react';
import { View, Text } from '../common/elements';
import Button from '../common/button';
import Checkbox from '../common/checkbox';
import Spinner from '../common/spinner';
import { IPolls } from '../../models/store/polls';
import { IPoll, IVote } from '../../models/poll/index';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { styles } from "../../styles/page";
import { isNN, isNNObject } from '../../util/index';
import { percentage } from '../../util/calc';
import { COLOR } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  polls: IPolls,
  onVote: (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  votePollId: string,
  votes: Array<IVote>,
  currentDate: Date
};

export default class Poll extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    let currentDate = new Date();
    this.state = {
      isLoading: false,
      votePollId: '',
      votes: [],
      currentDate: new Date((currentDate.getMonth() + 1) + '/' + currentDate.getDate() + '/' + currentDate.getFullYear())
    }
    this.loadVotes = this.loadVotes.bind(this);
  };

  componentWillUnmount() {};
  
  loadVotes = (poll: IPoll) => {
    let votes = [] as any;
    poll.options.map((po: string, poi: number) => 
      votes.push({
        option: po,
        votes: poll.votes.filter(v => v.option === poi).length
      })
    );
    return(
    votes.sort(function(a: any, b: any){return b.votes - a.votes}).map((vo: any, voi: number) => {
      return(
        <View key={'pvo' + voi} style={styles.ele}>
          <Text>
            {vo.votes + ' (' + percentage(vo.votes, poll.votes.length) +  '%) - ' + vo.option}
          </Text>
        </View>
      )
    })
    )
  };

  onSelectOption = (poll: IPoll, optionId: number) => {
    if(this.state.votePollId === poll._id){
      if(isNN(this.state.votes.filter(v => v.option === optionId))){
        this.setState({
          votes: this.state.votes.filter(v => v.option !== optionId)
        });
      }else{
        let votes = [] as Array<IVote>;
        if(poll.isAllowMultiple){
          votes = this.state.votes;
        }
          votes.push({
            _homeId: this.props.user.homes[this.props.appUser.homeIndex]._id,
            _memberId: this.props.user._id,
            option: optionId,
            isActive: true,
            createdBy: this.props.user._id
          } as IVote);
          this.setState({
            votes: votes
          });
        // if(poll.isAllowMultiple || !isNN(this.state.votes.filter(v => v.pollId === poll.id))){

        // }
      }
    }else{
      let votes = [] as Array<IVote>;
      votes.push({
        _homeId: this.props.user.homes[this.props.appUser.homeIndex]._id,
        _memberId: this.props.user._id,
        option: optionId,
        isActive: true,
        createdBy: this.props.user._id
      } as IVote);
      this.setState({
        votePollId: poll._id,
        votes: votes
      });
    }
  };

  onVote = (pollId: string) => {
    if(this.state.votePollId === pollId &&  isNN(this.state.votes)){
      this.setState({
        isLoading: true
      });
      this.props.onVote(pollId, this.state.votes, this.props.polls, this.props.user, this.props.appUser, (result: boolean) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          this.setState({
            votePollId: '',
            votes: []
          });
          Toast.success('Your vote saved successfully.');
        }else{
          Toast.fail('Failed to save your vote.');
        }
      });
    }else{
      Toast.warn('Please select any option to vote.');
    }
  };

  render() {
    return(
      <View>
      {
        (isNNObject(this.props.polls) && this.props.polls.isUpToDate && !this.props.polls.isError && isNN(this.props.polls.data.filter(p => isNN(p.options) && new Date(p.fromDate) <= new Date() && new Date(p.toDate) >= this.state.currentDate))) ?
        this.props.polls.data.filter(p => isNN(p.options) && new Date(p.fromDate) <= new Date() && new Date(p.toDate) >= this.state.currentDate).map((d: IPoll, i: number) => {
          return(
            <View key={"p" + d._id} style={{...styles.row, borderBottomWidth: 1, borderBottomColor: COLOR.HR_COLOR, borderBottomStyle: 'solid'}}>
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.topic}</Text>
              </View>
              <View style={styles.ele}>
                <Text>{d.description}</Text>
              </View>
              {
                (d.isOneVotePerFlat !== true || !isNN(d.votes) || !isNN(d.votes.filter(vo => vo._homeId === this.props.user.homes[this.props.appUser.homeIndex]._id))) &&
                d.options.map((o: string, oi: number) => {
                  return(
                    <View key={"popt" + oi} style={styles.ele}>
                      <Checkbox isActive={(this.state.votePollId === d._id && isNN(this.state.votes.filter(v => v.option === oi)))} text={o} onPress={() => this.onSelectOption(d, oi)} />
                    </View>
                  )
                })
              }
              {
                (d.isOneVotePerFlat !== true || !isNN(d.votes) || !isNN(d.votes.filter(vo => vo._homeId === this.props.user.homes[this.props.appUser.homeIndex]._id))) &&
                <View style={{marginTop: 5, width: '100%'}}>
                  <Button title="Vote" onPress={() => this.onVote(d._id)} style={{width: '100%'}} />
                </View>
              }
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>Your Votes:</Text>
              </View>
              {isNN(d.options) &&
                this.loadVotes(d)
              }
            </View>
          )
      })
      :
      <Text style={{padding: 20}}>{(this.props.polls.isUpToDate ? (this.props.polls.isError ? this.props.polls.errorMessage : "No active poll found!!!") : "Loading...")}</Text>
    }
    <Spinner visible={this.state.isLoading} />
    </View>
    );
  }
};