import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user/user';
import { IVehicle } from '../models/home';
import { save, remove } from '../store/actions/vehicle';
import Page from '../components/home/vehicles';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    homeIndex: state.AppUser.homeIndex
});
const mapDispatchToProps = (dispatch: any) => ({
    onSave: (data: IVehicle, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => dispatch(save(data, user, homeIndex, sCallback)),
    onRemove: (id: string, user: IUser, homeIndex: number, sCallback: (result: boolean) => void) => dispatch(remove(id, user, homeIndex, sCallback))
});

export const Vehicles = connect(mapStateToProps, mapDispatchToProps)(Page);