import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IUsers } from '../models/store/users';
import { removeByAM } from '../store/actions/member';

import Page from '../components/home/manager/users';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    users: state.Users,
    homes: state.Homes,
    userHomes: state.UserHomes
});
const mapDispatchToProps = (dispatch: any) => ({
    onRemove: (id: string, homeIndex: number, user: IUser, users: IUsers, callback: (result: boolean) => void) => dispatch(removeByAM(id, homeIndex, user, users, callback))
});

export const Users = connect(mapStateToProps, mapDispatchToProps)(Page);