import { actionTypes } from '../action-types';
import { IAppUser } from '../../models/app-user/app-user';

const createEmptyResult = (): IAppUser => ({
    isLoggedIn: false,
    role: '',
    homeIndex: -1
});

export const AppUser = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.SET_LOGIN_STATUS:
            return handleLoginResult(state, action.payload);
        case actionTypes.SET_ROLE:
            return handleRoleResult(state, action.payload);
        case actionTypes.SET_HOME_INDEX:
            return handleHomeIndexResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleLoginResult = (state: IAppUser, payload: boolean): IAppUser => {
    return {
        ...state,
        isLoggedIn: payload
    };
};

const handleRoleResult = (state: IAppUser, payload: string): IAppUser => {
    return {
        ...state,
        role: payload
    };
};

const handleHomeIndexResult = (state: IAppUser, payload: number): IAppUser => {
    return {
        ...state,
        homeIndex: payload
    };
};
