import * as React from 'react';
import { ScrollView, View, Text } from '../common/elements';
import Button from '../common/button';
import Spinner from '../common/spinner';
import { IUser } from '../../models/user/user';
import { IHome } from '../../models/home/home';
import { IPayment } from '../../models/home';
import { ITransaction } from '../../models/home/transaction';
import { isNN, generateUUID } from '../../util/index';
import Toast from  '../../util/toast';
import { MONTH, COLOR } from '../../constants';

interface IProps {
  history: any,
  user: IUser,
  home: IHome,
  onPaymentUpload: (file: any, homeId: string, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => void
};

interface IState {
  isLoading: boolean,
  payment: {
    paymentId: string,
    amount: number,
    transactionRef: string
  }
};

export default class MyDues extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      payment: {
        paymentId: '',
        amount: 0,
        transactionRef: ''
      }
    }
    this.getTotalDue = this.getTotalDue.bind(this);
    this.payNow = this.payNow.bind(this);
    this.clearTxn = this.clearTxn.bind(this);
    this.txnFailure = this.txnFailure.bind(this);
  };

  componentWillUnmount() {};
  
  getTotalDue = () => {
    let totalDue = 0;
    this.props.home.payments.map((d: IPayment, i: number) => {
      let amountPaid = 0;
        d.transactions.filter((t, j) => t.isSuccess === true).map((data, index) => amountPaid += data.amount);
      totalDue += d.amount - amountPaid;
      return 1;
    });
    return totalDue;
  };

  payNow = (paymentId: string, amount: number) => {
    if(paymentId && paymentId.length > 0 && amount && amount > 0){
      if(isNN(this.props.user.client.upi.id) && isNN(this.props.user.client.upi.name)){
        let transactionRef = generateUUID();
        this.setState({
          payment: {
            paymentId: paymentId,
            amount: amount,
            transactionRef: transactionRef
          }
        });
        
        Toast.warn('Payment is in progress. Please contact administrator.');
        // RNUpiPayment.initializePayment({
        //   vpa: this.props.user.client.upi.id, // or can be john@ybl or mobileNo@upi
        //   payeeName: this.props.user.client.upi.name,
        //   amount: amount + '',
        //   transactionRef: transactionRef,
        //   transactionNote: ''
        // }, this.successCallback, this.failureCallback);
      }else{
        Toast.warn('Please update UPI ID and UPI Name to app administrator to proceed further.');
      }
    }else{
      Toast.warn('Amount should be more then 0 to proceed payment.');
    }
  };
  
  successCallback = (result: any) => {
    console.log('Payment Success...', result);
    if(result && result.Status)
    {
      if(result.Status === 'SUCCESS' && result.responseCode && result.responseCode === '00'){
        this.setState({
          isLoading: true
        });
        let transaction = {
          txnRef: result.txnRef,
          txnId: result.txnId,
          amount: this.state.payment.amount,
          status: 'PAID',
          isSuccess: true,
          reason: ''
        } as ITransaction;
        this.props.onPaymentUpdate(this.state.payment.paymentId, transaction, (updateResult: boolean) => {
          this.setState({
            isLoading: false
          });
          this.clearTxn();
          if(updateResult === true){
            Toast.success('Payment success.');
          }else{
            Toast.fail('Payment failed.');
          }
        });
      }else{
        this.txnFailure(result);
      }
    }else{
      this.txnFailure(result);
    }
  };
   
  failureCallback = (result: any) => {
    this.txnFailure(result);
  };

  txnFailure = (result: any) => {
    console.log('Payment Failure...', result);
    let msg: string = '';
    if(result && result.Status)
    {
      if(result.Status === 'FAILURE' && result.responseCode && result.responseCode === 'ZM'){
        msg = 'Payment failed because of wrong pin.';
      }
      else if(result.Status === 'FAILURE' && result.message && result.message.length > 0){
        msg = 'Payment failed because ' + result.message;
      }else{
        msg = 'Payment failed.';
      }
    }
    else{
      msg = 'Payment failed.';
    }
    Toast.fail(msg);
    this.setState({
      isLoading: true
    });
    let transaction = {
      txnRef: this.state.payment.transactionRef,
      txnId: '',
      amount: this.state.payment.amount,
      status: 'FAIL',
      isSuccess: false,
      reason: msg
    } as ITransaction;
    this.props.onPaymentUpdate(this.state.payment.paymentId, transaction, (updateResult: boolean) => {
      this.setState({
        isLoading: false
      });
      this.clearTxn();
      // if(updateResult === true){
      //   Toast.success('Payment success.');
      // }else{
      //   Toast.fail('Payment failed.');
      // }
    });
  };

  clearTxn = () => {
    this.setState({
      payment: {
        paymentId: '',
        amount: 0,
        transactionRef: ''
      }
    });
  };

  render() {
    return (
      <View>
        <View style={{ margin: 5 }}>
          <Text style={{ marginTop: 2, fontWeight: 'bold', fontSize: 17 }}>{'Total Due Amount: Rs ' + this.getTotalDue()}</Text>
        </View>
        <ScrollView>
        {isNN(this.props.home.payments) ?
        this.props.home.payments.filter((p: IPayment, i: number) => {
          let amountPaid = 0;
          if (isNN(p.transactions)) {
            p.transactions.filter(t => t.isSuccess === true).map((p, j) => amountPaid += p.amount);
          }
          return(amountPaid < p.amount);
        }).map((d: IPayment, i: number) => {
            let amountPaid = 0;
            if (isNN(d.transactions)) {
              d.transactions.filter(p => p.isSuccess === true).map((data, index) => amountPaid += data.amount);
            }
            if (amountPaid < d.amount) {
              return (
                <View key={'my-due' + i} style={(i === 0) ? styles.myDueFirst : styles.myDue }>
                  <View style={{ marginTop: 4 }}>
                    <Text>{'Due for: ' + MONTH.filter(m => m.value === d.forMonth)[0].title  + '/' + d.forYear}</Text>
                  </View>
                  <View style={{ width: '100%', marginTop: 4, marginBottom: 3 }}>
                    <Text style={{ fontSize: 17 }}>{'Rs ' + (d.amount - amountPaid)}</Text>
                  </View>
                  <View style={{ width: '100%'}}>
                    <Text>{'Due Date: 15/' + MONTH.filter(m => m.value === d.forMonth)[0].title + '/' + d.forYear}</Text>
                  </View>
                  <View style={{ marginTop: 4 }}>
                    <Button 
                    title='PAY NOW'
                    onPress={() => this.payNow(d._id, (d.amount - amountPaid))} 
                    />
                  </View>
                </View>
              )
            } else {
              return null;
            }
          })
          :<Text style={{ padding: 20 }}>No Dues found!!!</Text>
        }
        </ScrollView>
        <Spinner visible={this.state.isLoading} />
      </View>
    );
  }
};

const styles = {
  myDue: {
    margin: 7, 
    paddingLeft: 15,
    borderLeftColor: COLOR.HR_COLOR, 
    borderLeftWidth: 1 
  },
  myDueFirst: {
    margin: 7
  }
};