import * as React from 'react';
import { Text } from '../common/elements';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IAds } from '../../models/store/ads';
import { IUsers } from '../../models/store/users';
import { IHomes } from '../../models/store/homes';
import { IBlocks } from '../../models/store/blocks';
import { IOutings } from '../../models/store/outings';
import { ITransaction } from '../../models/home/transaction';
import { ITickets } from '../../models/store/tickets';
import { IVisitor } from '../../models/home';
import Home from './home';
import AMHome from './manager/home';
import GKHome from './gatekeeper/home';
import { ROLE } from '../../constants/index';
import { isNN } from '../../util';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  ads: IAds,
  outings: IOutings,
  users: IUsers,
  homes: IHomes,
  tickets: ITickets,
  blocks: IBlocks,
  gateNo: string,
  onGateNoChanged: (gateNo: string) => void,
  onPaymentUpload: (file: any, homeId: string, user: IUser, callback: (result: boolean) => void) => void,
  onPaymentUpdate: (paymentId: string, transaction: ITransaction, homeIndex: number, user: IUser, callback: (result: boolean) => void) => void,
  onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearchVisitor: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState {
};

export default class Dashboard extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
    }
  };

  componentWillUnmount() {};
  
  render() {
    if(this.props.appUser.role === ROLE.USER) {
      if(this.props.appUser.homeIndex >= 0 && isNN(this.props.user.homes)) {
        return(<Home history={this.props.history} user={this.props.user} home={this.props.user.homes[this.props.appUser.homeIndex]} onPaymentUpload={(file: any, homeId: string, callback: (result: boolean) => void) => this.props.onPaymentUpload(file, homeId, this.props.user, callback)} onPaymentUpdate={(paymentId: string, transaction: ITransaction, callback: (result: boolean) => void) => this.props.onPaymentUpdate(paymentId, transaction, this.props.appUser.homeIndex, this.props.user, callback)} />);
      }else{
        return(<Text style={{margin: 20, padding: 15, paddingLeft: 59}}>No home assigned to you. Please contact administrator/manager.</Text>);
      }
    }
    else if(this.props.appUser.role === ROLE.ADMIN || this.props.appUser.role === ROLE.MANAGER) {
      return(<AMHome history={this.props.history} appUser={this.props.appUser} user={this.props.user} ads={this.props.ads} users={this.props.users} homes={this.props.homes} tickets={this.props.tickets} blocks={this.props.blocks} />);
    }
    else if(this.props.appUser.role === ROLE.GATEKEEPER) {
      return(<GKHome history={this.props.history} appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} gateNo={this.props.gateNo} onGateNoChanged={this.props.onGateNoChanged} onSaveVisitor={this.props.onSaveVisitor} onUpload={this.props.onUpload} onSearchVisitor={this.props.onSearchVisitor} />);
    }else{
      return(<Text style={{margin: 20, padding: 15, paddingLeft: 59}}>No role assigned to you. Please contact administrator/manager.</Text>);
    }
  }
};