import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IHomes } from '../models/store/homes';
import { IHome } from '../models/home';
import { IUser } from '../models/user';
import { load, save, checkNumberAvail } from '../store/actions/home';
import Page from '../components/home/manager/edit-home';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    users: state.Users,
    homes: state.Homes,
    blocks: state.Blocks
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => dispatch(load(id, sCallback, fCallback)),
    onSave: (data: IHome, user: IUser, homes: IHomes, callback: (result: boolean, message: string) => void) => dispatch(save(data, user, homes, callback)),
    onCheckNumberAvail: (id: string, number: string, callback: (result: boolean) => void) => dispatch(checkNumberAvail(id, number, callback))
});

export const EditHome = connect(mapStateToProps, mapDispatchToProps)(Page);