import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Modal from 'react-responsive-modal';
import { View, Text } from '../common/elements';
import Breadcrum from '../common/breadcrum';
import Spinner from '../common/spinner';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IVisitor } from '../../models/home/visitor';
import { IHomes } from '../../models/store/homes';
import { styles as pageStyles } from '../../styles/page';
import { styles as modelStyles } from '../../styles/model';
import {styles as popupStyles} from '../../styles/popup';
import Toast from  '../../util/toast';
import { COLOR, ROLE } from '../../constants';
import VisitorsView from './gatekeeper/visitors-view'
import NewVisitor from './gatekeeper/new-visitor';
import { isNN } from '../../util';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  homes: IHomes,
  gateNo: string,
  onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  void,
  onApprove: (id: string, callback: (result: boolean) => void) => void,
  onReject: (id: string, callback: (result: boolean) => void) => void,
  onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => void,
  onUpload: (data: any, callback: (result: boolean, message: string) => void) => void,
  onSearch: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isActive: boolean,
  isRequestNew: boolean,
  isLoading: boolean,
  passCode: string
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isActive: true,
      isRequestNew: false,
      isLoading: false,
      passCode: ''
    }
    this.loadData = this.loadData.bind(this);
    this.onActiveChange = this.onActiveChange.bind(this);
    this.onApprove = this.onApprove.bind(this);
    this.onReject = this.onReject.bind(this);
    this.onSave = this.onSave.bind(this);
  };

  loadData = () => {
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, this.props.user.homes[this.props.appUser.homeIndex]._id, this.state.isActive, (result: Array<IVisitor>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };

  componentWillUnmount() {};
  
  componentDidMount(){
    this.loadData();
  };
  
  onActiveChange = () => {
    this.setState({
      isActive: !this.state.isActive
    }, this.loadData);
  };
  
  onApprove = (id: string) => {
    confirmAlert({
      title: 'Confirm to Approve Visitor Entry',
      message: 'Are you sure you wish to Approve the visitor to Enter?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onApprove(id, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                let foundIndex = this.state.data.findIndex(v => v._id === id);
                this.state.data[foundIndex].isApproved = true;
                this.state.data[foundIndex].approvedBy = this.props.user.name;
                this.state.data[foundIndex].approvedAt = new Date();
                this.setState({ });
                Toast.success('Visitor approved successfully.');
              }else{
                Toast.fail('Failed to approve the entry of visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onReject = (id: string) => {
    confirmAlert({
      title: 'Confirm to Reject Visitor Entry',
      message: 'Are you sure you wish to reject visitor?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onReject(id, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                let foundIndex = this.state.data.findIndex(v => v._id === id);
                this.state.data[foundIndex].isRejected = true;
                this.state.data[foundIndex].approvedBy = this.props.user.name;
                this.state.data[foundIndex].approvedAt = new Date();
                this.setState({ });
                Toast.success('Visitor rejected successfully.');
              }else{
                Toast.fail('Failed to reject visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onSave = (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => {
    this.props.onSave(homeId, data, user, appUser, (result: IVisitor) => {
      sCallback(result);
      this.state.data.unshift(result);
      this.setState({
        passCode: result.passCode,
        isRequestNew: false
      });
    }, (message: string) => {
      fCallback(message);
    });
  };

  render() {
    return(
      <main style={pageStyles.content}>
        <Breadcrum data={[{title: 'Visitors', link: 'visitors'}]} />
        {this.props.appUser.role === ROLE.USER &&
        <View style={{...modelStyles.row, textAlign: 'right'}}>
          <Text className='pointer' onClick={() => this.setState({isRequestNew: !this.state.isRequestNew})} style={{color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'right'}}>{this.state.isRequestNew ? '< Back to Visitors' : 'Request for a New Visitor'}</Text>
        </View>
        }
        {!this.state.isRequestNew && <VisitorsView data={this.state.data} isActive={this.state.isActive} user={this.props.user} appUser={this.props.appUser} onActiveChange={this.onActiveChange} onApprove={this.onApprove} onReject={this.onReject} />}
        {this.state.isRequestNew && <NewVisitor appUser={this.props.appUser} user={this.props.user} homes={this.props.homes} gateNo={this.props.gateNo} onToggleLoading={(isLoading) => this.setState({ isLoading: isLoading })} onSave={this.onSave} onUpload={this.props.onUpload} onSearch={this.props.onSearch} />}
        {isNN(this.state.passCode) &&
        <Modal
        open={isNN(this.state.passCode)}
        onClose={() => this.setState({ passCode: '' })}>
          <View style={popupStyles.container}>
            <View style={modelStyles.row}>{'Visitor Request created successfully. Please share the passcode(' + this.state.passCode + ') to yor visitor and ask him/her to show it to the security in the gate while entering.'}</View>
            <View style={{...modelStyles.row, textAlign: 'center', fontSize: 30, fontWeight: 'bold', color: COLOR.SECONDARY}}>{this.state.passCode}</View>
          </View>
        </Modal>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};