import * as React from 'react';
import Modal from 'react-responsive-modal';
import { Link } from 'react-router-dom';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text, Image } from '../common/elements';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN } from '../../util/index';
import Images from '../images';
import {styles as popupStyles} from '../../styles/popup';
import { ROLE } from '../../constants/index';
import { IHome } from '../../models/home';
import { COLOR } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  appUser: IAppUser,
  user: IUser,
  onHomeChange: (index: number, callback: (result: boolean) => void) => void,
  onLogout: (callback: (result: boolean) => void) => void
};

interface IState{ 
  isShowHomeModel: boolean
};

export default class TitleBar extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isShowHomeModel: false
    };
    this.onToggleHomeModel = this.onToggleHomeModel.bind(this);
    this.onHomeChange = this.onHomeChange.bind(this);
    this.logout = this.logout.bind(this);
  }
  
  componentWillUnmount() {};
  
  onToggleHomeModel = (isShow: boolean) => {
    this.setState({
      isShowHomeModel: isShow
    });
  }

  onHomeChange = (index: number) => {
    this.props.onHomeChange(index, (result: boolean) => {
      if(result === true){
        this.onToggleHomeModel(false);
      }else{
      }
    });
  }
  
  logout = () => {
    confirmAlert({
      title: 'Confirm to logout',
      message: 'Are you sure you wish to logout?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.props.onLogout((result: boolean) => {
              if(result === true){
                Toast.success('Logout Success!');
              }else{
                Toast.fail('Failed to logout!');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return (
      <header className='center' style={styles.titleBar}>
        <Link className='fl' to=''>
          <Image src={Images.logoInv} className='' style={{ width: 22, height: 22}} />
        </Link>
          {/* <Image src={this.props.isMenuExpand ? Images.close : Images.menu} className='fl pointer' style={(this.props.isMenuExpand ? { width: 17, height: 17, marginLeft: 15, marginTop: 6} : { width: 22, height: 22, marginLeft: 15, marginTop: 4})} onClick={this.props.onToggleMenu} /> */}
          {(this.props.appUser.role === ROLE.USER) ?
          <Text className='pointer' style={styles.titleText} onClick={() => this.onToggleHomeModel(true)}>{((isNN(this.props.user.homes) && this.props.appUser.homeIndex >= 0) ? this.props.user.homes[this.props.appUser.homeIndex].number : "")}</Text>
          :
          <Text style={styles.titleText}>{(this.props.appUser.role === ROLE.ADMIN ? 'Admin' : (this.props.appUser.role === ROLE.MANAGER ? 'Manager' : (this.props.appUser.role === ROLE.GATEKEEPER ? 'Gate Keeper' : this.props.appUser.role)))}</Text>
          }
          <span className='fr pointer' onClick={this.logout} style={{flex: 1,}}>
            <Image src={Images.logout} style={{ width: 20, height: 20}}  />
          </span>
        {isNN(this.props.user.homes) &&
        <Modal
          open={this.state.isShowHomeModel}
          onClose={() => this.onToggleHomeModel(false)}>
          <View style={popupStyles.container}>
            {
            this.props.user.homes.map((d: IHome, i: number) => {
              return(
                <Text className='col-12 pointer' key={'h' + i} onClick={() => this.onHomeChange(i)} style={(this.props.appUser.homeIndex === i ? {...styles.homeList, ...styles.activeHome} : {...styles.homeList, ...styles.inActiveHome})}>
                {'' + d.number}
                </Text>
              )
            })
            }
          </View>
        </Modal>
        }
      </header>
    );
  }
};

const styles = {
  titleBar: {
    backgroundColor: COLOR.PRIMARY,
    // alignItems: 'flex-start',
    // justifyContent: 'flex-start',
    width: '100%',
    // flexDirection: 'row',
    padding: 7
  },
  titleText: {
    flex: 15, 
    // textAlign: 'center', 
    color: COLOR.FONT_COLOR3, 
    // fontWeight: 'bold', 
    fontSize: 15
  },
  homeList: {
    padding: 15, 
    borderBottomWidth: 1, 
    borderBottomColor: COLOR.PRIMARY,
    'border-bottom-style': 'solid'
    // textAlign: 'center'
  },
  inActiveHome: {
    color: COLOR.FONT_COLOR2
  },
  activeHome: {
    color: COLOR.PRIMARY,
    // fontWeight: 'bold',
    fontSize: 20
  }
};