import * as React from 'react';
import { HashRouter } from 'react-router-dom';
import { Login } from '../containers/login';
import { Home } from '../containers/home';
import { IAppUser } from '../models/app-user/app-user';
import { isNN } from '../util';

interface IProps {
  appUser: IAppUser,
  setLogin: (isLoggedIn: boolean) => void
};

interface IState{ 
};

export default class AppContainer extends React.Component<IProps, IState> {
  // constructor(props: any) {
  //   super(props);
  // };

  componentDidMount() {
    let token: string | null = sessionStorage.getItem('auth_token');
    if(isNN(token)){
      this.props.setLogin(true);
    }
  };

  componentWillUnmount() {};
  
  render() {
    return(
        this.props.appUser.isLoggedIn ? 
          <HashRouter>
            <Home />
          </HashRouter>
        :
          <Login />
    )
  }
};