import * as React from 'react';
import Modal from 'react-responsive-modal';
import { ScrollView, TouchableHighlight, View, TextInput, Text, Image } from '../../common/elements';
import Button from '../../common/button';
import RadioButton from '../../common/radio-button';
import Checkbox from '../../common/checkbox';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { styles } from "../../../styles/page";
import { styles as modelStyles } from "../../../styles/model";
import { styles as commonStyles} from '../../../styles/common';
import { styles as mlistStyles} from '../../../styles/mlist';
import {styles as popupStyles} from '../../../styles/popup';
import { COLOR, ROLE } from '../../../constants';
import { isNN } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IUserHomes } from '../../../models/store/user-homes';
import { IHome, IMember } from '../../../models/home';
import Images from '../../images';
import { styles as iconStyles } from "../../../styles/icon";
import Toast from  '../../../util/toast';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onLoad: (id: string, sCallback: (result: IMember, homeIds: Array<string>) => void, fCallback: (message: string) => void) => void,
  onSave: (data: IMember, homeIds: Array<string>, user: IUser, users: IUsers, isApprove: Boolean, callback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  id: string,
  number: string,
  name: string,
  age: number,
  gender: string,
  mobileNo: string,
  emailId: string,
  type: string,
  homeIds: Array<string>,
  role: {
    isAdmin: boolean,
    isManager: boolean,
    isGatekeeper: boolean,
    isUser: boolean
  },
  isShowHomes: boolean
};

export default class EditUser extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      id: '',
      number: '',
      name: '',
      age: 0,
      gender: '',
      mobileNo: '',
      emailId: '',
      type: 'OWNER',
      homeIds: [],
      role: {
        isAdmin: false,
        isManager: false,
        isGatekeeper: false,
        isUser: false
      },
      isShowHomes: false
    }
    this.getHomes = this.getHomes.bind(this);
    this.onSave = this.onSave.bind(this);
    this.onToggleHomeModel = this.onToggleHomeModel.bind(this);
  };

  componentDidMount(){
    let userId = this.props.match.params.id;
    if(userId.length > 0 && userId !== '0'){
      this.setState({
        isLoading: true
      });
      this.props.onLoad(userId, (result: IMember, homeIds: Array<string>) => {
        if(result && isNN(result._id)){
          this.setState({
            isLoading: false,
            id: result._id,
            number: result.number,
            name: result.name,
            age: result.age,
            gender: result.gender,
            mobileNo: result.mobileNo,
            emailId: result.emailId,
            type: result.type,
            homeIds: homeIds,
            role: {
              isAdmin: (',' + result.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0,
              isManager: (',' + result.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0,
              isGatekeeper: (',' + result.role + ',').indexOf(',' + ROLE.GATEKEEPER + ',') >= 0,
              isUser: (',' + result.role + ',').indexOf(',' + ROLE.USER + ',') >= 0,
            }
          });
        }else{
          Toast.fail('Failed to load User.');
        }
      }, (message: string) => {
        Toast.fail(isNN(message) ? message : 'Failed to load User.');
      });
    }

    /*
    if(userId > 0 && (this.props.users.isUpToDate && !this.props.users.isError && isNN(this.props.users.data))){
      let users = this.props.users.data.filter((u: IUser, i: number) => u._id === userId);
      if(isNNObject(users) && users.length > 0){
        let homeIds = [] as Array<string>;
        if(this.props.userHomes.isUpToDate && !this.props.userHomes.isError && isNN(this.props.userHomes.data)){
          this.props.userHomes.data.filter((uh: IUserHome, i: number) => uh.userId === userId).map(uh => {
            homeIds.push(uh.homeId);
          });
        }
        this.setState({
          id: users[0]._id,
          name: users[0].name,
          mobileNo: users[0].mobileNo,
          emailId: users[0].emailId,
          ////ownername: users[0].ownername,
          ////ownermobile: users[0].ownermobile,
          homeIds: homeIds,
          role: {
            isAdmin: (',' + users[0].type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0,
            isManager: (',' + users[0].type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0,
            isGatekeeper: (',' + users[0].type + ',').indexOf(',' + ROLE.GATEKEEPER + ',') >= 0,
            isUser: (',' + users[0].type + ',').indexOf(',' + ROLE.USER + ',') >= 0,
          }
        });
      }
    }
    */
  };

  componentWillUnmount() {};
  
  getHomes = () => {
    let homes = '';
    if(isNN(this.state.homeIds) && (this.props.homes.isUpToDate && !this.props.homes.isError && isNN(this.props.homes.data))){
      this.props.homes.data.filter(d => this.state.homeIds.indexOf(d._id) >= 0).map((h: IHome, i: number) => homes += (homes.length > 0 ? ', ' : '') + h.number);
    }
    return homes;
  };

  onSave = () => {
    if(isNN(this.state.name) && this.state.age > 0 && isNN(this.state.gender)){
      let role = '';
      if(this.state.role.isAdmin){
        role = ROLE.ADMIN
      }
      if(this.state.role.isManager){
        role += (role.length > 0 ? ',' : '') + ROLE.MANAGER
      }
      if(this.state.role.isGatekeeper){
        role += (role.length > 0 ? ',' : '') + ROLE.GATEKEEPER
      }
      if(this.state.role.isUser){
        role += (role.length > 0 ? ',' : '') + ROLE.USER
      }
        this.setState({
          isLoading: true
        });
        let member = {
          _id: this.state.id,
          number: '',
          name: this.state.name,
          age: this.state.age,
          gender: this.state.gender,
          mobileNo: this.state.mobileNo,
          emailId: this.state.emailId ? this.state.emailId.toLowerCase() : '',
          type: this.state.type,
          role: role
        } as IMember;
        this.props.onSave(member, this.state.homeIds, this.props.user, this.props.users, this.props.match.params.isApprove === 'true', (result: boolean, message: string) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              id: '',
              number: '',
              name: '',
              age: 0,
              gender: '',
              mobileNo: '',
              emailId: '',
              type: 'OWNER',
              homeIds: [],
              role: {
                isAdmin: false,
                isManager: false,
                isGatekeeper: false,
                isUser: false
              }
            });
            Toast.success('User saved successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save user.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onToggleHomeModel = (isShow: boolean) => {
    this.setState({
      isShowHomes: isShow
    });
  }

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Users', link: 'users'}, {title: 'Edit User', link: 'edit-user/0'}]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Name" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Age" className='form-control' maxLength={2} style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.age > 0 ? this.state.age + '' : ''} onChange={(event: any) => this.setState({age: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.gender === 'M'} text='Male' onPress={() => this.setState({gender: 'M'})} />
            <RadioButton isActive={this.state.gender === 'F'} text='Female' onPress={() => this.setState({gender: 'F'})} />
            <RadioButton isActive={this.state.gender === 'O'} text='Other' onPress={() => this.setState({gender: 'O'})} />
          </View>            
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Mobile Number" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.mobileNo} onChange={(event: any) => this.setState({mobileNo: event.target.value})} />
          </View>
          <View style={styles.ele}>
            <TextInput type='text' placeholder="Email ID" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.emailId} onChange={(event: any) => this.setState({emailId: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <RadioButton isActive={this.state.type === 'OWNER'} text='Owner' onPress={() => this.setState({type: 'OWNER'})} />
            <RadioButton isActive={this.state.type === 'TENANT'} text='Tenant' onPress={() => this.setState({type: 'TENANT'})} />
          </View>
          <View style={modelStyles.row}>
            <Text>Role: </Text>
          </View>
          <View style={modelStyles.row}>
            <Checkbox isActive={this.state.role.isAdmin} text={ROLE.ADMIN} onPress={() => {
              this.state.role.isAdmin = !this.state.role.isAdmin;
              this.setState({});
            }} />
            <Checkbox isActive={this.state.role.isManager} text={ROLE.MANAGER} onPress={() => {
              this.state.role.isManager = !this.state.role.isManager;
              this.setState({});
            }} />
            <Checkbox isActive={this.state.role.isGatekeeper} text={ROLE.GATEKEEPER} onPress={() => {
              this.state.role.isGatekeeper = !this.state.role.isGatekeeper;
              this.setState({});
            }} />
            <Checkbox isActive={this.state.role.isUser} text={ROLE.USER} onPress={() => {
              this.state.role.isUser = !this.state.role.isUser;
              this.setState({});
            }} />
          </View>
          <View style={modelStyles.row}>
            <Text>{'Home(s): ' + this.getHomes()}</Text>
          </View>
          <View style={modelStyles.row}>
            <TouchableHighlight style={{marginRight: 25}} onClick={() => {
              this.setState({
                isShowHomes: true
              });
             }}>
              <Image src={Images.edit} style={iconStyles.editIcon} />
            </TouchableHighlight>
          </View>
          {(this.props.homes.isUpToDate && !this.props.homes.isError && isNN(this.props.homes.data)) &&
          <Modal
          open={this.state.isShowHomes}
          onClose={() => this.onToggleHomeModel(false)}>
            <View style={popupStyles.container}>
              <ScrollView style={{...mlistStyles.mListCnr, maxHeight: '50%'}}>
                {
                  this.props.homes.data.map((d: IHome, i: number) => {
                    return(
                      <Text key={'home' + i} style={(this.state.homeIds.indexOf(d._id) >= 0 ? mlistStyles.mListActiveRow : mlistStyles.mListRow)} onClick={() => {
                        if(this.state.homeIds.indexOf(d._id) >= 0){
                          this.setState({
                            homeIds: this.state.homeIds.filter(e => e !== d._id)
                          });
                        }else{
                          this.state.homeIds.push(d._id);
                          this.setState({});
                        }
                      }}>{d.number}</Text>
                    )
                  })
                }
                </ScrollView>
              </View>
          </Modal>
          }
          <View style={modelStyles.saveBtn}>
            <Button
            title={this.props.match.params.isApprove === 'true' ? 'Approve' : 'SAVE'}
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};

