import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IVisitor } from '../../../models/home/visitor';
import { styles as pageStyles } from "../../../styles/page";
import Toast from  '../../../util/toast';
import { IHomes } from '../../../models/store/homes';
import VisitorsView from './visitors-view';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  homes: IHomes,
  gateNo: string,
  onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => void,
  onEnter: (id: string, gateNo: string, callback: (result: boolean) => void) => void,
  onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IVisitor>,
  isActive: boolean,
  isLoading: boolean
};

export default class Visitors extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isActive: true,
      isLoading: false
    }
    this.loadData = this.loadData.bind(this);
    this.onActiveChange = this.onActiveChange.bind(this);
    this.onEnter = this.onEnter.bind(this);
    this.onExit = this.onExit.bind(this);
  };

  loadData = () => {
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, '', this.state.isActive, (result: Array<IVisitor>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };

  componentDidMount(){
    this.loadData();
  };
  
  componentWillUnmount(){};

  onActiveChange = () => {
    this.setState({
      isActive: !this.state.isActive
    }, this.loadData);
  };

  onEnter = (id: string) => {
    confirmAlert({
      title: 'Confirm to Allow Visitor Entry',
      message: 'Are you sure you wish to Allow the visitor to Enter?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onEnter(id, this.props.gateNo, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                let foundIndex = this.state.data.findIndex(v => v._id === id);
                this.state.data[foundIndex].isEntered = true;
                this.state.data[foundIndex].enteredBy = this.props.user.name;
                this.state.data[foundIndex].enteredAt = new Date();
                this.setState({ });
                Toast.success('Visitor entered successfully.');
              }else{
                Toast.fail('Failed to allow the entry of visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onExit = (id: string) => {
    confirmAlert({
      title: 'Confirm to Exit Visitor',
      message: 'Are you sure you wish to exit visitor?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onExit(id, this.props.gateNo, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                this.setState({
                  data: this.state.data.filter(v => v._id !== id)
                });
                Toast.success('Visitor exit successfully.');
              }else{
                Toast.fail('Failed to exit visitor.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={pageStyles.content}>
        <Breadcrum data={[{title: 'Visitors', link: 'all-visitors'}]} />
        <VisitorsView user={this.props.user} appUser={this.props.appUser} data={this.state.data} isActive={this.state.isActive} onActiveChange={this.onActiveChange} onEnter={this.onEnter} onExit={this.onExit} />
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};