import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, TouchableHighlight, TextInput, Image, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { styles } from "../../../styles/page";
import { styles as iconStyles } from "../../../styles/icon";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { IHome, IMember } from '../../../models/home';
import { IUserHomes } from '../../../models/store/user-homes';
import { IUserHome } from '../../../models/user-home';
import Images from '../../images';
import { COLOR } from '../../../constants';
import { isNN, onEnter } from '../../../util';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser
  user: IUser,
  users: IUsers,
  homes: IHomes,
  userHomes: IUserHomes,
  onRemove: (id: string, user: IUser, homes: IHomes, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  searchText: string,
  searchResult: Array<IHome>,
  searchResultIndex: number
};

export default class Users extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      searchText: '',
      searchResult: [],
      searchResultIndex: 10
    }
    this.getUsers = this.getUsers.bind(this);
    this.onSearch = this.onSearch.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentWillUnmount() {};
  
  getUsers = (homeId: string) => {
    let users = '';
    if((this.props.users.isUpToDate && !this.props.users.isError && isNN(this.props.users.data)) && (this.props.userHomes.isUpToDate && !this.props.userHomes.isError && isNN(this.props.userHomes.data))){
      let userIds = [] as Array<string>;
      this.props.userHomes.data.filter(uh => uh.homeId === homeId).map((uh: IUserHome, i: number) => userIds.push(uh.userId));
      this.props.users.data.filter(d => userIds.indexOf(d._id) >= 0).map((u: IMember, i: number) => users += (users.length > 0 ? ', ' : '') + u.name);
    }
    return users;
  };

  onSearch = () => {
    if(isNN(this.state.searchText) && this.props.homes.isUpToDate && !this.props.homes.isError && isNN(this.props.homes.data)){
      let searchText = this.state.searchText.toLocaleLowerCase();
      this.setState({
        searchResult: this.props.homes.data.filter(d => (d.number + '').toLocaleLowerCase().indexOf(searchText) >= 0),
        searchResultIndex: 10
      });
    }else{
      this.setState({
        searchResult: [],
        searchResultIndex: 10
      });
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.user, this.props.homes, (result: boolean) => {
              this.setState({
                isLoading: false
              });
              if(result === true){
                this.onSearch();
                Toast.success('Home removed successfully.');
              }else{
                Toast.fail('Failed to remove home.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
      return(
        <main style={styles.content}>
          <Breadcrum data={[{title: 'Homes', link: 'homes'}]} />
          <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
            <Text className='fr pointer' onClick={() => this.props.history.push('edit-home/0')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
          </View>
          <View style={{...modelStyles.row, marginTop: 0, paddingTop: 0}}>
            <TextInput type='text' placeholder="Search Home" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele, display: 'inline', width: 'calc(100% - 40px)', marginRight: 10}} value={this.state.searchText} onChange={(event: any) => this.setState({searchText: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSearch);
            }} />
            <TouchableHighlight style={{height: 30, marginTop: 13}} onClick={this.onSearch}>
                <Image className='pointer' src={Images.search} style={{width: 30, height: 30}} />
            </TouchableHighlight> 
          </View>

          {isNN(this.state.searchResult) ?
          this.state.searchResult.slice(0, this.state.searchResultIndex).map((d: IHome, i: number) => {
            return(
              <View key={'home' + i} style={styles.row}>
                {/* <View style={styles.ele}>
                  <Text>{'Name: ' + (isNN(d.name) ? d.name : '')}</Text>                  
                </View> */}
                <View style={styles.ele}>
                  <Text>{'Number: ' + d.number}</Text>         
                </View>
                {/* <View style={styles.ele}>
                  <Text>{'Address: ' + (isNN(d.address) ? d.address : '')}</Text>         
                </View>
                <View style={styles.ele}>
                  <Text>{'User(s): ' + this.getUsers(d._id)}</Text>         
                </View> */}
                <View style={styles.ele}>
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.props.history.push('view-home/' + d._id);
                    // this.props.navigation.navigate('ViewHome', {id: d._id});
                  }}>
                    <Image src={Images.view} style={iconStyles.editIcon} />
                  </TouchableHighlight>
                  <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                    this.props.history.push('edit-home/' + d._id);
                    // this.props.navigation.navigate('EditHome', {id: d._id});
                    // this.setState({
                    //   isEdit: true,
                    //   id: d.id
                    // });
                  }}>
                    <Image src={Images.edit} style={iconStyles.editIcon} />
                  </TouchableHighlight>
                    <TouchableHighlight style={{}} onClick={() => {
                      this.onRemove(d._id);
                    }}>
                      <Image src={Images.delete} style={iconStyles.deleteIcon} />
                    </TouchableHighlight>
                </View>
              </View>
            )
          })
          :
          <Text style={{padding: 20}}>No home match with your search!!!</Text>
        }
        {(this.state.searchResult.length > this.state.searchResultIndex) &&
        <View style={styles.row}>
          <View style={{flex: 1, flexDirection: 'row', padding: 10}}>
            <Text style={{color: COLOR.SECONDARY, fontWeight: 'bold'}} onClick={() => 
            this.setState({
              searchResultIndex: this.state.searchResultIndex + 10
            })}>Show More</Text>
          </View>
        </View>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
      );
  }
};