import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text, TextInput, TextArea } from '../../common/elements';
import Spinner from '../../common/spinner';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { INotification } from '../../../models/notification';
import { styles } from "../../../styles/page";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { isNN, isNNObject, onEnter } from '../../../util/index';
import { toDateString } from '../../../util/convert';
import Toast from  '../../../util/toast';

interface IProps {
  user: IUser,
  onLoad: (appClientId: string, sCallback: (result: Array<INotification>) => void, fCallback: (message: string) => void) => void,
  onPush: (appClientId: string, data: INotification, user: IUser, callback: (result: INotification | null) => void) => void
};

interface IState{
  isLoading: boolean,
  data: Array<INotification>,
  title: string,
  body: string
};

export default class Notification extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      data: [],
      title: '',
      body: ''
    }
    this.onPush = this.onPush.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, (result: Array<INotification>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };
  
  componentWillUnmount(){};

  onPush = () => {
    if(isNN(this.state.title) && isNN(this.state.body)){
      confirmAlert({
        title: 'Confirm to send push notification',
        message: 'Are you sure? This notification will send to all users using this app in your Appartment. Are you sure you want to Push?',
        buttons: [
          {
            label: 'Yes',
            onClick: () => {
              this.setState({
                isLoading: true
              });
              let notification = {
                title: this.state.title,
                body: this.state.body
              } as INotification;
              this.props.onPush(this.props.user.client._id, notification, this.props.user, (result: INotification | null) => {
                this.setState({
                  isLoading: false,
                });
                if(isNNObject(result)){
                  let data = this.state.data;
                  data.unshift(result as INotification);
                  this.setState({
                    data: data,
                    title: '',
                    body: ''
                  });
                  Toast.success('Notification pushed successfully.');
                }else{
                  Toast.fail('Failed to push Notification.');
                }
              });
            }
          },
          {
            label: 'No',
            onClick: () => {}
          }
        ]
      });
    }else{
      Toast.warn('Please fill all the required fields before Push.');
    }
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Notification', link: 'notification'}]} />
        <View style={modelStyles.row}>
          <TextInput type='text' placeholder="Title" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.title} onChange={(event: any) => this.setState({title: event.target.value})} />
        </View>
        <View style={modelStyles.row}>
          <TextArea placeholder="Body" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.body} onChange={(event: any) => this.setState({body: event.target.value})}
          onKeyPress={(e) => {
            onEnter(e, this.onPush);
          }} />
        </View>
        <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
            <Button
            style={{marginRight: 10}}
            title="PUSH"
            onPress={this.onPush}
            />
            <Button
            title="CLEAR"
            onPress={() => {
              this.setState({
                title: '',
                body: ''
              });
            }}
            />
        </View>
      {(isNN(this.state.data)) ?
        this.state.data.map((d: INotification, i: number) => {
          return(
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
              </View>
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.title}</Text>
              </View>
              <View style={styles.ele}>
                <Text>{d.body}</Text>
              </View>
              <View style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                <Button title="Duplicate" onPress={() => {
                  this.setState({
                    title: d.title,
                    body: d.body
                  });
                }} />
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No Nofification found!!!</Text>
      }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};