import { actionTypes } from '../action-types';
import { IAds } from '../../models/store/ads';
import { IUser } from '../../models/user';
import { IAd } from '../../models/ad';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'ad';

export const loadAll = (appClientId: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getAdResult([], false, false, ''));
  new Api().get(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      dispatch(getAdResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const save = (appClientId: string, data: IAd, user: IUser, ads: Array<IAd>, callback: (result: boolean) => void) => (dispatch: any) => {  
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        let foundIndex = ads.findIndex(d => d._id === result.data);
        ads[foundIndex]._id = result.data;
        ads[foundIndex].title = data.title;
        ads[foundIndex].description = data.description;
        dispatch(getAdResult(ads, true, false, ''));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }else{
    new Api().post(controller + '/' + appClientId, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
          ads.unshift({
            _id: result.data,
            title: data.title,
            description: data.description,
            createdBy: user.name,
            createdAt: new Date()
          } as IAd);
        dispatch(getAdResult(ads, true, false, ''));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }
};

export const remove = (id: string, ads: Array<IAd>, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      ads = ads.filter((d, i) => d._id !== id);
      dispatch(getAdResult(ads, true, false, ''));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getAdResult = (data: Array<IAd>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_ADS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IAds
});