import { actionTypes } from '../action-types';
import { ITickets } from '../../models/store/tickets';

const createEmptyResult = (): ITickets => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const Tickets = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_TICKETS:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: ITickets, payload: ITickets): ITickets => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
