import { IVisitor } from '../../models/home/visitor';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';
// import { FILE_BASE_PATH } from '../../constants/index';
// import { IHome } from '../../models/home';

let controller: string = 'visitor';

export const loadAll = (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's' + (isNN(homeId) ? '/' + homeId : '') + '/' + isActive, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback((result && isNN(result.message)) ? result.message : 'Failed to load Visitors.');
    }
  });
};

export const save = (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  // data.image = FILE_BASE_PATH;
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if (isNNObject(result) && isNNObject(result.data) && result.data._id.length > 0) {
        /*
        if ((',' + user.type + ',').indexOf(',' + ROLE.USER + ',')>= 0) {
          let visitors = user.homes[appUser.homeIndex].visitors;
          var foundIndex = visitors.findIndex(d => d._id === result.data);
          visitors[foundIndex]._id = result.data;
          visitors[foundIndex].name = name;
          visitors[foundIndex].age = data.age;
          visitors[foundIndex].gender = data.gender;
          visitors[foundIndex].mobileNo = data.mobileNo;
          visitors[foundIndex].emailId = data.emailId;
          visitors[foundIndex].image = result.image;
          user.homes[appUser.homeIndex].visitors = visitors;
          dispatch(getUserResult(user as IUser));
        }
        */
        sCallback(result.data as IVisitor);
      } else {
        fCallback((result && isNN(result.message)) ? result.message : 'Failed to load Visitors.');
      }
    }, data);
  }else{
    new Api().post(controller + '/' + homeId, (result: any, message: string) => {
      if (isNNObject(result) && isNNObject(result.data) && result.data._id.length > 0) {
        /*
        if ((',' + user.type + ',').indexOf(',' + ROLE.USER + ',')>= 0) {
          let visitors = user.homes[appUser.homeIndex].visitors;
          data._id = result.data;
          data.createdBy = user.name;
          visitors.unshift(data);
          user.homes[appUser.homeIndex].visitors = visitors;
          dispatch(getUserResult(user as IUser));
        }
        */
        sCallback(result.data as IVisitor);
      } else {
        fCallback((result && isNN(result.message)) ? result.message : 'Failed to load Visitors.');
      }
    }, data);
  }
};

export const remove = (id: string, user: IUser, homeIndex: number, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data) && result.data === true) {
      /*
      user.homes[homeIndex].visitors = user.homes[homeIndex].visitors.filter((d, i) => d._id != id);
      dispatch(getUserResult(user as IUser));
      */
      callback(true);
    } else {
      callback(false);
    }
  });
};

