import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { loadAll, approve, reject, save, search } from '../store/actions/visitor';
import { upload as fileUpload } from '../store/actions/file';
import { IUser } from '../models/user';
import { IAppUser } from '../models/app-user';
import { IVisitor } from '../models/home';

import Page from '../components/home/visitors';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser,
    homes: state.Homes,
    gateNo: state.GateNo
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) =>  dispatch(loadAll(appClientId, homeId, isActive, sCallback, fCallback)),
    onApprove: (id: string, callback: (result: boolean) => void) => dispatch(approve(id, callback)),
    onReject: (id: string, callback: (result: boolean) => void) => dispatch(reject(id, callback)),
    onSave: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => dispatch(save(homeId, data, user, appUser, sCallback, fCallback)),
    onUpload: (data: any, callback: (result: boolean, message: string) => void) => dispatch(fileUpload(data, callback)),
    onSearch: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => dispatch(search(searchText, sCallback, fCallback))
});

export const Visitors = connect(mapStateToProps, mapDispatchToProps)(Page);