import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../../common/elements';
import Spinner from '../../common/spinner';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IServant, ITakeaway } from '../../../models/home/servant';
import { styles } from "../../../styles/page";
import {styles as commentsStyles} from '../../../styles/comments';
import Toast from  '../../../util/toast';
import { isNN } from '../../../util';
import { toDateTimeString } from '../../../util/convert';
import { ROLE } from '../../../constants';

interface IProps {
  user: IUser,
  appUser: IAppUser,
  gateNo: string,
  onLoad: (appClientId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => void,
  onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IServant>,
  isLoading: boolean
};

export default class Takeaways extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isLoading: false
    }
    this.onExit = this.onExit.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, (result: Array<IServant>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };
  
  componentWillUnmount(){};

  onExit = (id: string, servantId: string) => {
    confirmAlert({
      title: 'Confirm to Exit Servant with Takeaway',
      message: 'Are you sure you wish to exit the servant with this takeaway things?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onExit(id, this.props.gateNo, (result: boolean) => {
              if(result === true){
                var foundIndex = this.state.data.findIndex(d => d._id === servantId);
                this.state.data[foundIndex].takeaways = this.state.data[foundIndex].takeaways.filter(d => d._id !== id);
                this.setState({
                  isLoading: false
                });
                Toast.success('Servant exit with takeaway successfully.');
              }else{
                this.setState({
                  isLoading: false,
                });
                Toast.fail('Failed to exit servant with takeaway.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Takeaways', link: 'all-takeaways'}]} />
        {(isNN(this.state.data) && isNN(this.state.data.filter(d => d.takeaways && d.takeaways.length > 0))) ?
            this.state.data.filter(d => d.takeaways && d.takeaways.length > 0).map((d: IServant, i: number) => {
            return(
              <View key={i} style={styles.row}>
                <View style={styles.ele}>
                  <Text className='bold'>{d.createdBy + ' on ' + toDateTimeString(d.createdAt)}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.name}</Text>
                </View>
                <View style={styles.ele}>
                  <Text>{d.age + ', ' + (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other'))}</Text>
                </View>
                {isNN(d.mobileNo) &&
                <View style={styles.ele}>
                  <Text>{d.mobileNo}</Text>
                </View>
                }
                {isNN(d.emailId) &&
                <View style={styles.ele}>
                  <Text>{d.emailId}</Text>
                </View>
                }
                <View style={commentsStyles.cmntsCnr}>
                  <View style={commentsStyles.cmntsHdr}>
                    <Text className='bold' style={commentsStyles.cmntsHdrTxt}>Takeaways</Text>
                  </View>
                  <View style={commentsStyles.cmntsCnt}>
                    {isNN(d.takeaways) ?
                      d.takeaways.map((e: ITakeaway, j: number) => {
                        return(
                        <View key={'tkycmt' + j} style={j === 0 ? commentsStyles.cmntCnrFst : commentsStyles.cmntCnr}>
                          <View style={commentsStyles.cmntHdr}>
                            <Text className='bold'>{e.createdBy + ' on ' + toDateTimeString(e.updatedAt)}</Text>
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            <Text>{e.title}</Text>
                          </View>
                          <View style={commentsStyles.cmntCnt}>
                            <Text>{e.description}</Text>
                          </View>
                          {this.props.appUser.role === ROLE.GATEKEEPER &&
                          <View style={{...commentsStyles.cmntCnt, marginTop: 10}}>
                            {!e.isExit && <Button title='Exit' onPress={() => { this.onExit(e._id, d._id) }} />}
                          </View>
                          }
                        </View>
                        )
                      })
                      :
                      <Text style={{padding: 20}}>No takeaways found!!!</Text>
                    }
                  </View>
                </View>
              </View>
              );
          })
          :
          <Text>No data found!!!</Text>
        }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};