import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { loadWFAUsers, reject } from '../store/actions/member';
import { IMember } from '../models/home';

import Page from '../components/home/manager/approve-users';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => dispatch(loadWFAUsers(sCallback, fCallback)),
    onReject: (id: string, callback: (result: boolean) => void) => dispatch(reject(id, callback))
});

export const ApproveUsers = connect(mapStateToProps, mapDispatchToProps)(Page);