import { actionTypes } from '../action-types';
import { ITickets } from '../../models/store/tickets';
import { ITicket } from '../../models/home/ticket';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import Api from '../api/api';
import { isNN, isNNObject } from '../../util/index';
import { ITicketComment } from '../../models/home/ticket-comment';

let controller: string = 'ticket';

export const loadAll = (appClientId: string, homeId: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getTicketsResult([], false, false, ''));
  new Api().get(controller + 's' + (isNN(homeId) ? '/' + homeId : ''), (result: any, message: string) => {
    if (isNNObject(result)) {
      dispatch(getTicketsResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const loadAllByHome = (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Tickets.');
    }
  });
};

export const save = (data: ITicket, user: IUser, homeIndex: number, tickets: ITickets, callback: (result: boolean) => void) => (dispatch: any) => {  
  // update
  if(data && data._id && data._id.length > 0){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        // if(((',' + user.type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
          // if(((',' + user.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0)) {
          if(isNN(tickets.data)) {
            let arrTickets = [] as Array<ITicket>;
            arrTickets = tickets.data;
            var foundIndex = arrTickets.findIndex(d => d._id === result.data);
            arrTickets[foundIndex]._id = result.data;
            arrTickets[foundIndex].title = data.title;
            arrTickets[foundIndex].description = data.description;
            arrTickets[foundIndex].status = data.status;
            arrTickets[foundIndex].updatedBy = user.name;
            dispatch(getTicketsResult(arrTickets, true, false, ''));
          }
        // }
        /*
        if((',' + user.role + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
          let arrTickets = user.homes[homeIndex].tickets;
          var foundIndex = arrTickets.findIndex(d => d._id === result.data);
          arrTickets[foundIndex]._id = result.data;
          arrTickets[foundIndex].title = data.title;
          arrTickets[foundIndex].description = data.description;
          arrTickets[foundIndex].status = data.status;
          arrTickets[foundIndex].updatedBy = user.name;
          user.homes[homeIndex].tickets = arrTickets;
          dispatch(getUserResult(user as IUser));
        }
        */
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }else{
    new Api().post(controller + '/' + user.homes[homeIndex]._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        // if(((',' + user.type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
          // if(((',' + user.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0)) {
          let arrTickets = [] as Array<ITicket>;
          if(isNN(tickets.data)) {
            arrTickets = tickets.data;
            arrTickets.unshift({
              _id: result.data,
              _homeId: user.homes[homeIndex].number,
              title: data.title,
              description: data.description,
              status: data.status,
              createdBy: user.name,
              updatedBy: '',
              createdAt: new Date(),
              comments: []
            } as ITicket);
            dispatch(getTicketsResult(arrTickets, true, false, ''));
          }
        // }
        /*
        if((',' + user.role + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
          let arrTickets = user.homes[homeIndex].tickets;
            arrTickets.unshift({
              _id: result.data,
              title: data.title,
              description: data.description,
              status: data.status,
              createdBy: user.name,
              updatedBy: '',
              createdAt: new Date(),
              comments: []
            } as ITicket);
          user.homes[homeIndex].tickets = arrTickets;
          dispatch(getUserResult(user as IUser));
        }
        */
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }
};

export const remove = (id: string, user: IUser, homeIndex: number, tickets: ITickets, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      // if(((',' + user.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
        if(isNN(tickets.data)) {
          tickets.data = tickets.data.filter((d, i) => d._id !== id);
          dispatch(getTicketsResult(tickets.data, true, false, ''));
        }
      // }
      // if((',' + user.role + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
      //   user.homes[homeIndex].tickets = user.homes[homeIndex].tickets.filter((d, i) => d._id !== id);
      //   dispatch(getUserResult(user as IUser));
      // }
      callback(true);
    }else{
      callback(false);
    }
  });
};

