export const start = (id: string, user: IUser, homeIndex: number, tickets: ITickets, callback: (result: boolean) => void) => (dispatch: any) => {
  let newStatus = 'IN-PROGRESS';
  new Api().put(controller + '/start/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      // if(((',' + user.role + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.role + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
        if(isNN(tickets.data)) {
          let arrTickets = [] as Array<ITicket>;
          arrTickets = tickets.data;
          var foundIndex = arrTickets.findIndex(d => d._id === id);
          arrTickets[foundIndex].status = newStatus;
          dispatch(getTicketsResult(arrTickets, true, false, ''));
        }
      /*
      }
      if((',' + user.role + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
        let arrTickets = user.homes[homeIndex].tickets;
        var foundIndex = arrTickets.findIndex(d => d._id === id);
        arrTickets[foundIndex].status = newStatus;
        user.homes[homeIndex].tickets = arrTickets;
        dispatch(getUserResult(user as IUser));
      }
      */
      callback(true);
    }else{
      callback(false);
    }
  },{
    status: newStatus
  });
};

export const addComment = (ticketId: string, comment: ITicketComment, appUser: IAppUser, user: IUser, tickets: ITickets, callback: (result: boolean) => void) => (dispatch: any) => {
  if(isNN(comment._id)){
    new Api().put(controller + '/comment/' + comment._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        comment.createdAt = new Date();
        // if(((',' + user.type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
        // if((appUser.role === ROLE.ADMIN || appUser.role === ROLE.MANAGER) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
          let arrTickets = [] as Array<ITicket>;
          if(isNN(tickets.data)) {
            arrTickets = tickets.data;
            var ticketIndex = arrTickets.findIndex(d => d._id === ticketId);
            var commentIndex = arrTickets[ticketIndex].comments.findIndex(d => d._id === comment._id);
            // arrTickets[ticketIndex].comments[commentIndex] = result.data;
            arrTickets[ticketIndex].comments[commentIndex].comment = comment.comment;
            arrTickets[ticketIndex].comments[commentIndex].updatedBy = comment.updatedBy;
            arrTickets[ticketIndex].comments[commentIndex].updatedAt = comment.updatedAt;
            dispatch(getTicketsResult(arrTickets, true, false, ''));
          }
        /*
        }
        // if((',' + user.type + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
        else if(appUser.role === ROLE.USER) {
          var ticketIndex = user.homes[appUser.homeIndex].tickets.findIndex(d => d._id === ticketId);
          var commentIndex = user.homes[appUser.homeIndex].tickets[ticketIndex].comments.findIndex(d => d._id === comment._id);
          user.homes[appUser.homeIndex].tickets[ticketIndex].comments[commentIndex] = comment;
          dispatch(getUserResult(user as IUser));
        }
        */
        callback(true);
      }else{
        callback(false);
      }
    }, comment);
  }else{
    new Api().post(controller + '/comment/' + ticketId, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        comment._id = result.data;
        comment.createdAt = new Date();
        // if(((',' + user.type + ',').indexOf(',' + ROLE.ADMIN + ',') >= 0 || (',' + user.type + ',').indexOf(',' + ROLE.MANAGER + ',') >= 0) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
          // if((appUser.role === ROLE.ADMIN || appUser.role === ROLE.MANAGER) && isNNObject(tickets) && tickets.isUpToDate && !tickets.isError) {
          let arrTickets = [] as Array<ITicket>;
          if(isNN(tickets.data)) {
            arrTickets = tickets.data;
            var ticketIndex = arrTickets.findIndex(d => d._id === ticketId);
              // if(!isNN(user.home[homeIndex].ticket[ticketIndex].ticketComment)){
              //   user.home[homeIndex].ticket[ticketIndex].ticketComment = [];
              // }
              arrTickets[ticketIndex].comments.unshift(comment);
            dispatch(getTicketsResult(arrTickets, true, false, ''));
          }
        /*
        }
        // if((',' + user.type + ',').indexOf(',' + ROLE.USER + ',') >= 0) {
        else if(appUser.role === ROLE.USER) {
          var ticketIndex = user.homes[appUser.homeIndex].tickets.findIndex(d => d._id === ticketId);
            // if(!isNN(user.home[homeIndex].ticket[ticketIndex].ticketComment)){
            //   user.home[homeIndex].ticket[ticketIndex].ticketComment = [];
            // }
            user.homes[appUser.homeIndex].tickets[ticketIndex].comments.unshift(comment);
          dispatch(getUserResult(user as IUser));
        }
        */
        callback(true);
      }else{
        callback(false);
      }
    }, comment);
  }
};

export const removeComment = (id: string, ticketId: string, user: IUser, homeIndex: number, tickets: ITickets, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/comment/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
        if(isNN(tickets.data)) {
          var foundIndex = tickets.data.findIndex(d => d._id === ticketId);
          tickets.data[foundIndex].comments = tickets.data[foundIndex].comments.filter((d, i) => d._id !== id);
          dispatch(getTicketsResult(tickets.data, true, false, ''));
        }
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getTicketsResult = (data: Array<ITicket>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_TICKETS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as ITickets
});