import * as React from 'react';
import { View, TextInput } from '../../common/elements';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IUser } from '../../../models/user/user';
import { styles } from "../../../styles/page";
import { styles as modelStyles } from "../../../styles/model";
import {styles as commonStyles} from '../../../styles/common';
import { COLOR } from '../../../constants';
import { isNN, onEnter } from '../../../util/index';
import { IAppUser } from '../../../models/app-user';
import { IBlocks } from '../../../models/store/blocks';
import { IBlock } from '../../../models/block';
import { IHomes } from '../../../models/store/homes';
import { IHome } from '../../../models/home';
import Toast from  '../../../util/toast';

interface IProps {
  match: any,
  appUser: IAppUser,
  user: IUser,
  blocks: IBlocks,
  homes: IHomes,
  onSave: (appClientId: string, data: IBlock, user: IUser, blocks: Array<IBlock>, homes: Array<IHome>, callback: (result: boolean, message: string) => void) => void
};

interface IState{
  isLoading: boolean,
  id: string,
	name: string,
  description: string,
  homeCount: number
};

export default class EditBlock extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      id: '',
      name: '',
      description: '',
      homeCount: 0
    }
    this.onSave = this.onSave.bind(this);
  };

  componentDidMount() {
    let blockId = this.props.match.params.id; // this.props.navigation.state.params.id;
    if(blockId.length > 0 && blockId !== '0' && this.props.blocks && isNN(this.props.blocks.data)){
      let block = this.props.blocks.data.filter((d, i) => {
        return d._id === blockId
      });
      if(isNN(block)){
        this.setState({
          id: block[0]._id,
          name: block[0].name,
          description: block[0].description
        });
      }
    }
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.name) && (isNN(this.state.id) || this.state.homeCount > 0)){
      if(!isNN(this.props.blocks.data) || !isNN(this.props.blocks.data.filter(b => b.name === this.state.name))){
        this.setState({
          isLoading: true
        });
        let block = {
          _id: this.state.id,
          name: this.state.name,
          description: this.state.description
        } as IBlock;
        block = Object.assign(block, {
          homeCount: this.state.homeCount
        });
        this.props.onSave(this.props.user.client._id, block, this.props.user, (this.props.blocks && this.props.blocks.data) ? this.props.blocks.data : [], (this.props.homes && this.props.homes.data) ? this.props.homes.data : [], (result: boolean, message: string) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              id: '',
              name: '',
              description: '',
              homeCount: 0
            });
            Toast.success(isNN(this.state.id) ? 'Block saved successfully.' : 'Block saved and home(s) created successfully.');
          }else{
            Toast.fail(isNN(message) ? message : 'Failed to save block.');
          }
        });
      }else{
        Toast.fail('Block with same name already exists. Please give different name.');
      }
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Blocks', link: 'blocks'}, {title: 'Edit Block', link: 'edit-block/0'}]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Name" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.name} onChange={(event: any) => this.setState({name: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Description" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSave);
            }} />
          </View>
          {!isNN(this.state.id) &&
          <View style={modelStyles.row}>
            <TextInput type='number' placeholder="Number of houses in this block" className='form-control' style={{...commonStyles.textInput, ...modelStyles.ele}} value={this.state.homeCount > 0 ? this.state.homeCount + '' : ''} onChange={(event: any) => this.setState({homeCount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
          </View>
          }
          <View style={modelStyles.saveBtn}>
            <Button
            title="SAVE"
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};