import * as React from 'react';
import { View, TextInput, TextArea } from '../../common/elements';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import Breadcrum from '../../common/breadcrum';
import { IAds } from '../../../models/store/ads';
import { IUser } from '../../../models/user/user';
import { IAd } from '../../../models/ad';
import { styles } from "../../../styles/page";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import { COLOR } from '../../../constants';
import { isNN, onEnter } from '../../../util/index';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  ads: IAds,
  onSave: (appClientId: string, data: IAd, user: IUser, ads: Array<IAd>, callback: (result: boolean) => void) => void,
};

interface IState{
  isLoading: boolean,
  id: string,
  title: string,
  description: string
};

export default class NewAd extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      id: '',
      title: '',
      description: ''
    }
    this.onSave = this.onSave.bind(this);
  };

  componentWillUnmount() {};
  
  onSave = () => {
    if(isNN(this.state.title) && isNN(this.state.description)){
        this.setState({
          isLoading: true
        });
        let ad = {
          _id: this.state.id,
          title: this.state.title,
          description: this.state.description
        } as IAd;
        this.props.onSave(this.props.user.client._id, ad, this.props.user, this.props.ads.data, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              id: '',
              title: '',
              description: ''
            });
            Toast.success('Ad saved successfully.');
          }else{
            Toast.fail('Failed to save ad.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  render() {
    return(
      <main style={{...styles.content, flex: 1, backgroundColor: COLOR.INVERSE}}>
        <Breadcrum data={[{title: 'Ads', link: 'ads'}, {title: 'New Ad', link: 'new-ad'}]} />
        <View style={modelStyles.container}>
          <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Title" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.title} onChange={(event: any) => this.setState({title: event.target.value})} />
          </View>
          <View style={modelStyles.row}>
            <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})}
            onKeyPress={(e) => {
              onEnter(e, this.onSave);
            }} />
          </View>
          <View className='center' style={modelStyles.saveBtn}>
            <Button
            title="SAVE"
            onPress={this.onSave}
            />
          </View>
        </View>
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};