import * as React from 'react';
import { Link } from 'react-router-dom';
import { Image } from '../common/elements';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { isNN } from '../../util/index';
import { NAV } from '../../constants/nav';
import { COLOR } from '../../constants';
import Images from '../images';

interface IProps {
  appUser: IAppUser,
  user: IUser
};

interface IState{
  isExpand: boolean,
  activeMenu: string,
  activeTopMenu: string
};

export default class NavBar extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isExpand: false,
      activeMenu: 'Home',
      activeTopMenu: ''
    };
    this.loadNav = this.loadNav.bind(this);
    this.onToggleMenu = this.onToggleMenu.bind(this);
    this.getImage = this.getImage.bind(this);
  }
  
  componentWillUnmount() {};
  
  getImage = (img: string) => {
    switch(img){
      case 'homeIconWhite':
        return Images.homeIconWhite;
      case 'visitorsWhite':
        return Images.visitorsWhite;
      case 'notificationWhite':
        return Images.notificationWhite;
      case 'settingWhite':
        return Images.settingWhite;
      case 'infoWhite':
        return Images.infoWhite;
      default:
        return '';
    }
  };

  loadNav = (nav: any, pTitle: string, isTopLevel: boolean = false) => {
    let that = this;
    return(
      <ul className={'navbar-nav' + ((!isTopLevel && this.state.activeTopMenu !== pTitle) ? ' hide' : '')} style={!isTopLevel ? styles.inner : {}}>
      {
        nav.map((d: any, i: number) => {
          if((isNN(d.path) || isNN(d.child)) && (!isNN(d.roles) || d.roles.indexOf(this.props.appUser.role) >= 0) && (!d.isMRole || this.props.user.role.indexOf(',') > 0)){
          return(
            <li key={'nav-item' + i } className='nav-item' title={d.title}>
              <Link className={'nav-link' + (this.state.activeMenu === d.title || (d.child && isNN(d.child.filter((c: any) => c.title === this.state.activeMenu))) ? ' active' : '')} to={isNN(d.path) ? d.path : '#' } onClick={() => {
                if(this.state.activeTopMenu === d.title){
                  this.setState({
                    activeTopMenu: ''
                  });
                }else if(isTopLevel){
                  this.setState({
                    activeTopMenu: d.title
                  });
                  if(isNN(d.child)){
                    this.setState({
                      isExpand: true
                    });
                  }
                }
                if(isNN(d.path)){
                  this.setState({
                    activeMenu: d.title,
                    activeTopMenu: '',
                    isExpand: false
                  });
                }
              }}>
                {(!this.state.isExpand && isNN(d.image)) ?
                <Image src={this.getImage(d.image)} className='' style={{ width: 26, height: 22}} />
                :
                <span>{d.title}</span>
                }
                </Link>
              {
                isNN(d.child) && 
                that.loadNav(d.child, d.title)
              }
            </li>
          )
          }else{
            return null;
          }
        })
      }
      </ul>
    );
  };

  onToggleMenu = () => {
    this.setState({
      isExpand: !this.state.isExpand
    });
  };

  render() {
    return (
      <nav style={{...styles.nav, width: (this.state.isExpand ? '250px' : '44px')}}>
        {
          this.loadNav(NAV, '', true)
        }
      </nav>
    );
  }
};

const styles = {
  nav: {
    backgroundColor: COLOR.SECONDARY,
    color: COLOR.INVERSE
  },
  inner:{
    backgroundColor: COLOR.PRIMARY,
    paddingLeft: 20
  }
};