import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../../common/elements';
import Button from '../../common/button';
import Breadcrum from '../../common/breadcrum';
import Spinner from '../../common/spinner';
import { IMember } from '../../../models/home/member';
import { styles } from "../../../styles/page";
import { styles as pageStyles } from "../../../styles/page";
import Toast from  '../../../util/toast';
import { isNN } from '../../../util';
import { toDateTimeString } from '../../../util/convert';

interface IProps {
  history: any,
  onLoad: (sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => void,
  onReject: (id: string, callback: (result: boolean) => void) => void
};

interface IState{
  data: Array<IMember>,
  isLoading: boolean
};

export default class ApproveUsers extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      data: [],
      isLoading: false
    }
    this.onApprove = this.onApprove.bind(this);
    this.onReject = this.onReject.bind(this);
  };

  componentWillUnmount() {};
  
  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad((result: Array<IMember>) => {
      this.setState({
        isLoading: false,
        data: result
      });
    }, (message: string) => {
      this.setState({
        isLoading: false
      });
      Toast.fail(message);
    });
  };

  onApprove = (id: string) => {
    confirmAlert({
      title: 'Confirm to Approve',
      message: 'You want to Approve the user?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.props.history.push('edit-user/' + id + '/true');
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  onReject = (id: string) => {
    confirmAlert({
      title: 'Confirm to Reject',
      message: 'You want to Reject the user?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onReject(id, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                this.setState({
                  data: this.state.data.filter(v => v._id !== id)
                });
                Toast.success('User rejected successfully.');
              }else{
                Toast.fail('Failed to reject the user.');
              }
            }); 
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Users', link: 'users'}, {title: 'Users Waiting for Approval', link: 'approve-users'}]} />
        {isNN(this.state.data) ?
        this.state.data.map((d: IMember, i: number) => {
          return(
            <View key={i} style={pageStyles.row}>
              <View style={pageStyles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.name}</Text>
              </View>
              {(d.age > 0 || isNN(d.gender)) && 
              <View style={pageStyles.ele}>
                <Text>{(d.age > 0 ? d.age : '') + ((d.age > 0 && isNN(d.gender)) ? ', ' : '') + (isNN(d.gender) ? (d.gender === 'M' ? 'Male' : (d.gender === 'F' ? 'Female' : 'Other')) : '')}</Text>
              </View>
              }
              <View style={pageStyles.ele}>
                On <Text style={{fontWeight: 'bold'}}>{toDateTimeString(d.createdAt)}</Text>
              </View>
              {isNN(d.mobileNo) && 
              <View style={pageStyles.ele}>
                <Text>{d.mobileNo}</Text>
              </View>
              }
              {isNN(d.emailId) && 
                <View style={pageStyles.ele}>
                  <Text>{d.emailId}</Text>
                </View>
              }
              <View style={pageStyles.ele}>
                <Button title='Approve' style={{marginRight: '10px'}} onPress={() => { this.onApprove(d._id); }} />
                <Button title='Reject' onPress={() => { this.onReject(d._id); }} />
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>No users found!!!</Text>
      }
      <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};