import { INotification } from '../../models/notification';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';
import { IUser } from '../../models/user';

let controller: string = 'notification';

export const loadAll = (appClientId: string, sCallback: (result: Array<INotification>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Notification.');
    }
  });
};

export const push = (appClientId: string, data: INotification, user: IUser, callback: (result: INotification | null) => void) => (dispatch: any) => {  
  new Api().post(controller + '/' + appClientId, (result: any, message: string) => {
    if(isNNObject(result) && isNN(result.data))
    {
      callback({
        _id: result.data,
        title: data.title,
        body: data.body,
        createdBy: user.name,
        createdAt: new Date()
      } as INotification);
    }else{
      callback(null);
    }
  }, data);
};