import * as React from 'react';
import Modal from 'react-responsive-modal';
import { View, Text, TextInput, ScrollView, TextArea } from '../../common/elements';
import DatePicker from 'react-date-picker';
import Button from '../../common/button';
import Spinner from '../../common/spinner';
import { IAppUser } from '../../../models/app-user';
import { IUser } from '../../../models/user/user';
import { IHomes } from '../../../models/store/homes';
import { IHome, IPayment } from '../../../models/home';
import { styles as pageStyles } from "../../../styles/page";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import {styles as popupStyles} from '../../../styles/popup';
import { COLOR, MONTH } from '../../../constants';
import { toDateString } from '../../../util/convert';
import { isNN } from '../../../util';
import Toast from '../../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  homes: IHomes,
  onSearch: (appClientId: string, data: any, user: IUser, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => void,
  onUpdate: (id: string, data: any, user: IUser, callback: (result: boolean, message: string) => void) => void
};

interface IState {
  isLoading: boolean,
  isShowSearch: boolean,
  fromDate: any,
  toDate: any,
  datePickerType: string,
  datePickerDate: Date,
  homes: Array<IHome>,
  searchText: string,
  showHomes: boolean,
  data: Array<IPayment>,
  isShowUpdateModel: boolean,
  uPaymentId: string,
  uAmount: number,
  uDescription: string
};

export default class PaymentHistory extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isShowSearch: true,
      fromDate: '',
      toDate: '',
      datePickerType: '',
      datePickerDate: new Date(),
      homes: [],
      searchText: '',
      showHomes: false,
      data: [],
      isShowUpdateModel: false,
      uPaymentId: '',
      uAmount: 0,
      uDescription: ''
    };
    this.onSearch = this.onSearch.bind(this);
    this.onUpdate = this.onUpdate.bind(this);
  };

  onSearch = () => {
    if(this.state.fromDate && this.state.toDate){
      let homeIds = [] as any;
      this.state.homes.map((h,i) => homeIds.push(h._id));
      let data = {
        fromDate: this.state.fromDate,
        toDate: this.state.toDate,
        homes: homeIds
      };
      this.setState({
        isLoading: true
      });
      this.props.onSearch(this.props.user.client._id, data, this.props.user, (result: Array<IPayment>) => {
        this.setState({
          isLoading: false,
          isShowSearch: false,
          data: result
        });
      }, (message: string) => {
        this.setState({
          isLoading: false,
        });
        Toast.fail(message ? message : 'Failed to load payment.');
      });
    }else{
      Toast.warn('Please fill all required fields before Search.');
    }
  };

  onUpdate = () => {
    if(isNN(this.state.uPaymentId) && this.state.uAmount > 0){
      let data = {
        amount: this.state.uAmount,
        description: this.state.uDescription,
      };
      this.setState({
        isLoading: true
      });
      this.props.onUpdate(this.state.uPaymentId, data, this.props.user, (result: boolean, message: string) => {
        this.setState({
          isLoading: false,
        });
        if(result === true){
          // this.props.navigation.navigation.goBack(null);
          let pData = this.state.data;
          var foundIndex = pData.findIndex(d => d._id === this.state.uPaymentId);
          pData[foundIndex].amount = this.state.uAmount;
          pData[foundIndex].description = this.state.uDescription;
          this.setState({
            isShowUpdateModel: false,
            uPaymentId: '',
            uAmount: 0,
            uDescription: '',
            data: pData
          });
          Toast.success('Payment updated successfully.');
        }else{
          Toast.fail( message ? message : 'Failed to update payment.');
        }
      });
    }else{
      Toast.warn('Please fill all required fields before update.');
    }
  };

  render() {
    return(
        <View style={styles.container}>
          {this.state.isShowSearch ?
          <View>
            <View style={modelStyles.row}>
              <DatePicker
              onChange={(date : any) => {
                this.setState({
                  fromDate: date
                });
              }}
              value={this.state.fromDate}
              />
            </View>
            <View style={modelStyles.row}>
              <DatePicker
              onChange={(date : any) => {
                this.setState({
                  toDate: date
                });
              }}
              value={this.state.toDate}
              />
          </View>
            <View style={modelStyles.row}>
            <Text style={{marginRight: 15}}>{'Home(s): ' + (this.state.homes.length > 0 ? this.state.homes.length : 'All')}</Text>
          </View>
            <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
                {
                  this.state.homes.map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='pointer'
                      key={'ho' + i}
                      style={{flexDirection: 'column', marginRight: 6, color: COLOR.SECONDARY }}
                      onClick={() => {
                        this.setState({
                          homes: this.state.homes.filter((h, i) => h._id !== d._id)
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
            <View style={modelStyles.row}>
            <TextInput type='text' placeholder="Enter Home Number" className='form-control'
            style={{...modelStyles.ele,
              flexDirection: 'row',
              padding: 6,
              borderWidth: 1,
              borderColor: COLOR.HR_COLOR,
              borderRadius: 5
            }}
            value={this.state.searchText} 
            onChange={(event: any) => this.setState({showHomes: true, searchText: event.target.value})} />
          </View>
            {(this.state.showHomes && isNN(this.state.searchText) && this.props.homes && this.props.homes.data.length > 0 ) &&
            <ScrollView style={{...modelStyles.row, width: '100%', overflow: 'scroll'}}>
                {
                  this.props.homes.data.filter((d, i) => d.number.toLowerCase().indexOf(this.state.searchText.toLowerCase()) >= 0 && (this.state.homes.filter(h => h._id === d._id).length < 1)).map((d: IHome, i: number) => {
                    return(
                      <Text 
                      className='pointer'
                      key={'ho' + i}
                      style={{flexDirection: 'column', marginRight: 6}}
                      onClick={() => {
                        let homes = this.state.homes;
                        homes.push(d);
                        this.setState({
                          homes: homes
                         });
                      }}>{d.number}</Text>
                    )
                  })
                }
          </ScrollView>
            }
            <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                <Button
                title="SEARCH"
                onPress={this.onSearch}
                />
            </View>
          </View>
          :
          <View>
             <Text 
             className='fr pointer'
             style={{color: COLOR.SECONDARY, fontWeight: 'bold', textAlign: 'right'}}
             onClick={() => {
               this.setState({
                 isShowSearch: true
               });
             }}>Show Search</Text>
          </View>
          }
          <View>
            {(isNN(this.state.data)) ?
              this.state.data.map((d: IPayment, i: number) => {
                return(
                  <View key={i} style={pageStyles.row}>
                    <View style={pageStyles.ele}>
                        <Text style={{fontWeight: 'bold'}}>{d._homeId + ' by ' + d.createdBy + ' on ' + toDateString(d.createdAt)}</Text>
                    </View>
                    <View style={pageStyles.ele}>
                      <Text style={{fontWeight: 'bold'}}>{'Rs.' + d.amount + ' for ' + MONTH.filter(m => m.value === d.forMonth)[0].title  + "/" + d.forYear + (isNN(d.mode) ? ' ' + d.mode : '') + (isNN(d.type) ? ' ' + d.type : '')}</Text>
                    </View>
                    {isNN(d.description) &&
                      <View style={pageStyles.ele}>
                        <Text>{d.description}</Text>
                      </View>
                    }
                    <View style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                      <Button title="Update" onPress={() => {
                        this.setState({
                          isShowUpdateModel: true,
                          uPaymentId: d._id,
                          uAmount: d.amount,
                          uDescription: d.description
                        });
                      }} />
                    </View>
                  </View>
                )
              })
              :
              <Text style={{padding: 20}}>No Payment found!!!</Text>
            }
          </View>
          <Modal
          open={this.state.isShowUpdateModel}
          onClose={() => {
            this.setState({
              isShowUpdateModel: false,
              uPaymentId: '',
              uAmount: 0,
              uDescription: ''
            });
          }}>
          <View style={popupStyles.container}>
            <View style={popupStyles.content}>
              <View style={modelStyles.row}>
                <TextInput type='text' placeholder="Amount" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.uAmount > 0 ? this.state.uAmount + '' : ''} onChange={(event: any) => this.setState({uAmount: +(event.target.value.replace(/[^0-9]/g, ''))})} />
              </View>
              <View style={modelStyles.row}>
                <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.uDescription} onChange={(event: any) => this.setState({uDescription: event.target.value})} />
              </View>
              <View className='center' style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                  <Button
                  style={{marginRight: 10}}
                  title="SAVE"
                  onPress={this.onUpdate}
                  />
                  <Button
                  title="CLEAR"
                  onPress={() => {
                    this.setState({
                      uAmount: 0,
                      uDescription: ''
                    });
                  }}
                  />
              </View>
            </View>
          </View>
        </Modal>
          <Spinner visible={this.state.isLoading} />
        </View>
    );
  }
};

const styles = {
  container:{
    width: '100%',
    paddingLeft: 15,
    paddingRight: 15,
  }
};