import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IUser } from '../models/user';
import { IMember } from '../models/home';
import { IUsers } from '../models/store/users';
import { load, saveByAM } from '../store/actions/member';
import Page from '../components/home/manager/edit-user';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    users: state.Users,
    homes: state.Homes,
    userHomes: state.UserHomes
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (id: string, sCallback: (result: IMember, homeIds: Array<string>) => void, fCallback: (message: string) => void) => dispatch(load(id, sCallback, fCallback)),
    onSave: (data: IMember, homeIds: Array<string>, user: IUser, users: IUsers, isApprove: Boolean, callback: (result: boolean, message: string) => void) => dispatch(saveByAM(data, homeIds, user, users, isApprove, callback))
});

export const EditUser = connect(mapStateToProps, mapDispatchToProps)(Page);