import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { TouchableHighlight, View, Image, Text, TextInput, TextArea } from '../../common/elements';
import Breadcrum from '../../common/breadcrum';
import Spinner from '../../common/spinner';
import { IAds } from '../../../models/store/ads';
import { IUser } from '../../../models/user/user';
import { IAd } from '../../../models/ad';
import { styles } from "../../../styles/page";
import { styles as iconStyles } from "../../../styles/icon";
import { styles as modelStyles } from "../../../styles/model";
import {styles as CommonStyles} from '../../../styles/common';
import Images from '../../images';
import { COLOR } from '../../../constants';
import { isNN } from '../../../util/index';
import { toDateString } from '../../../util/convert';
import Toast from  '../../../util/toast';

interface IProps {
  history: any,
  user: IUser,
  ads: IAds,
  onLoad: (appClientId: string, callback: (result: boolean) => void) => void,
  onSave: (appClientId: string, data: IAd, user: IUser, ads: Array<IAd>, callback: (result: boolean) => void) => void,
  onRemove: (id: string, ads: Array<IAd>, callback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean,
  isEdit: boolean,
  id: string,
  title: string,
  description: string
};

export default class Ads extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false,
      isEdit: false,
      id: '',
      title: '',
      description: ''
    }
    this.onSave = this.onSave.bind(this);
    this.onRemove = this.onRemove.bind(this);
  };

  componentDidMount(){
    this.setState({
      isLoading: true
    });
    this.props.onLoad(this.props.user.client._id, (result: boolean) => {
      this.setState({
        isLoading: false
      });
      if(!result){
        Toast.fail('Failed to load Ads.');
      }
    });
  };
  
  componentWillUnmount(){};

  onSave = () => {
    if(isNN(this.state.title) && isNN(this.state.description)){
        this.setState({
          isLoading: true
        });
        let ad = {
          _id: this.state.id,
          title: this.state.title,
          description: this.state.description
        } as IAd;
        this.props.onSave(this.props.user.client._id, ad, this.props.user, this.props.ads.data, (result: boolean) => {
          this.setState({
            isLoading: false,
          });
          if(result === true){
            this.setState({
              isEdit: false,
              id: '',
              title: '',
              description: ''
            });
            Toast.success('Ad saved successfully.');
          }else{
            Toast.fail('Failed to save ad.');
          }
        });
    }else{
      Toast.warn('Please fill all the required fields before '+ (this.state.id.length > 0 ? 'Update' : 'Save') + '.');
    }
  };

  onRemove = (id: string) => {
    confirmAlert({
      title: 'Confirm to delete',
      message: 'Are you sure you wish to delete?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onRemove(id, this.props.ads.data, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result === true){
                Toast.success('Ad removed successfully.');
              }else{
                Toast.fail('Failed to remove ad.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <main style={styles.content}>
        <Breadcrum data={[{title: 'Ads', link: 'ads'}]} />
        <View style={{flex: 1, flexDirection: 'row', justifyContent: 'flex-end'}}>
          <Text className='fr pointer' onClick={() => this.props.history.push('new-ad')} style={{...styles.ele, color: COLOR.SECONDARY, textAlign: 'right'}}>Add New</Text>
        </View>
      {(this.props.ads.isUpToDate && !this.props.ads.isError && isNN(this.props.ads.data)) ?
        this.props.ads.data.map((d: IAd, i: number) => {
          return(
            (this.state.isEdit === true && this.state.id === d._id) ?
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
              </View>
              <View style={styles.ele}>
                <TextInput type='text' placeholder="Title" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.title} onChange={(event: any) => this.setState({title: event.target.value})} />
              </View>
              <View style={styles.ele}>
                <TextArea placeholder="Description" className='form-control' style={{...CommonStyles.textInput, ...modelStyles.ele}} value={this.state.description} onChange={(event: any) => this.setState({description: event.target.value})} />
              </View>
              <View style={styles.ele}>
                <TouchableHighlight style={{marginRight: 25}} onClick={this.onSave}>
                  <Image src={Images.tick} style={iconStyles.editIcon} />
                </TouchableHighlight>                
                <TouchableHighlight style={{}} onClick={() => {
                  this.setState({
                    isEdit: false,
                    id: '',
                    title: '',
                    description: ''
                  });
                }}>
                  <Image src={Images.cancel} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
            :
            <View key={i} style={styles.row}>
              <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{d.createdBy + " on " + toDateString(d.createdAt)}</Text>
              </View>
              <View style={styles.ele}>
                <Text>{d.title}</Text>
              </View>
              <View style={styles.ele}>
                <Text>{d.description}</Text>
              </View>
              <View style={styles.ele}>
                <TouchableHighlight style={{marginRight: 25}} onClick={() => {
                  this.setState({
                    isEdit: true,
                    id: d._id,
                    title: d.title,
                    description: d.description
                  });
                }}>
                  <Image src={Images.edit} style={iconStyles.editIcon} />
                </TouchableHighlight>                
                <TouchableHighlight style={{}} onClick={() => {
                  this.onRemove(d._id);
                }}>
                  <Image src={Images.delete} style={iconStyles.deleteIcon} />
                </TouchableHighlight>      
              </View>
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>{(this.props.ads.isUpToDate ? (this.props.ads.isError ? this.props.ads.errorMessage : "No Ad found!!!") : "Loading...")}</Text>
      }
        <Spinner visible={this.state.isLoading} />
      </main>
    );
  }
};