import { actionTypes } from '../action-types';
import { IUserHomes } from '../../models/store/user-homes';

const createEmptyResult = (): IUserHomes => ({
    data: [],
    isUpToDate: false,
    isError: false,
    errorMessage: ""
});

export const UserHomes = (state = createEmptyResult(), action: any) => {
    switch (action.type) {
        case actionTypes.LOAD_USERHOMES:
            return handleResult(state, action.payload);
        case actionTypes.RESET:
            state = createEmptyResult();
    }
    return state;
};

const handleResult = (state: IUserHomes, payload: IUserHomes): IUserHomes => {
    return {
        ...state,
        data: payload.data,
        isUpToDate: payload.isUpToDate,
        isError: payload.isError,
        errorMessage: payload.errorMessage
    };
};
