import * as React from 'react';
import { View, Image } from '../../common/elements';
import Images from '../../images';
import { IAds } from '../../../models/store/ads';
import { IUser } from '../../../models/user/user';
import { styles as cardStyles } from '../../../styles/card';
import { ROLE } from '../../../constants';
import { IAppUser } from '../../../models/app-user';
import { IUsers } from '../../../models/store/users';
import { IHomes } from '../../../models/store/homes';
import { ITickets } from '../../../models/store/tickets';
import { IBlocks } from '../../../models/store/blocks';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  ads: IAds,
  users: IUsers,
  homes: IHomes,
  tickets: ITickets
  blocks: IBlocks
};

interface IState{
};

export default class Home extends React.Component<IProps, IState> {
  // constructor(props: any) {
  //   super(props);
  // };

  componentWillUnmount() {};
  
  render() {
    return(
      <main style={styles.content}>
        <View className='fl' style={cardStyles.container}>
            <View style={cardStyles.row}>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('tickets')}>
                  <Image src={Images.support} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                    {/* {this.props.tickets.isUpToDate ?
                    ((!this.props.tickets.isError) ? this.props.tickets.data.length : 0) + " TICKET" + ((this.props.tickets.isError || this.props.tickets.data.length === 0 || this.props.tickets.data.length > 1) ? "S" : "")
                    :
                    "Loading"
                    } */}
                    TICKET
                  </View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('ads')}>
                  <Image src={Images.addvertise} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                    {/* {this.props.ads.isUpToDate ?
                    ((!this.props.ads.isError) ? this.props.ads.data.length : 0) + " AD" + ((this.props.ads.isError || this.props.ads.data.length === 0 || this.props.ads.data.length > 1) ? "S" : "")
                    :
                    "Loading"
                    } */}
                    ADS
                  </View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('notification')}>
                  <Image src={Images.reminder} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>NOTIFICATION</View>
                </View>
                {this.props.appUser.role === ROLE.ADMIN &&
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('users')}>
                  <Image src={Images.users} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                    {this.props.users.isUpToDate ?
                    ((!this.props.users.isError) ? this.props.users.data.length : 0) + " USER" + ((this.props.users.isError || this.props.users.data.length === 0 || this.props.users.data.length > 1) ? "S" : "")
                    :
                    "Loading"
                    }
                  </View>
                </View>
                }
                {this.props.appUser.role === ROLE.ADMIN &&
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('homes')}>
                  <Image src={Images.homes} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                    {this.props.homes.isUpToDate ?
                    ((!this.props.homes.isError) ? this.props.homes.data.length : 0) + " HOME" + ((this.props.homes.isError || this.props.homes.data.length === 0 || this.props.homes.data.length > 1) ? "S" : "")
                    :
                    "Loading"
                    }
                  </View>
                </View>
                }
                {this.props.appUser.role === ROLE.ADMIN &&
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('blocks')}>
                  <Image src={Images.blocks} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>
                    {this.props.blocks.isUpToDate ?
                    ((!this.props.blocks.isError) ? this.props.blocks.data.length : 0) + " BLOCK" + ((this.props.blocks.isError || this.props.blocks.data.length === 0 || this.props.blocks.data.length > 1) ? "S" : "")
                    :
                    "Loading"
                    }
                  </View>
                </View>
                }
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('all-visitors')}>
                  <Image src={Images.visitors} style={cardStyles.img} />
                  {/* <Text style={cardStyles.txt}>
                  {(this.props.home.visitors ? this.props.home.visitors.length + '' : '') + ' VISITOR' + ((this.props.home.visitors.length === 0 || this.props.home.visitors.length > 1) ? 'S' : '')}
                  </Text> */}
                  <View className='pointer' style={cardStyles.txt}>VISITOR(S)</View>
                </View>
                <View className='fl pointer' style={{...cardStyles.inner, textAlign: 'center'}} onClick={() => this.props.history.push('payment')}>
                  <Image src={Images.money} style={cardStyles.img} />
                  <View className='pointer' style={cardStyles.txt}>PAYMENT</View>
                </View>
            </View>
        </View>     
      </main>
    );
  }
};

const styles = {
  content: {
    flex: 1,
    // backgroundColor: 'transparent',
    width: '100%',
  }
};