import { actionTypes } from '../action-types';
import { IVehicle } from '../../models/home/vehicle';
import { IUser } from '../../models/user/user';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';

let controller: string = 'vehicle';

export const loadAllByHome = (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => (dispatch: any) => {
  new Api().get(controller + 's/' + homeId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      sCallback(result.data);
    } else {
      fCallback(result && isNN(result.message) ? result.message : 'Failed to load Members.');
    }
  });
};

export const save = (data: IVehicle, user: IUser, homeIndex: number, callback: (result: boolean) => void) => (dispatch: any) => {
  // update
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        let vehicles = user.homes[homeIndex].vehicles;
        var foundIndex = vehicles.findIndex(d => d._id === result.data);
        vehicles[foundIndex]._id = result.data;
        vehicles[foundIndex].number = data.number;
        vehicles[foundIndex].type = data.type;
        user.homes[homeIndex].vehicles = vehicles;
        dispatch(getUserResult(user as IUser));
        callback(true);
      }else{
        callback(false);
      }
    }, data);
  }else{ // add
  new Api().post(controller + '/' + user.homes[homeIndex]._id, (result: any, message: string) => {
    if(isNNObject(result) && isNN(result.data))
    {
      data._id = result.data;
      data.createdBy = user.name;
      let vehicles = user.homes[homeIndex].vehicles;
      vehicles.push(data);
      user.homes[homeIndex].vehicles = vehicles;
      dispatch(getUserResult(user as IUser));
      callback(true);
    }else{
      callback(false);
    }
  }, data);
  }
};

export const remove = (id: string, user: IUser, homeIndex: number, callback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      user.homes[homeIndex].vehicles = user.homes[homeIndex].vehicles.filter((d, i) => d._id !== id);
      dispatch(getUserResult(user as IUser));
      callback(true);
    }else{
      callback(false);
    }
  });
};

const getUserResult = (data: IUser) => ({
  type: actionTypes.LOAD_USER,
  payload: data
});