import * as React from 'react';
import { confirmAlert } from 'react-confirm-alert';
import { View, Text } from '../common/elements';
import Spinner from '../common/spinner';
import Button from '../common/button';
import { IAppUser } from '../../models/app-user';
import { IUser } from '../../models/user/user';
import { IOutings } from '../../models/store/outings';
import { IOuting } from '../../models/outing';
import { styles } from "../../styles/page";
import { styles as modelStyles } from "../../styles/model";
import { isNN, isNNObject } from '../../util/index';
import { toDateTimeString } from '../../util/convert';
import { ROLE, COLOR } from '../../constants';
import Toast from  '../../util/toast';

interface IProps {
  history: any,
  appUser: IAppUser,
  user: IUser,
  outings: IOutings,
  onReturnBack: (id: string, user: IUser, outings: IOutings, callback: (result: boolean) => void) => void
};

interface IState{
  isLoading: boolean
};

export default class Outing extends React.Component<IProps, IState> {
  constructor(props: any) {
    super(props);
    this.state = {
      isLoading: false
    }
    this.onReturnBack = this.onReturnBack.bind(this);
  };

  componentDidMount(){};
  
  componentWillUnmount(){};
  
  onReturnBack = (outingId: string) => {
    confirmAlert({
      title: 'Confirm to return back from outing',
      message: 'Are you sure? You return back from this outing?',
      buttons: [
        {
          label: 'Yes',
          onClick: () => {
            this.setState({
              isLoading: true
            });
            this.props.onReturnBack(outingId, this.props.user, this.props.outings, (result: boolean) => {
              this.setState({
                isLoading: false,
              });
              if(result){
                Toast.success('Successfully returned back.');
              }else{
                Toast.fail('Failed to Return back.');
              }
            });
          }
        },
        {
          label: 'No',
          onClick: () => {}
        }
      ]
    });
  };

  render() {
    return(
      <View>
      {(this.props.appUser.role === ROLE.USER && (!isNNObject(this.props.outings) || !isNN(this.props.outings.data.filter(o => o.isReturned !== true)))) &&
        <View style={styles.row}>
          <Button
          title="Go Outing"
          onPress={() => {
            this.props.history.push('new-outing')
          }}
          />
        </View>
      }
      {
        (isNNObject(this.props.outings) && this.props.outings.isUpToDate && !this.props.outings.isError && isNN(this.props.outings.data)) ?
        this.props.outings.data.map((d: IOuting, i: number) => {
          return(
            <View key={"o" + d._id} style={{...styles.row, borderBottomWidth: 1, borderBottomColor: COLOR.HR_COLOR, borderBottomStyle: 'solid'}}>
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d._homeId + ' by ' + d.createdBy + ' on ' + toDateTimeString(d.createdAt)}</Text>
              </View>
              <View style={styles.ele}>
                  <Text style={{fontWeight: 'bold'}}>{toDateTimeString(d.fromDate) + " to " + toDateTimeString(d.toDate)}</Text>
              </View>
              {isNN(d.place) &&
              <View style={styles.ele}>
                <Text style={{fontWeight: 'bold'}}>{d.place}</Text>
              </View>
              }
              {isNN(d.address) &&
              <View style={styles.ele}>
                <Text>{d.address}</Text>
              </View>
              }
              {d.isReturned &&
              <View style={styles.ele}>
                <Text>{'Returned on ' + toDateTimeString(d.returnedAt)}</Text>
              </View>
              }
              {this.props.appUser.role !== ROLE.USER && 
                <View style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                  <Text>No one in the home</Text>
                </View>
              }
              {(this.props.appUser.role === ROLE.USER && d.isReturned !== true) &&
                <View style={{...modelStyles.saveBtn, flexDirection: 'row'}}>
                  <Button title="Return to Home" onPress={() => {
                    this.onReturnBack(d._id);
                  }} />
                </View>
              }
            </View>
          )
        })
        :
        <Text style={{padding: 20}}>{(this.props.outings.isUpToDate ? (this.props.outings.isError ? this.props.outings.errorMessage : "No outings found!!!") : "Loading...")}</Text>
      }
        <Spinner visible={this.state.isLoading} />
      </View>
    );
  }
};