import { actionTypes } from '../action-types';
import { IPolls } from '../../models/store/polls';
import { IPoll, IVote } from '../../models/poll/index';
import { IUser } from '../../models/user';
import { IAppUser } from '../../models/app-user';
import Api from '../api/api';
import { isNNObject, isNN } from '../../util/index';
//import { ROLE } from '../../constants';

let controller: string = 'poll';

export const loadAll = (appClientId: string, userRole: string, callback: (result: boolean) => void) => (dispatch: any) => {
  dispatch(getPollResult([], false, false, ''));
  // new Api().get(controller + 'loadall/?appClientId=' + appClientId + '&userRole=' + userRole, (result: any, message: string) => {
  new Api().get(controller + 's/' + appClientId, (result: any, message: string) => {
    if (isNNObject(result) && isNNObject(result.data)) {
      dispatch(getPollResult(result.data, true, false, ''));
      callback(true);
    } else {
      callback(false);
    }
  });
};

export const save = (appClientId: string, data: IPoll, polls: Array<IPoll>, user: IUser, sCallback: (result: boolean) => void) => (dispatch: any) => {
  if(data && isNN(data._id)){
    new Api().put(controller + '/' + data._id, (result: any, message: string) => {
      if(isNNObject(result) && isNN(result.data))
      {
        var foundIndex = polls.findIndex(d => d._id === result.data);
        polls[foundIndex]._id = result.data;
        polls[foundIndex].topic = data.topic;
        polls[foundIndex].description = data.description;
        polls[foundIndex].isAllowMultiple = data.isAllowMultiple;
        polls[foundIndex].isOneVotePerFlat = data.isOneVotePerFlat;
        polls[foundIndex].isAnonymous = data.isAnonymous;
        polls[foundIndex].fromDate = data.fromDate;
        polls[foundIndex].toDate = data.toDate;
        polls[foundIndex].options = data.options;
        dispatch(getPollResult((isNNObject(polls) ? polls : []), true, false, ""));
        sCallback(true);
      }else{
        sCallback(false);
      }
    }, data);
  }else{
    new Api().post(controller + '/' + appClientId, (result: any, message: string) => {
      // console.log("OUT: " + result);
      if(isNNObject(result) && isNN(result.data))
      {
        data._id = result.data;
        data.createdBy = user.name;
        polls.unshift(data);
        dispatch(getPollResult((isNNObject(polls) ? polls : []), true, false, ""));
        sCallback(true);
      }else{
        sCallback(false);
      }
    }, data);
  }
};

export const remove = (id: string, polls: Array<IPoll>, userId: string, sCallback: (result: boolean) => void) => (dispatch: any) => {
  new Api().delete(controller + '/' + id, (result: any, message: string) => {
    if(isNNObject(result) && isNNObject(result.data) && result.data === true)
    {
      polls = polls.filter((d, i) => d._id !== id);
      dispatch(getPollResult((isNNObject(polls) ? polls : []), true, false, ""));
      sCallback(true);
    }else{
      sCallback(false);
    }
  }, {
    Id: id,
    UserId: userId
  });
};

export const vote = (pollId: string, votes: Array<IVote>, polls: IPolls, user: IUser, appUser: IAppUser, sCallback: (result: boolean) => void) => (dispatch: any) => {
  new Api().post(controller + '/vote/' + pollId, (result: any, message: string) => {
    if(isNNObject(result) && isNN(result.data))
    {
      var foundIndex = polls.data.findIndex(d => d._id === pollId);
      polls.data[foundIndex].votes = result.data;
      dispatch(getPollResult(polls.data, true, false, ''));

      /*
      // Update home
      if(!isNN(user.homes[appUser.homeIndex].votes)){
        user.home[appUser.homeIndex].vote = [];
      }
      result.data.map((v: IVote, vi: number) => {
        user.home[appUser.homeIndex].vote.push(v);
      });
      dispatch(getUserResult(user));
      */
      // Update polls
      // if(polls.isUpToDate && !polls.isError && isNN(polls.data) && ((',' + user.type + ',').indexOf(ROLE.ADMIN) >= 0 || (',' + user.type + ',').indexOf(ROLE.MANAGER) >= 0)){
        if(polls.isUpToDate && !polls.isError && isNN(polls.data)){
        let lstPoll = polls.data;
        let pollIndex = lstPoll.findIndex(p => p._id === pollId);
        lstPoll[pollIndex].votes = result.data;
        /*
        if(!isNN(lstPoll[pollIndex].votes)){
          lstPoll[pollIndex].vote = [];
        }
        result.data.map((v: IVote, vi: number) => {
          lstPoll[pollIndex].vote.unshift(v);
        });
        */
        dispatch(getPollResult(lstPoll, true, false, ""));
      }
     // dispatch(getUserResult(user));
      
      sCallback(true);
    }else{
      sCallback(false);
    }
  }, {
    votes: votes
  });
};

const getPollResult = (data: Array<IPoll>, isUpToDate: boolean, isError: boolean, errorMessage: string) => ({
  type: actionTypes.LOAD_POLLS,
  payload: { 
    data,
    isUpToDate,
    isError,
    errorMessage
  } as IPolls
});