import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IHome, IMember, IVehicle, IServant, ITicket, IPayment, IVisitor } from '../models/home';
import { IDocument } from '../models/document';
import { load } from '../store/actions/home';
import { loadAllByHome as loadMembers} from '../store/actions/member';
import { loadAllByHome as loadVehicles } from '../store/actions/vehicle';
import { loadAllByHome as loadServants } from '../store/actions/servant';
import { loadAllByHome as loadTickets } from '../store/actions/ticket';
import { loadAll as loadVisitors } from '../store/actions/visitor';
import { loadAll as loadPayments } from '../store/actions/payment';
import { loadAllByHome as loadDocuments } from '../store/actions/documents';
import Page from '../components/home/manager/view-home/index';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    users: state.Users,
    homes: state.Homes,
    blocks: state.Blocks
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (id: string, sCallback: (result: IHome) => void, fCallback: (message: string) => void) => dispatch(load(id, sCallback, fCallback)),
    onLoadMembers: (homeId: string, sCallback: (result: Array<IMember>) => void, fCallback: (message: string) => void) => dispatch(loadMembers(homeId, sCallback, fCallback)),
    onLoadVehicles: (homeId: string, sCallback: (result: Array<IVehicle>) => void, fCallback: (message: string) => void) => dispatch(loadVehicles(homeId, sCallback, fCallback)),
    onLoadServants: (homeId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) => dispatch(loadServants(homeId, sCallback, fCallback)),
    onLoadTickets: (homeId: string, sCallback: (result: Array<ITicket>) => void, fCallback: (message: string) => void) => dispatch(loadTickets(homeId, sCallback, fCallback)),
    onLoadVisitors: (homeId: string, isActive: boolean, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => dispatch(loadVisitors('', homeId, isActive, sCallback, fCallback)),
    onLoadPayments: (homeId: string, sCallback: (result: Array<IPayment>) => void, fCallback: (message: string) => void) => dispatch(loadPayments('', homeId, sCallback, fCallback)),
    onLoadDocuments: (homeId: string, sCallback: (result: Array<IDocument>) => void, fCallback: (message: string) => void) => dispatch(loadDocuments(homeId, sCallback, fCallback))
});

export const ViewHome = connect(mapStateToProps, mapDispatchToProps)(Page);