import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IAppUser } from '../models/app-user';
import { IUser } from '../models/user/index';
import { ITransaction } from '../models/home/transaction';
import { IVisitor } from '../models/home';
import { setGateNo } from '../store/actions/account';
import { upload, update } from '../store/actions/payment';
import { save as saveVisitor, search as searchVisitor } from '../store/actions/visitor';
import { upload as fileUpload } from '../store/actions/file';
import Page from '../components/home/dashboard';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    appUser: state.AppUser,
    user: state.User,
    ads: state.Ads,
    outings: state.Outings,
    users: state.Users,
    homes: state.Homes,
    tickets: state.Tickets,
    blocks: state.Blocks,
    gateNo: state.GateNo
});
const mapDispatchToProps = (dispatch: any) => ({
    onGateNoChanged: (gateNo: string) => dispatch(setGateNo(gateNo)),
    onPaymentUpload: (file: any, homeId: string, user: IUser, callback: (result: boolean) => void) => dispatch(upload(file, homeId, user, callback)),
    onPaymentUpdate: (paymentId: string, transaction: ITransaction, homeIndex: number, user: IUser, callback: (result: boolean) => void) => dispatch(update(paymentId, transaction, homeIndex, user, callback)),
    onSaveVisitor: (homeId: string, data: IVisitor, user: IUser, appUser: IAppUser, sCallback: (result: IVisitor) => void, fCallback: (message: string) => void) => dispatch(saveVisitor(homeId, data, user, appUser, sCallback, fCallback)),
    onUpload: (data: any, callback: (result: boolean, message: string) => void) => dispatch(fileUpload(data, callback)),
    onSearchVisitor: (searchText: string, sCallback: (result: Array<IVisitor>) => void, fCallback: (message: string) => void) => dispatch(searchVisitor(searchText, sCallback, fCallback))
});

export const Dashboard = connect(mapStateToProps, mapDispatchToProps)(Page);