import { connect } from 'react-redux';
import { IAppState } from '../models/app-state';
import { IServant } from '../models/home';
import { loadActiveTakeaways, exitTakeaway } from '../store/actions/servant';

import Page from '../components/home/gatekeeper/takeaways';

const mapStateToProps = (state: IAppState, ownProps: any) => ({
    user: state.User,
    appUser: state.AppUser,
    gateNo: state.GateNo
});
const mapDispatchToProps = (dispatch: any) => ({
    onLoad: (appClientId: string, sCallback: (result: Array<IServant>) => void, fCallback: (message: string) => void) =>  dispatch(loadActiveTakeaways(appClientId, sCallback, fCallback)),
    onExit: (id: string, gateNo: string, callback: (result: boolean) => void) => dispatch(exitTakeaway(id, gateNo, callback))
});

export const AllTakeaways = connect(mapStateToProps, mapDispatchToProps)(Page);